import OAuthAccessTokens from './accessToken.model';
import User from '../../../api/user/user.model';
import config from '../../../config';
import auth from '../../auth.service.js';
import logger from '../../../modules/logger';
import vw from '../../../api/viewBase/viewBase';
import esErr from '../../../errors/errors';
import dbWp from '../../../utils/dbwrapper';
import async from 'async';

exports.sync_add_accesstoken = function(src, data, cb){
    var accesstoken = data.token;
    var onesnaUser = data.onesnaUser;
    async.waterfall([
        function(callback){
            dbWp.execute(User, User.findOne, src.id, {ndbid: accesstoken.userId}, callback);
        },
        function(user, callback){
            if (!user || user.lastupdatetime !== onesnaUser.lastupdatetime){
                onesnaUser.ndbid = onesnaUser.id;
                onesnaUser.secret = onesnaUser.security_token;
                delete onesnaUser.id;
                logger.info(src.id, 'User need insert or update by new data ' + onesnaUser.username);
                dbWp.execute(User, User.findOneAndUpdate, src.id, {ndbid: onesnaUser.ndbid},onesnaUser, {upsert: true, new: true}, (err, savedUser)=>{
                  if (!err){
                    return callback(null, savedUser);
                  }
                  else if (err.code == esErr.DBErrorDuplicateKey){
                    //Under ndbid unique and username unique condition, happen such error only possible be username duplicate
                    //To avoid problem of endless loop, get user by username and replace secret in memory to make verify pass
                    logger.info(reqid, 'Happen duplicate username ' + onesnaUser.username + '. Get user by same username from db!');
                    dbWp.execute(User, User.findOne, reqid, {username: onesnaUser.username}, (err, savedUser)=>{
                      if (err || (!savedUser)){
                        logger.error(reqid, 'Failed to get user by username ' + onesnaUser.username);
                        return cb(new esErr.ESErrors(esErr.SyncAddAccessTokenFailed));
                      }
                      return callback(null, savedUser);
                    });
                  }
                  else{
                    return callback(err, savedUser);
                  }
                });
            }
            else{
                return callback(null, user);
            }
        },
        function(saved, callback){
            if (!saved) {
                logger.info(src.id, 'Insert or update user failed');
                return callback(new esErr.ESErrors(esErr.SyncAddAccessTokenFailed));
            }
            dbWp.execute(OAuthAccessTokens,
                         OAuthAccessTokens.create,
                         src.id,
                         {
                            accessToken: accesstoken.token,
                            clientId: accesstoken.clientId,
                            userId: saved._id,
                            expires: accesstoken.expires,
                            scope: accesstoken.scope
                         },
                         callback);
        },
        function(access_token, callback){
            return callback(null, access_token);
        }
    ], function(err, result){
        return cb(err, result);
    });
};


