function TransferUrlLink(src, inData){ 
  var transferedObj = {
    provider: MessageConstants.DATA_PROVIDER_TYPES.URLLINK,
    providerFileType: inData.providerFileType || '',
    fileType: inData.fileType ||  '',
    name: inData.name || undefined,
    path: inData.path || '',
    icon: inData.icon || '',
    thumbnail: inData.thumbnail || '',
    keywords: inData.keywords || [],
    description: inData.description || '',
    sitename: inData.sitename || '',
    previewFile: inData.previewFile || '',
    thumbnailFile: inData.thumbnailFile || ''
  };
  if (!transferedObj.path){
    logger.error(src.id, 'The path in ' + MessageConstants.DATA_PROVIDER_TYPES.URLLINK +' Can not be null');
    throw new esErr.ESErrors(esErr.MessageInvalidPath);
  }
  return transferedObj;
}

function TransferNativeLink(src, inData){ 
  var transferedObj = {
    provider: MessageConstants.DATA_PROVIDER_TYPES.NATIVE,
    providerFileType: inData.providerFileType || '',
    fileType: inData.fileType ||  '',
    name: inData.name || '',
    fileId: inData.fileId || '',    
    icon: inData.icon || '',
    thumbnail: inData.thumbnail || '',
    keywords: inData.keywords || [],
    description: inData.description || '',
    previewFile: inData.previewFile || '',
    fileSize: inData.fileSize || 0,
    thumbnailFile: inData.thumbnailFile || '',
    metaData: inData.metaData,
    pages: inData.pages,
    convertStatus: inData.convertStatus,
  };
  
  if (!transferedObj.fileId){
    logger.error(src.id, 'The fileId not existed');
    throw new esErr.ESErrors(esErr.MessageInvalidFileId);
  }
  if (!transferedObj.fileSize){
    logger.error(src.id, 'The fileSize must be integer larger than zero');
    throw new esErr.ESErrors(esErr.MessageInvalidFileSize);
  }
  return transferedObj;  
}

var transferProviderMap = {};
transferProviderMap[MessageConstants.DATA_PROVIDER_TYPES.NATIVE] = TransferNativeLink;
transferProviderMap[MessageConstants.DATA_PROVIDER_TYPES.URLLINK] = TransferUrlLink;

function isValidSenderType(senderType){
  let senderTypeEnum = [cst.TypeUser, cst.TypeAnonymous];
  if (senderTypeEnum.indexOf(senderType) < 0){
    return false;
  }
  return true;
}

function fillContentByCategory(src, data){
  if (data.parentMsg){
    if ((!data.parentMsg) || (!data.parentMsg._id)){     
      delete data.parentMsg;
    }
  }
  if ((data.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.IDEA || data.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.TASK) && (!data.parentMsg)
      && data.content){
    data.content.description = data.content.description || '';
    if (MessageConstants.ACTIVITY_CATEGORY_TYPES.TASK){
      data.content.status = data.content.status || TaskConstants.TASK_STATUS_TYPES.PENDING.status;
      data.content.dueDate = data.content.dueDate || Date.now();
      data.content.assignees = data.content.assignees || [];
    }
    else{
      delete data.content.status;
      delete data.content.dueDate;
      delete data.content.assignees;
    }
  }
  else{
    delete data.content.description;
    delete data.content.status;
    delete data.content.dueDate;
    delete data.content.assignees;
  }
}

function addUniqueLikeId(src, data, cb){
  if (data.category === MessageConstants.ACTIVITY_CATEGORY_TYPES.LIKE &&
      data.parentMsg && data.parentMsg._id){
    let queryCondition = {
        'category': MessageConstants.ACTIVITY_CATEGORY_TYPES.LIKE,
        'parentMsg._id': data.parentMsg._id,
        'sender._id': data.sender._id,
        'sender.type': data.sender.type
    };
    data.uniqueLikeId = MessageConstants.ACTIVITY_CATEGORY_TYPES.LIKE + 
                        data.parentMsg._id.toString() +
                        data.sender._id.toString() +
                        data.sender.type;    
  }
  cb(null, data);
}

function beforeAddMessageHandle(src, data, cb){
  let reqId = src.id || '';
  if (!MessageConstants.isValidCategory(data)){
    logger.error(src.id, 'The category is not valid');
    return cb(new esErr.ESErrors(esErr.MessageInvalidCategory));
  }
  if (!isValidSenderType(data.sender.type)){
    logger.error(src.id, 'The sender type is invalid');
    return cb(new esErr.ESErrors(esErr.MessageNotSupportSenderType));
  }
  fillContentByCategory(src, data);
  let transferdObjs = [];
  if (data.content.data && data.content.data.length > 0){
    for (let dataItem of data.content.data){
      if (!dataItem.provider){
        logger.error(src.id, 'The provider in content.data can not be null');
        return cb(new esErr.ESErrors(esErr.MessageInvalidProvider));
      }
      if (dataItem.provider in transferProviderMap){
        let transferFun = transferProviderMap[dataItem.provider];
        try{
          let transferObj = transferFun(src, dataItem);
          transferdObjs.push(transferObj);
        }
        catch(err){
          logger.error(src.id, err);
          return cb(err);
        }
      }
      else{
        return cb(new esErr.ESErrors(esErr.MessageInvalidProvider));
      }
    }
    data.content.data = transferdObjs;
  }
  return cb(null, data);
}

exports.addMessage = function(src, data, cb){
  if (!MessageConstants.isStorableEventType(data)){
    return cb(null, data);
  } 
  let reqId = src.id || '';
  delete data._id;  
  data.created = data.created || Date.now();
  data.modified = null;
  if (data.category === MessageConstants.ACTIVITY_CATEGORY_TYPES.IDEA){
    data.modified = data.created;
  }
  
  async.waterfall([
    (interCallback) => {
      beforeAddMessageHandle(src, data, interCallback);
    },
    (msgObj, interCallback) => {
      addUniqueLikeId(src, data, interCallback);
    },
    (msgObj, interCallback) =>{
      dbWp.execute(TopicMessage, TopicMessage.create, reqId, msgObj, (err, createdMessage) => {
        if (err){
          logger.warn(src.id, err.message);
        }       
        return interCallback(err, createdMessage);
      });
    }
    
  ], (err, result) => {
    if (err){
      logger.warn(src.id, err.message);
    }
    else{
      MessageEvent.emitMessageCreated(src, result);
    }
    return cb(err, result);
  });
};

function mergeMessage(src, oriMessage, newMessage){
  let functionName = '[mergeMessage] ';
  oriMessage.modified = Date.now();
  oriMessage.content = newMessage.content;  
}

exports.updateMessage = function(src, data, cb){
  let functionName = '[message.backend.updateMessage]'  
  if (!MessageConstants.isStorableEventType(data)){
    return cb(null, data);
  } 
  let reqId = src.id || '';
  data.created = data.created || Date.now();
  
  async.waterfall([
    (interCallback) => {
      dbWp.execute(TopicMessage, TopicMessage.findById, reqId, data._id, (err, oriMessage)=>{
        if (data.modified && 
            Math.floor(new Date(data.modified).getTime() / 1000) <  Math.floor(oriMessage.modified.getTime() / 1000)){
          logger.warn(src.id, functionName + 'Outdated message update for updated modified ' + Math.floor(new Date(data.modified).getTime() / 1000) + ' old modified ' + Math.floor(oriMessage.modified.getTime() / 1000))
          return interCallback(new esErr.ESErrors(esErr.UpdateRecordWithModifiedOutdate));
        }
        else{
          data.modified = Date.now();
          return interCallback(null, oriMessage);
        }
      }); 
    },
    (oriMessage, interCallback) => {
      mergeMessage(src, oriMessage, data)
      beforeAddMessageHandle(src, oriMessage, interCallback);
    },    
    (msgObj, interCallback) =>{
      dbWp.execute(TopicMessage, TopicMessage.findOneAndUpdate, reqId, {_id: data._id}, msgObj,{new: true},(err, updatedMessage) => {
        if (err){
          logger.warn(src.id, err.message);
        }
        return interCallback(err, updatedMessage);
      });
    }
    
  ], (err, result) => {
    if (err){
      logger.warn(src.id, err.message);
    }
    else{
      MessageEvent.emitMessageUpdated(src, result);
    }
    return cb(err, result);
  });
};

exports.addMessageWhenNoId = function(src, data, cb){
  //If the message has _id do different business process, 
  //Or else addMessage
  logger.debug(src.id, 'addMessageWhenNoId try to save data');
  if (data._id){
    beforeAddMessageHandle(src, data, (err, msgObj) => {
      if (err){
        return cb(err);
      }
      cb(null, msgObj);
    });
  }
  else{
    exports.addMessage(src, data, (err, msgObj) => {
      if (err){
        return cb(err);
      }
      cb(null, msgObj);
    });
  }
};

function toDownloadableDataItem(src, dataItem, cb){  
  var promiseArray = [];
  if (dataItem.provider == MessageConstants.DATA_PROVIDER_TYPES.NATIVE){
    if (dataItem.fileId){
      let inData = {key: dataItem.fileId, name: dataItem.name};
      let aPromise = new Promise((resolve, reject)=>{
        fs.getDownloadSignedUrl(src, inData, (err, url) => {
          if (err){
            logger.error(src.id, 'Get download signed url happen error');
            reject(new esErr.ESErrors(esErr.DownloadUrlCreateFailed));
          }
          else{
            dataItem.path = url;
            resolve(url);
          }          
        });
      });
      promiseArray.push(aPromise);
    }
    let thumbnailFile = dataItem.thumbnailFile
    if(thumbnailFile){      
      let inData = {key: thumbnailFile, name: dataItem.name};
      let aPromise = new Promise((resolve, reject)=>{
        fs.getDownloadSignedUrl(src, inData, (err, url) => {      
          if (err){      
            logger.error(src.id, 'Get download signed url happen error');
            reject(new esErr.ESErrors(esErr.DownloadUrlCreateFailed));
          }  
          else{
            dataItem.previewUrl = url;
            dataItem.thumbnailUrl = url;
            resolve(url);
          }
        });
      });
      promiseArray.push(aPromise);
    }
    else if (dataItem.previewFile){
      let inData = {key: dataItem.previewFile, name: dataItem.name};
      let aPromise = new Promise((resolve, reject)=>{
        fs.getDownloadSignedUrl(src, inData, (err, url) => {      
          if (err){      
            logger.error(src.id, 'Get download signed url happen error');
            reject(new esErr.ESErrors(esErr.DownloadUrlCreateFailed));
          }  
          else{
            dataItem.previewUrl = url;            
            resolve(url);
          }
        });
      });
      promiseArray.push(aPromise);
    }
  } 
  else if (dataItem.provider == MessageConstants.DATA_PROVIDER_TYPES.URLLINK){
    let thumbnailFile = dataItem.thumbnailFile;
    if(thumbnailFile){
      if (thumbnailFile.startsWith(cst.HTTPSchema) || 
          thumbnailFile.startsWith(cst.HTTPSSchema)){
        dataItem.previewUrl = thumbnailFile;
        dataItem.thumbnailUrl = thumbnailFile;
      }
      else{
        let inData = {key: thumbnailFile, name: dataItem.name};
        let aPromise = new Promise((resolve, reject)=>{
          fs.getDownloadSignedUrl(src, inData, (err, url) => {
            if (err){      
              logger.error(src.id, 'Get download signed url happen error');
              reject(new esErr.ESErrors(esErr.DownloadUrlCreateFailed));
            } 
            else{
              dataItem.previewUrl = url;
              dataItem.thumbnailUrl = url;
              resolve(url);
            }
          });
        });
        promiseArray.push(aPromise);
      }
    }
    else if (dataItem.previewFile){
      if (dataItem.previewFile.startsWith(cst.HTTPSchema) || 
          dataItem.previewFile.startsWith(cst.HTTPSSchema)){
        dataItem.previewUrl = thumbnailFile;        
      }
      else{
        let inData = {key: dataItem.previewFile, name: dataItem.name};
        let aPromise = new Promise((resolve, reject)=>{
          fs.getDownloadSignedUrl(src, inData, (err, url) => {
            if (err){      
              logger.error(src.id, 'Get download signed url happen error');
              reject(new esErr.ESErrors(esErr.DownloadUrlCreateFailed));
            } 
            else{
              dataItem.previewUrl = url;
              resolve(url);
            }
          });
        });
        promiseArray.push(aPromise);
      }
    }
  }
  if (promiseArray.length){
    Promise.all(promiseArray)
      .then(()=>{
        return cb(null, dataItem);
      })
      .catch(()=>{
        return cb(null, dataItem);
      })
  }
  else{
    return cb(null, dataItem);
  }
//  if (shouldReturn){
//    return cb(null, dataItem);
//  }
//  else{
//    setTimeout(function(){
//      return cb(null, dataItem);
//    }, 1000);
//  }
}

exports.toDownloadableMessage = function(src, data, cb){
  //This function will fill download url for each message native attachment
  let reqId = src.id || '';
  if (data.content.data && data.content.data.length > 0){
    async.forEach(data.content.data, (dataItem, callback) => {
      toDownloadableDataItem(src, dataItem, (err, dataItem) => {
        if (dataItem.previewUrl){
          dataItem.previewUrl = dataItem.previewUrl.replace(':id', data._id);          
        }
        callback(err, dataItem);
      });
    }, (err) => {
      return cb(err);
    });
  }
  else{
    return cb(null, data);
  }
};

exports.toClientFormatMessage = function(src, data, cb){
  //This function will remove _id from content.data
  if (data.content.data && data.content.data.length > 0){ 
    for (let dataItem in data.content.data){
      dataItem._id = undefined;
    }
  }
  return  cb(null, data.toJSON());
};

function covertTOIsostring(data){
  if (data.created && typeof data.created != 'string'){
    data.created = data.created.toISOString();
  }
  if (data.modified && typeof data.modified != 'string'){
    data.modified = data.modified.toISOString();
  }
  if (data.content && data.content.dueDate && typeof data.content.dueDate != 'string'){
    data.content.dueDate = data.content.dueDate.toISOString();
  }
}

exports.toDownloadableClientFormatMessage = function(src, data, cb){
  //This function will remove _id from content.data and fill download url
  let reqId = src.id || '';
  if ('toJSON' in data && typeof data.toJSON == 'function'){
    data = data.toJSON();
  }
  //covertTOIsostring(data);
  if (data.content.data && data.content.data.length > 0){
    async.forEach(data.content.data, (dataItem, callback) => {
      toDownloadableDataItem(src, dataItem, (err, dataItem) => {
        if(dataItem){
          dataItem._id = undefined; 
          if (dataItem.previewUrl){
            dataItem.previewUrl = dataItem.previewUrl.replace(':id', data._id);          
          }
        }
        else{
          logger.warn(src.id, 'toDownloadableDataItem happen error', err);
        }
        return callback(err, dataItem);
      });      
    }, (err) => {  
      if (err){
        return cb(err);
      }      
      return cb(null, data); 
    });
  }
  else{
    return cb(null, data);
  }
};

function addChatCount(src, msgObj, addedVal){
  let functionName = '[addChatCount]';
  dbWp.execute(TopicMessage,
    TopicMessage.update,
    src.id,
    {_id: msgObj._id},
    {$inc: {'chatCount': addedVal}},
    (err, result)=>{
      if (err){
        logger.error(src.id, ndutil.format("%s Add count %d to topic message %s failed",functionName, addedVal, msgObj._id.toString()), err);
      }
      else{
        logger.info(src.id, ndutil.format("%s Add count %d to topic message %s successfully",functionName, addedVal, msgObj._id.toString()), err);
      }
  });
}

function addLikeCount(src, msgObj, addedVal){
  let functionName = '[addLikeCount]';
  dbWp.execute(TopicMessage,
    TopicMessage.update,
    src.id,
    {_id: msgObj._id},
    {$inc: {'likeCount': addedVal}},
    (err, result)=>{
      if (err){
        logger.error(src.id, ndutil.format("%s Add like %d to topic message %s failed",functionName, addedVal, msgObj._id.toString()), err);
      }
      else{
        logger.info(src.id, ndutil.format("%s Add like %d to topic message %s successfully",functionName, addedVal, msgObj._id.toString()), err);
      }
  });
}


function fillParentMsgInformation(src, data, cb){
  let functionName = '[fillParentMsgInformation]';
  if (data.parentMsg && data.parentMsg._id){
    let execobj = TopicMessage.findById(data.parentMsg._id, {'category':1, 'content.bodyText': 1, 'chatCount':1, 'likeCount':1});
    logger.info(src.id, ndutil.format('%s Get title of message=%s', functionName, data.parentMsg._id.toString()));
    dbWp.execute(execobj, execobj.exec, src.id, (err, parentMsg)=>{
      if (err){
        logger.error(src.id, ndutil.format('%s Get title of message failed', functionName), err);
        return cb(null, data);
      }
      else{
        logger.info(src.id, ndutil.format('%s Get title of message successfully', functionName));
        data.parentMsg.bodyText = parentMsg.content.bodyText;
        data.parentMsg.category = parentMsg.category;
        
        if (parentMsg.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.IDEA){
          setTimeout(function(){addChatCount(src, parentMsg, 1)}, 1000);
        }
        if (data.category === MessageConstants.ACTIVITY_CATEGORY_TYPES.LIKE){
          setTimeout(function(){addLikeCount(src, parentMsg, 1)}, 1000);
        }
        return cb(null, data);
      }
    });    
  }
  else{
    return cb(null, data);
  }
}

exports.addMessagetoNoSenderPublicInfo = function(src, data, cb){
  async.waterfall([
  (InternalCallback) => {
    fillParentMsgInformation(src, data, InternalCallback);
  },
  (createdMessage, InternalCallback) => {
    exports.addMessage(src, createdMessage, InternalCallback);
  },
  (createdMessage, InternalCallback) => {
      exports.toDownloadableClientFormatMessages(src, [createdMessage], InternalCallback);
  },(results, InternalCallback) => {
    let result = results[0];
    //Throw event to update parentMessage if it is idea
    if (result.parentMsg && 
        result.parentMsg.category === MessageConstants.ACTIVITY_CATEGORY_TYPES.IDEA){      
      MessageEvent.emitUpdateModifyTime(src.id, result.parentMsg._id);
    }
    return cb(null, result);
  }],(err) => {
      if(err){
        return cb(err);
      }    
  });
};

exports.updateMessagetoNoSenderPublicInfo = function(src, data, cb){
  async.waterfall([(InternalCallback) => {
    exports.updateMessage(src, data, InternalCallback);
  },(createdMessage, InternalCallback) => {
      exports.toDownloadableClientFormatMessages(src, [createdMessage], InternalCallback);
  },(results, InternalCallback) => {
    let result = results[0];
    //Throw event to update parentMessage if it is idea
    if (result.parentMsg && 
        result.parentMsg.category === MessageConstants.ACTIVITY_CATEGORY_TYPES.IDEA){      
      MessageEvent.emitUpdateModifyTime(src.id, result.parentMsg._id);
    }
    return cb(null, result);
  }],(err) => {
      if(err){
        return cb(err);
      }    
  });
};

exports.addMessageWhenNoIdtoNoSenderPublicInfo = function(src, data, cb){
  async.waterfall([
  (InternalCallback) => {
    fillParentMsgInformation(src, data, InternalCallback);
  },
  (createdMessage, InternalCallback) => {
    exports.addMessageWhenNoId(src, createdMessage, InternalCallback);
  },(createdMessage, InternalCallback) => {
      exports.toDownloadableClientFormatMessages(src, [createdMessage], InternalCallback);
  },(results, InternalCallback) => {
    let result = results[0];
    //Throw event to update parentMessage if it is idea and data._id not existed
    if ((!data._id) && 
        result.parentMsg && 
        result.parentMsg.category === MessageConstants.ACTIVITY_CATEGORY_TYPES.IDEA){
      MessageEvent.emitUpdateModifyTime(src.id, result.parentMsg._id);
    }
    return cb(null, result);
  }],(err) => {
      if(err){
        return cb(err);
      }    
  });
};

function getSenderPublicInfo (src, data, callback){
  if (data.sender.type == cst.TypeUser){
    let userId = data.sender._id;
    dbWp.execute(User, User.findById, src.id, userId, function(err, user) {
      if(err || !user){
        logger.error(src.id, 'getSenderPublicInfo in addMessagetoDownloadableClientFormatMessage failed with userid=' + userId);
        return callback(new esErr.ESErrors(esErr.NotExsistedError));
      } 
      data.sender.username = user.username;
      data.sender.picture_url = user.picture_url;
      data.sender.displayname = user.displayname;
      return callback(null);
    });
  }
  else if(data.sender.type == cst.TypeAnonymous){
    let anonymousId = data.sender._id;
    dbWp.execute(Anonymous, Anonymous.findById, src.id, anonymousId, function(err, anonymous) {
      if(err || !anonymous){
        logger.error(src.id, 'getSenderPublicInfo in addMessagetoDownloadableClientFormatMessage failed with anonymousid=' + anonymousId);
        return callback(new esErr.ESErrors(esErr.NotExsistedError));
      } 
      //anonymous = anonymous.toJSON();
      data.sender.username = anonymous.username;
      data.sender.picture_url = anonymous.picture_url;
      data.sender.displayname = anonymous.displayname;
      return callback(null);
    });
  }
  else{
    return callback(new esErr.ESErrors(esErr.MessageNotSupportSenderType));
  }
}

function getParentMsgInfo (src, data, callback){
  if (data.parentMsg){
    if (data.parentMsg._id == data._id){
      logger.error('The parent message is the message self. It is impossilbe');
      callback(null, null);
    }
    else{
      dbWp.execute(TopicMessage, TopicMessage.findById, src.id, data.parentMsg, function(err, parentMsg){
        if (err){
          logger.warn(src.id, 'getParentMsgInfo happen error', err);
          return callback(err);
        }
        if (!parentMsg){
          logger.warn(src.id, 'There is no message with with _id =' + data.parentMsg._id.toString());
          return callback(new esErr.ESErrors(esErr.ParentMessageNotExsistedError));
        }
        exports.toDownloadableClientFormatMessages(src, [parentMsg], callback);
      });
    }
  }
  else{
    callback(null, null);
  }    
}

exports.addMessagetoDownloadableClientFormatMessage = function(src, data, cb){
  async.parallel([
    (InternalCallback) => {
      getSenderPublicInfo(src, data, InternalCallback);
    },
    (InternalCallback) => {
      exports.addMessagetoNoSenderPublicInfo(src, data, InternalCallback);
    },
    (InternalCallback) => {
      getParentMsgInfo(src, data, InternalCallback);
    }
  ], function(err, results){
    if (!err){
      let sender = results[0];
      let message = results[1];
      let parentMsg = null;
      if (results[2]){
        parentMsg = results[2][0];        
      }
      message.sender = _.extend(message.sender, sender); 
      if (parentMsg){
        message.parentMsg = parentMsg;       
      }
      //Throw event to update parentMessage if it is idea
      if (message.parentMsg && 
          message.parentMsg.category === MessageConstants.ACTIVITY_CATEGORY_TYPES.IDEA){
        MessageEvent.emitUpdateModifyTime(src.id, message.parentMsg._id);
      }
      return cb(null, message);
    }
    logger.error(src.id, 'Call addMessagetoDownloadableClientFormatMessage failed ' + err.message);
    return cb(err);    
  });
};

exports.addMessageWhenNoIdtoDownloadableClientFormatMessage = function(src, data, cb){
  async.parallel([
    (InternalCallback) => {
      if ((!data.sender.username) || (!data.sender.displayname) || (!data.sender.picture_url)){
        getSenderPublicInfo(src, data, InternalCallback);
      }
      else{
        return InternalCallback(null);
      }
    },
    (InternalCallback) => {
      exports.addMessageWhenNoIdtoNoSenderPublicInfo(src, data, InternalCallback);
    },
    (InternalCallback) => {
      getParentMsgInfo(src, data, InternalCallback);
    }
  ], function(err, results){
    if (!err){
      let sender = results[0];
      let message = results[1];
      let parentMsg = null;
      if (results[2]){
        parentMsg = results[2][0];        
      }
      message.sender = _.extend(message.sender, sender); 
      if (parentMsg){
        message.parentMsg = parentMsg;       
      }
      //Throw event to update parentMessage if it is idea and data._id is not existed
      if ((!data._id_) && 
          message.parentMsg && 
          message.parentMsg.category === MessageConstants.ACTIVITY_CATEGORY_TYPES.IDEA){
        MessageEvent.emitUpdateModifyTime(src.id, message.parentMsg._id);
      }
      return cb(null, message);
    }
    logger.error(src.id, 'Call addMessageWhenNoIdtoDownloadableClientFormatMessage failed ' + err.message, err);
    return cb(err);    
  });
};

exports.queryByRef = function(src, data, cb){
  let matchCondition = {topicId: data.topicId};
  let sort = {created: -1};    
  if (data.page <= 1){
    delete data.nextRefObjId;
    delete data.prevRefObjId;
    data.page = 1;
  }
  let includeEqual = data.includeEqual || false;
  delete data.includeEqual;
  
  
  if (data.direction == cst.DirectionAfter){
    if(data.refTime){
      matchCondition.created = {$gte: new Date(data.refTime)};
    }
    else{
      sort = {_id: -1};      
    }
  }
  else{
    if(data.refTime){
      matchCondition.created = {$lte: new Date(data.refTime)};
    }
    else{
      sort = {_id: -1};
    }
  }  
  
  if (data.nextRefObjId){
    if (includeEqual){
      matchCondition._id = {$lte: mongoose.Types.ObjectId(data.nextRefObjId)};
    }
    else{
      matchCondition._id = {$lt: mongoose.Types.ObjectId(data.nextRefObjId)};
    }
    sort = {_id: -1};
  }
  else if(data.prevRefObjId){
    if (includeEqual){
      matchCondition._id = {$gte: mongoose.Types.ObjectId(data.prevRefObjId)};
    }
    else{
      matchCondition._id = {$gt: mongoose.Types.ObjectId(data.prevRefObjId)};
    }
    sort = {_id: 1};
  }  
  
  var exeobj = TopicMessage.find(matchCondition).sort(sort).limit(data.size + 1).lean();
  dbWp.execute(exeobj,
               exeobj.exec,
               src.id, 
               function(err, results){
    if (err){
      logger.error(src.id, 'queryByRef happen error', err.message);
      return cb(null, {results:[]});
    }    

    if (results.length > data.size){
      if (data.prevRefObjId){
        results = results.reverse();
        return cb(null, {results:results.slice(1, data.size + 1), havingNextPage: true});
      }
      return cb(null, {results:results.slice(0, data.size), havingNextPage: true});
    }
    else{
      if (data.prevRefObjId){
        results = results.reverse();        
      }
      return cb(null, {results:results, havingNextPage: false});
    }
  });    
};


