function syncMessageSenderDefer(src, data, cb){
  let functionName = '[syncMessageSenderDefer] ';
  logger.info(src.id, ndutil.format('%s sync topicmessage sender info.', functionName), data);
  let senderId = data.senderId;
  let senderType = data.senderType;
  let senderData = data.senderData;
  dbWp.execute(TopicMessage,
    TopicMessage.update,
    src.id,
    {'sender._id': senderId, 'sender.type': senderType},
    {'$set': senderData},
    {'multi': true},
    (err, result)=>{      
      if (err){
        logger.error(src.id, ndutil.format('%s sync topicmessage sender info failed.', functionName), err);
        return cb(new esErr.ESErrors(esErr.SyncMessageSenderFailed));
      }
      else{
        logger.info(src.id, ndutil.format('%s sync topicmessage sender info successfully.', functionName));
        return cb(null);
      }
  });
}
taskqueue.registerDeferHandle('syncMessageSenderDefer', syncMessageSenderDefer);

