MeetingConstants.getAttendeeObjectByType = function (type){
	for(var i in MeetingConstants.ATTENDEE_TYPES){
		if(MeetingConstants.ATTENDEE_TYPES[i].type==type){
			return MeetingConstants.ATTENDEE_TYPES[i];
		}
	}
	return null;
}

