import { Router } from 'express';
import request from "request";
import jQuery from 'jquery';



const router = new Router();

//const AMS_IP = "http://54.208.80.119:7150"; //demo
const AMS_IP = "http://146.148.71.203:7150"; //gae
const AMS_URL = AMS_IP + "/mediacontrol/";
const LOGAN_NS = "default";


router.post("/", function(req, res){
  "use strict";

  let options = {
    method: "POST",
    url: AMS_URL + LOGAN_NS + req.query.sub,
    headers: {
      "Content-Type": "application/xml"
    },
    body: req.body.xmldoc
  };
  console.log("OPTIONS:", options);

  request(options, function(error, response, body){
    console.log("AMS status code:", response.statusCode);

    //console.log("RESPONSE:", response);
    //console.log("ERROR:", error);
    //console.log("BODY:", body);
    res
      .set("Content-Type", "text/xml")
      .status(response.statusCode)
      .send(body);
  });
});


router.get("/", function(req, res){
  "use strict";

  let options = {
    method: "GET",
    url: AMS_URL + LOGAN_NS + req.query.sub,
    headers: {
      "Content-Type": "application/xml"
    }
  };
  //console.log("OPTIONS:", options);

  request(options, function(error, response, body){
    console.log("AMS status code:", response.statusCode);
    //console.log("ERROR:", error);
    //console.log("BODY:", body);
    res
      .set("Content-Type", "text/xml")
      .status(response.statusCode)
      .send(body);
  });
});




export default router;



