import newrelic from 'newrelic';
import 'babel-core/polyfill';
import path from 'path';
import express from 'express';
import React from 'react';
import ReactDOM from 'react-dom/server';
import Router from './routes';
import config from './config';
import mongoose from 'mongoose';
import morgan from 'morgan';
import logger from './modules/logger';
import fs from 'fs';
import connect_timeout from 'connect-timeout';
import cst from './utils/serverConstants';
import process from 'process';
import ga from './modules/analytics/google';
import pmx from 'pmx';
// require("http").globalAgent.maxSockets = Infinity;

const app = global.server = express();

if (config.env === 'logan-testing' || config.env === 'logan-staging' || config.env === 'logan-production') {
  pmx.init({
    http: true, // HTTP routes logging (default: true)
    errors: true, // Exceptions loggin (default: true)
    custom_probes: true, // Auto expose JS Loop Latency and HTTP req/s as custom metrics
    network: true, // Network monitoring at the application level
    ports: true
  });
}
//function esReqTimeout(req, res, next){
//  if (req.path.indexOf('/taskqueue') == -1){
//    console.log("=============================>>> esReqTimeout 180000")
//    let req_timeout_obj = req_timeout(180000);
//    req_timeout_obj(req, res, next)
//  }
//  else{
//    console.log("=============================>>> esReqTimeout 240000")
//    let req_timeout_obj = req_timeout(240000);
//    req_timeout_obj(req, res, next)
//  }
//};
app.use(connect_timeout(cst.defaultTimeoutSeconds * 1000));
app.set('port', (config.port || 5000));
// express configuration
// -----------------------------------------------------------------------------
require('./config-env/express')(app);
require('./config-env/customizedMiddlewares')(app);
//

//https server setup for front end
var server;

if (config.sslServer && config.env === 'development') {
  var options = {
    key: fs.readFileSync(require('path').normalize(__dirname + '/..') + '/private/key.pem', 'utf8'),
    cert: fs.readFileSync(require('path').normalize(__dirname + '/..') + '/private/key-cert.pem', 'utf8')
  };
  server = require('https').createServer(options, app);
} else {
  server = require('http').createServer(app);
}

if (!process.env["backserver"]) {
  var socketio = require('./socketio');
  socketio.initialize(server);
 }

//var socketio = require('./socketio');
//socketio.initialize(server);
mongoose.connect(config.mongo.uri, config.mongo.options);

mongoose.connection.on('connected', function() {
  logger.info('MongoDB connected');
  process.env['mongoUA'] = 'connected';
  }
);
mongoose.connection.on('error', function(err) {
    logger.error('MongoDB connection error: ' + err);
    mongoose.disconnect();
});
mongoose.connection.on('disconnected', function() {
    logger.warn('MongoDB disconnected! Reconnecting in 3 seconds');
    process.env['mongoUA'] = 'disconnected';
    setTimeout(function () {
        mongoose.connect(config.mongo.uri, config.mongo.options);
    },3000);
});

require('./routes')(app);
// require('./seed').generateAdminList();
//


//
//
// Start server
// -----------------------------------------------------------------------------
// Not expire connection by http.server way
server.timeout = 0;
var listenIP = config.ip || '0.0.0.0';
server.listen(app.get('port'), listenIP, () => {
  /* eslint-disable no-console */
  if (config.env === 'development') {
    logger.info('The server is running at http://localhost:' + app.get('port') + ' under ' + app.get('env'));
    logger.info('Version is ' + config.version);
  } else {
    logger.info('server start listening!  Environment: ' + app.get('env') + '  on Port: ' + config.port);
    logger.info('Version is ' + config.version);
    if (config.mongoLog) {
      logger.info('Saving Logs');
    }
  }

  if (process.send) {
    process.send('online');
  }
});

// Expose app
exports = module.exports = app;



