'use strict';

import { Router } from 'express';
import controller from './file.controller';
import {asView} from '../viewBase/viewBase';

const router = new Router();

router.post('/getuploadurl', asView(controller.getUploadUrl));
router.post('/getProfileImageUploadUrl', asView(controller.getProfileImageUploadUrl));
router.post('/getdownloadurl', asView(controller.getDownloadUrl));
router.post('/getdownloadurlPublic', asView(controller.getDownloadUrlPublic));
router.get('/viewUrls/:token', asView(controller.getviewUrls))
export default router;


