
'use strict';

import config from './config';
import pmx from 'pmx';

module.exports = function(app) {
	app.use('/content', require('./content/content'));
	app.use('/api/users', require('./api/user'));
	app.use('/api/messages', require('./api/message')); //messages belong to room with id.
	app.use('/api/companies', require('./api/company'));
	app.use('/api/topics', require('./api/topic'));
	app.use('/api/spaces', require('./api/topic'));
	app.use('/api/tasks', require('./api/task'));
	app.use('/api/ideas', require('./api/idea'));
	app.use('/api/ams', require('./api/ams'));
	app.use('/api/anonymous', require('./api/anonymous'));
	app.use('/api/files', require('./api/file'));
	app.use('/api/sync',require('./api/sync'));
	app.use('/api/taskqueue',require('./api/taskqueue'));
	app.use('/api/migrate',require('./api/migrate'));
	app.use('/api/enrolls', require('./api/enroll'));
	app.use('/auth', require('./auth'));
	app.use('/api/fileviewer', require('./api/fileviewer'));

	//
	// Register Database connection
	// -----------------------------------------------------------------------------

	//
	// Statis Index.html
	// -----------------------------------------------------------------------------
	app.route('/*')
	.get(function(req, res) {
		res.sendFile(app.get('appPath') + '/public/index.html');
		// res.redirect('/');
	});

	// if (config.env && config.env !== 'development') {
		app.use(pmx.expressErrorHandler());
	// }
}



