'use strict';

var mongoose = require('mongoose');
var Schema = mongoose.Schema;
var crypto = require('crypto');
var config = require('../../config');
var cst = require('../../utils/serverConstants');
var _ = require('lodash');
var isSiteAdmin = require('../../auth/siteAdmin-config');

var UserSchema = new Schema({
  name: {
        familyname: String,
        formatted: String,
        givenname: String,
        honorific_prefix: String,
        honorific_suffix: String,
        middlename: String,
        pronunciation: String,
        pronunciation_url: String
    },
  ndbid: String,
  phone_numbers: [Number],
  addresses: [String],
  gender: String,
  displayname: String,
  lastupdatetime: Date,
  username: { type: String, lowercase: true },
  //
  aType: { type: String, default: 'user' }, //  user | anonymous
  hashedPassword: String,
  provider: String,
  salt: String,
  secret: String,
  user_action_required: Boolean,
  emails: [
      {   
          _id: false,
          value: { type: String, lowercase: true },
          type: {type: String},
          primary: Boolean,
          label: String,
          relationdef_id: String,
          cid: { type: Schema.Types.ObjectId, ref: 'Company' },
      }
  ],
  languages: [
      {
          _id: false,
          code: String,
          primary: Boolean
      }
  ],
  timezone: String,

  // picture_url: String,
  picturefile: String,
  relation_graphs: [{
    _id: false,
    relationdef_id: String,
    initiator_id: {type:String, index:true},
    initiator_type: String,
    relation_type: String
  }],
  permissions:[{type: String}]
});

/**
 * Indexes
 */
UserSchema.index({ndbid: 1}, {unique: true});
UserSchema.index({username: 1}, {unique: true});

/**
 * Virtuals
 */
UserSchema.set('toJSON', {
    virtuals: true
});

UserSchema.options.toJSON = {

    transform : function(doc, ret, options) {
        delete ret.__v;
        delete ret.id;        
        return ret;
    },
    virtuals: true
};

UserSchema
  .virtual('password')
  .set(function(password) {
    this._password = password;
    this.salt = this.makeSalt();
    this.hashedPassword = this.encryptPassword(password);
  })
  .get(function() {
    return this._password;
  });

UserSchema
  .path('lastupdatetime')
  .set(function(lastupdatetime) {
    this.lastupdatetime = lastupdatetime.toUTCString();
  });

UserSchema
  .virtual('picture_url')
  .get(function() {
    if (!this.picturefile) {
      return 'https://www.onesna.com/norevimages/noimage.jpg';
    }
    if (this.picturefile.indexOf('http://') > -1 || this.picturefile.indexOf('https://') > -1) {
      return this.picturefile;
    }
    return 'https://storage.googleapis.com/' + config.bucket + '/' + this.picturefile;
  });

// Public profile information
UserSchema
  .virtual('profile')
  .get(function() {
    return {
      'name': this.name,
      'displayname': this.displayname,
      'username': this.username,
      'phone_numbers': this.phone_numbers,
      'picture_url': this.picture_url,
      'addresses': this.addresses,
      'gender': this.gender
    };
  });

// Non-sensitive info we'll be putting in the token
UserSchema
  .virtual('token')
  .get(function() {
    return {
      '_id': this._id,
      'role': this.role
    };
  });

/**
 * Validations
 */

// Validate empty email
UserSchema
  .path('username')
  .validate(function(username) {
    return username.length;
  }, 'Email cannot be blank');

// Validate empty password
UserSchema
  .path('hashedPassword')
  .validate(function(hashedPassword) {
    return hashedPassword.length;
  }, 'Password cannot be blank');

// Validate email is not taken
UserSchema
  .path('username')
  .validate(function(value, respond) {
    var self = this;
    this.constructor.findOne({username: value, role: 'user'}, function(err, user) {
      if(err) throw err;
      if(user) {
        if(self.id === user.id) return respond(true);
        return respond(false);
      }
      respond(true);
    });
}, 'The specified email address is already in use.');

var validatePresenceOf = function(value) {
  return value && value.length;
};

/**
 * Methods
 */
UserSchema.methods = {
  /**
   * Authenticate - check if the passwords are the same
   *
   * @param {String} plainText
   * @return {Boolean}
   * @api public
   */
  authenticate: function(plainText) {
    return this.encryptPassword(plainText) === this.hashedPassword;
  },

  /**
   * Make salt
   *
   * @return {String}
   * @api public
   */
  makeSalt: function() {
    return crypto.randomBytes(16).toString('base64');
  },

  /**
   * Encrypt password
   *
   * @param {String} password
   * @return {String}
   * @api public
   */
  encryptPassword: function(password) {
    if (!password || !this.salt) return '';
    var salt = new Buffer(this.salt, 'base64');
    return crypto.pbkdf2Sync(password, salt, 10000, 64).toString('base64');
  }
};

module.exports = mongoose.model('User', UserSchema);



