'use strict';

import User from '../user/user.model'
import passport from 'passport'
import config from '../../config'
import jwt from 'jsonwebtoken'
import escapeStringRegexp from 'escape-string-regexp'
import mongoose from 'mongoose'
import emailCtrl from '../../modules/email/email.controller'
import util from '../../utils/serverHelper';
import logger from '../../modules/logger/index'
import auth from '../../auth/auth.service'
import vw from '../viewBase/viewBase'
import userBz from '../user/user.backend'
import esErr from '../../errors/errors'
import cst from '../../utils/serverConstants'
import syncBz from './sync.backend.js';
import permGp from '../../auth/permissiongroup.model';
import dbWp from '../../utils/dbwrapper';

var ObjectId = mongoose.Schema.Types.ObjectId;

var validationError = function(res, err) {
  return res.status(422).json(err);
};

exports.syncUsers = class syncUsers extends vw.serverCallView{
  handle(req, res, cb){
    var src = util.getSrcFromRequest(req);
    let data = {users: req.body};
    src.fn = '[syncUsers]';
    logger.info(src.id, '[syncUsers]Start syncing users from ' + config.getEsnaLink(src.domain));
    syncBz.syncUsers(src, data, function (err, result) {
      if (err) {
        logger.error(src.id, '[syncUsers]Error syncing users', err);
        return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.BadRequestError));
      }
      logger.info(src.id, '[syncUsers]Sync finished : ');
      res.status(cst.HttpSuccessStatus).send(result);
    });
  };
};

exports.syncCompanies = class syncCompanies extends vw.serverCallView{
    handle(req, res, cb){
        var src = util.getSrcFromRequest(req);
        src.fn ='[syncCompanies]';
        let data = {companies: req.body};

        logger.info(src.id, '[syncCompanies]Start syncing companies from ' + config.getEsnaLink(src.domain) + ' with payload', data);
        syncBz.syncCompanies(src, data, function (err, result) {
            if (err) {
              logger.error(src.id, '[syncCompanies]Error syncing companies', err);
              return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.BadRequestError));
            }
            logger.info(src.id, '[syncCompanies]Sync finished : ');
            logger.debug(src.id, '[syncCompanies]Sync result to onesna:', result);
            res.status(cst.HttpSuccessStatus).send(result);
        });
    };
};
/**
 * Authentication callback
 */
exports.authCallback = function(req, res, next) {
  res.redirect('/');
};

exports.syncPermissionGroup = class syncPermissionGroup extends vw.serverCallView{
  handle(req, res, cb){
    var src = util.getSrcFromRequest(req);
    var postData = {
      ndbid: req.body.id || '',
      description: req.body.description || '',
      name: req.body.name || '',
      type: req.body.type || '',
      permission_name: req.body.permission_name || '',
      permissions: req.body.permissions || []
    };
    if (!postData.permission_name){
      return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.BadRequestError));
    }
    dbWp.execute(permGp,
                 permGp.findOneAndUpdate,
                 src,
                 {permission_name: postData.permission_name},
                 postData,
                 {upsert: true, new: true},
                 (err, result)=>{
     if (err){
       logger.error(src, 'syncPermissionGroup happen error', err);
       return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.SysUnkownError));
     }
     else{
       res.status(cst.HttpSuccessStatus).json(result.toJSON());
     }
   });
  }
};



