var syncOneUser = exports.syncOneUser = function (src, data, cb) {
	var fn = src.fn + '[syncOneUser]';
	if (data.operate_type === cst.SyncOperateDelete) {
		dbWp.execute(User, User.findOneAndRemove, src.id, {ndbid: data.id}, function (err, deletedUser) {
			if (err) {
				return cb(err);
			}
			if (deletedUser) {
				logger.sync(src.id, fn + 'user deleted on logan ' + deletedUser._id + ' with ndbid: ' + data.id);
				userEvent.emitUserDeleted(src, deletedUser);
			} else {
				logger.sync(src.id, fn + ' user not found on logan with ndbid: ' + data.id);
			}
			return cb(null, deletedUser);
		});
	} else {
		dbWp.execute(User, User.findOne, src.id, {ndbid: data.id}, function(err, user) {
			if (err) {
				return cb(err);
			}
			if (!user || user.lastupdatetime.getTime() < (new Date(data.lastupdatetime)).getTime()) {
				logger.sync(src.id, fn + 'User needs update');
				data.ndbid = data.id;
				data.secret = data.security_token;
				delete data.id;
				dbWp.execute(User, User.findOneAndUpdate, src.id, {ndbid: data.ndbid}, data, {upsert: true, new: true}, function (err,newUser) {
					if (err) {
						return cb(err);
					}
					logger.sync(src.id, fn + 'new user updated with logan id :' + newUser._id);
					logger.sync(src.id, fn + 'this is new User', newUser.toJSON());
					userEvent.emitUserUpdated(src, newUser, user);
					return cb(null, newUser);
				});
			} else {
				logger.sync(src.id, fn + 'This user is already up to date');
				return cb(null, user);
			}
		});
	}
}

