exports.syncUsers = function (src, data, cb) {
	var fn = src.fn + '[syncUsers]';
	var processUsers = function (index, ul, resultList, callback) {
		if (!ul) {
			return callback(new esErr.ESErrors(esErr.BadRequestError));
		}

		var cur_user = ul[index];
		if (!cur_user) {
			return callback(null, resultList);
		} else {
			logger.sync(src.id, fn + 'processing user number ' + index + ' with user Id: ' + cur_user.id + ' with username: ' + cur_user.username);
			  syncOneUser(src, cur_user, function (err, result) {
    			if (err) {
    				logger.error(src.id, fn + 'Error sync user number ' + index + ' with user Id: ' + cur_user.id + ' with username: ' + cur_user.username);
    				resultList.push({id: cur_user.id, index: index, status: 'fail', error: err});
    				return processUsers(index+1, ul, resultList, callback);
    			} else {
					resultList.push({id: cur_user.id, index: index, status: 'success'});
					return processUsers(index+1, ul, resultList, callback);    			
				}
    		});
    	}
	};

	processUsers(0, data.users, [], function(err, processResult) {
		if (err) {
			logger.error(src.id, fn + 'Error while syncing users ',err);
			return cb(err);
		}

		return cb(null,processResult);
	});
};
