var getCompanyOnesna = function (src, data, cb) {
	var fn = src.fn + '[getCompanyOnesna]';
    var url = config.getEsnaLink(src.domain) + '/api/1.0/companies/' + data.companyId + '/logan/';
    var token = config.ESNA_API_KEY;
    var options = {
      url: url,
      headers: {
        'Authorization': 'API_KEY ' + token
      },
      accept: '*/*',
    };
    logger.sync(src.id, fn + 'Access esna grabbing company by url: ' + url);
    request.get(options, function(err, response) {
      if (err) {
        logger.error(src.id, fn + 'from ' + url + ' Wrong response!: ', err.message);
        return cb(new esErr.ESErrors(esErr.AccessOnesnaHappenError));
      }
      if (response.statusCode !== 200) {
      	logger.error(src.id, fn + 'from ' + url + ' Wrong response!: ', response.body);
        return cb(new esErr.ESErrors(esErr.AccessOnesnaHappenError));
      }
      return cb(null, response.body);
    });
}

var updateUserCompanies = function (src, data, cb) {
	var fn = src.fn + '[updateUserCompanies]';
	var processData = function (index, ul, callback) {
		if (!ul) {
			return callback(new esErr.ESErrors(esErr.BadRequestError));
		}

		var cur_company = ul[index];
		if (!cur_company) {
			return callback(null, ul);
		} else {
			dbWp.execute(Company, Company.findOne, src.id, {ndbid: cur_company.initiator_id}, function (err,company) {
				var logCompanyId = cur_company.initiator_id.toString();
				if (err) {
					logger.error(src.id, fn + 'Error finding company with ndbid: ' + logCompanyId);
					return processData(index+1, ul, callback);
				}
				if (company) {
					logger.sync(src.id, fn + 'Found company on logan updated user with ndbid: ' + logCompanyId);
					ul[index].initiator_id = company._id;
					return processData(index+1, ul, callback);
				} else {
					logger.sync(src.id, fn + 'Company not found with ndbid: ' + logCompanyId);
					getCompanyOnesna(src, {companyId: cur_company.initiator_id}, function (err, result) {
						if (err) {
						  return processData(index+1, ul, callback);
						}
						var thisCompany = JSON.parse(result);
						thisCompany.ndbid = thisCompany.id;
						delete thisCompany.id;
						logger.sync(src.id, fn + 'Company grabbed from onesna :', thisCompany);
						dbWp.execute(Company, Company.findOneAndUpdate, src.id, {ndbid: thisCompany.ndbid},thisCompany, {upsert: true, new: true}, function (err,newCompany) {
							if (err) {
								logger.error(src.id, fn + 'Error update company with company ndbid: ' + thisCompany.ndbid + ' with name: ' + thisCompany.name);
								return processData(index+1, ul, callback);
							}
							logger.sync(src.id, fn + 'Company update successfully with: ' + newCompany._id + ' with name: ' + newCompany.name);
							companyEvent.emitCompanyUpdated(src, newCompany);
							ul[index].initiator_id = newCompany._id;
							return processData(index+1, ul, callback);
						});
					});
				}
			});
    	}
	};

	processData(0, data, function(err, processResult) {
		if (err) {
			logger.error(src.id, err);
			return cb(err);
		}

		return cb(null,processResult);
	});
};

var updateCompanyDeletedForUser = function (src, data, cb) {
	var fn = src.fn + '[updateCompanyDeletedForUser]';
	dbWp.execute(User, User.update, src.id, {"relation_graphs.initiator_id": data._id},{$pull: {relation_graphs: {initiator_id: data._id}}}, function (err,updatedUsers) {
		if (err) {
			logger.error(src.id, fn + 'Error delete company in user relation_graphs with company', data);
			return cb(err);
		}
		logger.sync(src.id, fn + ' Updatedusers :'+ updatedUsers.nModified);
		return cb(null);
	});
}

var updateCompanyAddedForUser = function (src, data, cb) {
	var fn = src.fn + '[updateCompanAddedForUser]';
	dbWp.execute(User, User.update, src.id, {"relation_graphs.initiator_id": data.ndbid},{$set: {'relation_graphs.$.initiator_id': data._id}}, function (err,updatedUsers) {
		if (err) {
			logger.error(src.id, fn + 'Error update company in user relation_graphs with company', data);
			return cb(err);
		}
		logger.sync(src.id, fn + ' Updatedusers :'+ updatedUsers.nModified);
		return cb(null);
	});
}
