var userUpdated = function (src, data, cb) {
	logger.sync(src.id, src.fn + 'New User updated! Checking data...');
	updateUserCompanies(src, data.relation_graphs, cb);
};

var companyUpdated = function (src, data, cb) {
	logger.sync(src.id, src.fn + 'New Company updated! Checking data...');
	updateCompanyAddedForUser(src, data, cb);
};

var companyDeleted = function (src, data, cb) {
	logger.sync(src.id, src.fn + 'Onesna Company deleted! Checking data...');
	updateCompanyDeletedForUser(src, data, cb);
};


userEvent.onUserUpdated(function(src, user) {
	var fn = src.fn + '[userEvent.onUserUpdated]';
	userUpdated(src, user, function (err, result) {
		if (err) {
			logger.error(src.id, fn + 'User relation graph update failed');
		} else {
		  	logger.sync(src.id, fn + 'User relation table updated ' + user._id);
		  	dbWp.execute(User, User.findOneAndUpdate, src.id, {_id: user._id},{
		  		$set: {
					relation_graphs: result
				}
			}, {new: true}, function (err,newUser) {
		        if (err) {
		          logger.error(src.id, fn + 'Error updating User relation graph', err);
		        }
	        	logger.sync(src.id, fn + 'User relation graph update successfully for user with id: ' + newUser._id);
      		});
		}
	});
});
companyEvent.onCompanyUpdated(function(src, company){
	var fn = src.fn + '[companyEvent.onCompanyUpdated]';
	companyUpdated(src, company, function (err, result) {
		if (err) {
			logger.error(fn + 'Update 1 company with ' + company._id + ' for all user relation graph failed');
		} else {
		  	logger.sync(fn + 'Update 1 company ' + company._id + ' for all user relation graph finished');
		}
	});
});
companyEvent.onCompanyDeleted(function(src, company) {
	var fn = src.fn + '[companyEvent.onCompanyDeleted]';
	companyDeleted(src, company, function (err, result) {
		if (err) {
			logger.error(fn + 'Delete company with ' + company._id + ' from all user relation graph failed');
		} else {
		  	logger.sync(fn + 'Delete company with ' + company._id + ' from all user relation graph finished');
		}
	});
});


