'use strict';

import passport from 'passport';
import auth from '../auth.service';
import { Router } from 'express';
import accessToken from './accessToken/accessToken.controller';
const router =  new Router();
import vW from '../../api/viewBase/viewBase';

router.post('/accesstokens/add', vW.asView(accessToken.newToken));
router.post('/accesstokens/delete', vW.asView(accessToken.deleteTokens));


module.exports = router;


