'use strict';

import config from '../../config';

var pmx = require('pmx'),
    winston = require('winston');
    //winstonMongoDB = require('winston-mongodb').MongoDB,

//var helpers = require('./helper');

var logger = new winston.Logger({
        levels: { error: 0, warn: 1, sync: 2, info: 3, verbose: 4, debug: 5, silly: 6},
        level: config.logLevel,
        colors: { sync: 'cyan'},
        exitOnError: false,
        transports: [
            new winston.transports.Console({
                timestamp: true,
                colorize: true,
                prettyPrint: true
            })
        ]

    });

var os = require('os');

[
    'info',
    'debug',
    'warn',
    'error',
    'sync',
    'verbose',
    'silly'
].forEach(function (method) {
    exports[method] = function () {
        var args = Array.prototype.slice.call(arguments, 0);
        args.unshift(method);
//        var msg = args[0];
//        args = Array.prototype.slice.call(args, 1);
        var newArgs = [];
        args.forEach(function (arg) {
            if (typeof arg === 'undefined') {
                return;
            } else if (typeof arg === 'string') {
                newArgs.push(arg);
            } else if (!!arg && !!arg.toJSON) {
                newArgs.push(arg.toJSON());
            } else if (!!arg && !!arg.toString && arg.toString() !== '[object Object]') {
                newArgs.push(arg);
            } else {
                newArgs.push(arg);
            }
        });
        if (method === 'error' && !!config.traceOnError) {
            var err = new Error();
            newArgs.push(err.stack);
            pmx.notify(newArgs);
        }

        logger.log.apply(logger, newArgs);
    };
});

var getLogglyCredential = function(){
  return {
    'token': config.logglyToken,
    'subdomain': config.logglySubdomain
  };
};

if (config.env != 'development' && !!config.mongoLog)
{
  var hostname = os.hostname();
  console.log("Start writting logs to loggly now!");
  require('winston-loggly');
  var tags = [os.hostname()];
  
  var addedtag = config.env;
  
  if (hostname.indexOf('socket') > 0){
    addedtag = addedtag + '-socket';
  }
  if (hostname.indexOf('task') > 0){
    addedtag = addedtag + '-task';
  }
  
  if (hostname.indexOf('candidate') > 0){
    addedtag = addedtag + '-candidate';
  }
  tags.push(addedtag);
  let cred = getLogglyCredential();
  /*  
  logger.add(winston.transports.Loggly, {
    token: cred.token,
    subdomain: cred.subdomain,
    tags: tags,
    json:true
  });*/
}

