ClientEnvironment.isLocalTesting = function(port, endpointType) {
	return (port >= ClientEnvironment.LOCAL_TESTING_PORTS.from
	&& port <= ClientEnvironment.LOCAL_TESTING_PORTS.to
	&& endpointType != ClientEnvironment.SERVER_TYPES.loginServer);
};

ClientEnvironment.getGUID = function(){
  Log.log(ns + '[getGUID]');
  let parts = window.location.hostname.split('.');
  let i=0;
  for(; i<parts.length;i++){
    let part = parts[i];
    let cand = part.split('-');
    if(cand.length>0)
      return ClientEnvironment.serversSubdomains[cand[0]].environmentGUID;
    if(ClientEnvironment.serversSubdomains[part])
      return ClientEnvironment.serversSubdomains[part].environmentGUID;
  }
};

ClientEnvironment.getServerRootUrl = function(endpointType, location){
	var func = ns + '[getServerMode]';
	if(!location){
		location = window.location;
	}
	var hostname = location.hostname;
	Log.log(func, 'endpointType', endpointType, 'hostname', hostname);

	var candidate = '';
	var socketPref = '';
	var subDomain = 'localhost';
	var domain = 'localhost';
	if (hostname.indexOf('localhost') < 0) {
		var hst = hostname.split('.');
		Log.log(func, 'hst', hst);
		subDomain = hst[0];
		if(hst.length>0){
			hst.splice(0, 1);
		}


		var candidateTag = subDomain.replace('-socket', '');
		var socketTag = subDomain.replace('-candidate', '');

		var lstCandid = candidateTag.split('-');
		if(lstCandid.length>1){
			subDomain = lstCandid[0];
			candidate = '-' + lstCandid[1];
		}

		var lstSock = socketTag.split('-');
		if(lstSock.length>1){
			subDomain = lstSock[0];
			socketPref = '-' + lstSock[1];
		}


		Log.log(func, 'hst', hst);
		var domain = hst.join('.');
	}
	Log.log(func, 'domain', domain);
	Log.log(func, 'subDomain', subDomain);
	Log.log(func, 'candidate', candidate, 'socketPref', socketPref);

	if(endpointType==ClientEnvironment.SERVER_TYPES.loginServer){
		candidate = '';
		socketPref = '';
	}

	if(endpointType==ClientEnvironment.SERVER_TYPES.eventServer){
		socketPref = '';
	}
	Log.log(func, 'candidate', candidate, 'socketPref', socketPref);


	var subObj = ClientEnvironment.serversSubdomains[subDomain];
	if(subObj) {
		subObj = subObj[endpointType];
		Log.log(func, 'subObj', subObj);
		var port = '';
		var url = '';
		if(ClientEnvironment.isLocalTesting(location.port, endpointType)){
			subObj.port = location.port;
		}
		if(subObj.port){
			port=':' + subObj.port;
		}

		if(subObj.subdomain=='localhost'){
			url = location.protocol + '//' + subObj.subdomain + port;// + '/';
		}
		else {
			url = location.protocol + '//' + subObj.subdomain + socketPref + candidate + '.' + domain + port;// + '/';
		}

		if(endpointType==ClientEnvironment.SERVER_TYPES.wcsServer){
			url = subObj.subdomain;
		}

		Log.log(func, 'url', url);
		return url
	}
};

ClientEnvironment.getGoogleAnalyticKey = function(location){
 var func = ns + '[getGoogleAnalyticKey]';
 if(!location){
  location = window.location;
 }
 var hostname = location.hostname;
 Log.log(func, 'hostname', hostname);

 var candidate = '';
 var socketPref = '';
 var subDomain = 'localhost';
 var domain = 'localhost';
 if (hostname.indexOf('localhost') < 0) {
  var hst = hostname.split('.');
  Log.log(func, 'hst', hst);
  subDomain = hst[0];
  if(hst.length>0){
   hst.splice(0, 1);
  }


  var candidateTag = subDomain.replace('-socket', '');
  var socketTag = subDomain.replace('-candidate', '');

  var lstCandid = candidateTag.split('-');
  if(lstCandid.length>1){
   subDomain = lstCandid[0];
   candidate = '-' + lstCandid[1];
  }

  var lstSock = socketTag.split('-');
  if(lstSock.length>1){
   subDomain = lstSock[0];
   socketPref = '-' + lstSock[1];
  }


  Log.log(func, 'hst', hst);
  var domain = hst.join('.');
 }
 Log.log(func, 'domain', domain);
 Log.log(func, 'subDomain', subDomain);
 Log.log(func, 'candidate', candidate, 'socketPref', socketPref);

 var subObj = ClientEnvironment.serversSubdomains[subDomain];
 if(subObj) {
  return subObj.ga;
 }
 return null;
};

export default ClientEnvironment;

