/**
 * Using Rails-like standard naming convention for endpoints.
 * GET     /company              ->  index
 * POST    /company              ->  create
 * GET     /company/:id          ->  show
 * PUT     /company/:id          ->  update
 * DELETE  /company/:id          ->  destroy
 */

'use strict';

var _ = require('lodash');
var Company = require('./company.model');
var logger = require('../../modules/logger');
import cst from '../../utils/serverConstants';

// Get list of companies
exports.index = function(req, res) {
  Company.find(function (err, companies) {
    
    if(err) { return handleError(res, err); }
    return res.status(cst.HttpSuccessStatus).json(companies);
  });
};

// Get a single Company
exports.show = function(req, res) {
  Company.findById(req.params.id, function (err, company) {
    if(err) { return handleError(res, err); }
    if(!company) { return res.status(404).send('Not Found'); }
    return res.json(company);
  });
};

// Creates a new Company in the DB.
exports.create = function(req, res) {
  Company.create(req.body, function(err, company) {
    if(err) { return handleError(res, err); }
    return res.status(201).json(company);
  });
};

// Updates an existing Company in the DB.
exports.update = function(req, res) {
  if(req.body._id) { delete req.body._id; }
  Company.findById(req.params.id, function (err, company) {
    if (err) { return handleError(res, err); }
    if(!company) { return res.status(404).send('Not Found'); }
    var updated = _.merge(company, req.body);
    updated.save(function (err) {
      if (err) { return handleError(res, err); }
      return res.status(cst.HttpSuccessStatus).json(company);
    });
  });
};

// Deletes a Company from the DB.
exports.destroy = function(req, res) {
  Company.findById(req.params.id, function (err, company) {
    if(err) { return handleError(res, err); }
    if(!company) { return res.status(404).send('Not Found'); }
    Company.remove(function(err) {
      if(err) { return handleError(res, err); }
      return res.status(204).send('No Content');
    });
  });
};

function handleError(res, err) {
  return res.status(cst.HttpErrorStatus).send(err);
}


