/**
 * Checks if the user role meets the minimum requirements of the route
 */
function hasRole(roleRequired) {
  if (!roleRequired) throw new Error('Required role needs to be set');

  return compose()
  .use(isAuthenticated())
  .use(function meetsRequirements(req, res, next) {
    if (config.userRoles.indexOf(req.user.role) >= config.userRoles.indexOf(roleRequired)) {
    next();
    }
    else {
    res.status(403).send('Forbidden');
    }
  });
}

/**
 * Returns a jwt token signed by the app secret
 */
function signToken(id) {
  return jwt.sign({ _id: id }, config.secrets.session, { expiresInMinutes: 60*5 });
}

/**
 * Set token cookie directly for oAuth strategies
 */
function setTokenCookie(req, res) {
  if (!req.user) return res.status(404).json({ message: 'Something went wrong, please try again.'});
  var token = signToken(req.user._id, req.user.role);
  res.cookie('token', JSON.stringify(token));
  // res.redirect('/');
  return res.status(cst.HttpSuccessStatus);
}

class JwtAuthenticator {
  constructor(){
    this.name = 'JwtAuthenticator';
  }
  
  getJwtToken(req, cb) {
    var token;
    if (req.headers && req.headers.authorization) {
      var parts = req.headers.authorization.split(' ');
      if (parts.length == 2) {
        var scheme = parts[0].toLowerCase();
        var credentials = parts[1];

        if (/^jwt$/i.test(scheme)) {
          token = credentials;
        } else {
          return null;
        }
      } else {
        return null;
      }
    }
    return token;
  }

  getUserOnesna(cb) {
    var functionName = '[JwtAuthenticator.getUserOnesna] ';
    var url = config.getEsnaLink(this.request.esDomain) + '/api/1.0/users/self/logan';
    var token = this.token;
    var options = {
      url: url,
      headers: {
        'Authorization': 'jwt ' + token
      },
      accept: '*/*',
    };
    var self = this;    
    logger.info(self.reqId, functionName + 'Access esna by url ' + url);
    request.get(options, function(err, response) {
      if (err || response.statusCode !== 200) {
        logger.error(self.reqId, functionName + 'Wrong response!: ');
        return cb(new esErr.ESErrors(esErr.AccessOnesnaHappenError));
      }
      return cb(null, response.body);
    });
  }


  get_secret_token_user(userid, cb) {
    var functionName = '[JwtAuthenticator.get_secret_token_user] ';
    var self = this;
    var reqid = this.reqId;
    var oldUser = null;
    
    async.waterfall([
      function(callback){
        let exeobj = User.findOne({ndbid: userid}, {displayname:1, lastupdatetime:1, username:1, picturefile:1, permissions:1, secret:1, relation_graphs:1, emails:1}).lean();
        dbWp.execute(exeobj, exeobj.exec, reqid, callback);        
      },
      function(user, callback){
        if (user){
          user.aType = cst.TypeUser;
          oldUser = user;
        }
        if (!user || user.lastupdatetime.getTime() < (new Date(self.payload.lastupdatetime)).getTime()){
          logger.info(reqid, functionName + 'No user found, lets go to onesna');
          self.getUserOnesna(callback);
        }
        else{
          return cb(null, user);
        }
      },
      function(newUser, callback){
        var loganUser = JSON.parse(newUser);
        loganUser.ndbid = loganUser.id;
        loganUser.secret = loganUser.security_token;
        delete loganUser.id;
        dbWp.execute(User, User.findOneAndUpdate, reqid, {ndbid: loganUser.ndbid},loganUser, {upsert: true, new: true}, (err, savedUser)=>{
          if (!err){
            return callback(null, savedUser);
          }
          else if (err.code == esErr.DBErrorDuplicateKey){
            //Under ndbid unique and username unique condition, happen such error only possible be username duplicate
            //To avoid problem of endless loop, get user by username and replace secret in memory to make verify pass
            logger.info(reqid, functionName + 'Happen duplicate username ' + loganUser.username + '. Get user by same username from db!');
            dbWp.execute(User, User.findOne, reqid, {username: loganUser.username}, (err, savedUser)=>{
              if (err || (!savedUser)){
                logger.error(reqid, functionName + 'Failed to get user by username ' + loganUser.username);
                return cb(new esErr.ESErrors(esErr.AuthenticateErrorJWT));
              }
              savedUser.fakeSecret = loganUser.secret;
              return callback(null, savedUser);
            });
          }
          else{
            return callback(err, savedUser);
          }
        });
      },
      function(savedUser, callback){
        if (!savedUser) {
          logger.error(reqid, functionName + 'Error while save/update Logan user');
          return callback(new esErr.ESErrors(esErr.DBError));
        }
        userEvent.emitUserUpdated({id: reqid, domain:self.request.esDomain, type:'req', fn: '[JwtAuthenticator]'}, savedUser, oldUser);
        logger.info(reqid, functionName + 'Logan user saved/updated!');
        return callback(null, savedUser);
      }
    ],function(err, result){
        if (err){
          logger.error(reqid, functionName + ' happen error!', err.message);
        }
        return cb(err, result);
      }
    );
  }

  verifyToken(secret, token, req, res, cb) {
    var functionName = '[JwtAuthenticator.verifyToken] ';
    var self = this;
    var verify = expressJwt({
      secret: secret + config.commonSecretJwt,
      getToken: function () {
        return token;
      }
    });
    verify(req, res, function(err, result){
      if (err){
        logger.info(req.id, functionName + 'Verify failed for token ' + token);
        return cb(err);
      }
      else{
        //verify domain of jwt
        if (util.getIssFromHostname(req.hostname) != self.payload.iss &&
            util.getIssFromHostnameOld(req.hostname) != self.payload.iss){
          logger.info(req.id, functionName + 'Issure is not correct with value ' + self.payload.iss);
          return cb(new esErr.ESErrors(esErr.AuthenticateErrorJWT));
        }
        else{
          return cb(null);
        }
      }
    });
  }

  auth(req, res, cb){   
    var functionName = '[JwtAuthenticator.auth] ';
    var token = this.getJwtToken(req);
    this.reqId = req.id;

    if (!token) {
      logger.info(req.id, functionName + 'No jwt token in header');
      return cb(new esErr.ESErrors(esErr.AuthenticateErrorJWT));
    }
    this.token = token;
    var payload = jwt.decode(token);
    if (!payload){
        logger.warn(req.id, functionName + 'Payload is undefined in token' + token);
        return cb(new esErr.ESErrors(esErr.AuthenticateErrorJWT));
    }
    if (!payload.user_id){
      logger.warn(req.id, functionName + 'user_id is undefined in Payload of token' + token);
      return cb(new esErr.ESErrors(esErr.AuthenticateErrorJWT));
    }
    var uid = payload.user_id;
    var self = this;
    var userObj = null;
    this.payload = payload;
    this.request = req;
    async.waterfall([
      function(callback){
        self.get_secret_token_user(uid, callback);
      },
      function(userObj, callback){
        self.userObj = userObj;
        if (userObj.ndbid == uid){
          self.verifyToken(userObj.secret, token, req, res, callback);
        }
        else{
          if (userObj.fakeSecret){
            self.verifyToken(userObj.fakeSecret, token, req, res, (err)=>{
              if (err){
                return self.verifyToken(userObj.secret, token, req, res, callback);
              }
              else{
                return callback(null)
              }
            });
          }
          else{
            self.verifyToken(userObj.secret, token, req, res, callback);
          }
        }
      },
    ], function(err, result){
      if (err){
        logger.warn(req.id, functionName + 'Verify Token :' + token + ' has Error: ', err);
        return cb(new esErr.ESErrors(esErr.AuthenticateErrorJWT));
      }
      else{
        if (self.userObj.fakeSecret){
          delete self.userObj.fakeSecret;
        }
        req.user = self.userObj;
        req.auth = payload;
        req.auth.authenType = cst.AuthenticateTypeJWT;
        userBk.getUserPermissions(req, req.user, (err, result)=>{
          req.user.extendPermissions = result;
          return cb();
        })
      }
    });
  }
}

class Oauth2Authenticator {
  constructor(){
    this.name = 'Oauth2Authenticator';
  }
  getBearerToken(req) {
    var token;
    if (req.headers && req.headers.authorization) {
      var parts = req.headers.authorization.split(' ');
      if (parts.length == 2) {
        var scheme = parts[0].toLowerCase();
        var credentials = parts[1];

        if (/^bearer$/i.test(scheme)) {
          token = credentials;
        } else {
          return null;
        }
      } else {
        return null;
      }
    }
    return token;
  }
  
  verify(token, cb){
    var functionName = '[Oauth2Authenticator.verify] ';
    var reqid = this.reqId;
    var self = this;
    async.waterfall(
      [function(callback){
        //Get token information from table accesstoken
        dbWp.execute(OAuthAccessTokens, OAuthAccessTokens.findOne, reqid, {accessToken: token}, callback);
       },     
       function(accessToken, callback){
         if (!accessToken){
             logger.warn(reqid, functionName + "Can't get access token " + token + " from db");
             return cb(new esErr.ESErrors(esErr.OAuth2AccessTokenNotExisted));
         }
         //Check the user is existed in system 
         if ((new Date(accessToken.expires)).getTime() < Date.now()){
             //Expired
             return cb(new esErr.ESErrors(esErr.AuthenticateErrorOauth2));
         }
         self.accessToken = accessToken;
         let exeobj = User.findOne({_id: accessToken.userId}, {displayname:1, lastupdatetime:1, username:1, picturefile:1, permissions:1, secret:1, aType:1, relation_graphs:1, emails:1}).lean();
         dbWp.execute(exeobj, exeobj.exec, reqid, callback);
       },
       function(user, callback){
         //Not exist throw error
         if (!user){
           logger.warn(reqid, functionName + "Can't get user object by id " + accessToken.userId + " from db");
           return cb(new esErr.ESErrors(esErr.AuthenticateErrorOauth2));
         }
         else{
           user.aType = cst.TypeUser;
           return cb(null, user);
         }
       }
      ], 
      function(err, result){
        if (err){
          logger.error(reqid, functionName + 'Happen error!', err);
        }        
        return cb(err, result);
    });
  }
  
  auth(req, res, cb) {
    var functionName = '[Oauth2Authenticator.auth] ';
    var token = this.getBearerToken(req);
    if (!token){
      logger.info(req.id, functionName + 'No oauth2 access token in header');
      return cb(new esErr.ESErrors(esErr.AuthenticateErrorOauth2));
    }
    this.reqId = req.id;
    var self = this;
    this.verify(token, function(err, result){
      if (err){
        return cb(new esErr.ESErrors(esErr.AuthenticateErrorOauth2));
      }
      else{
        req.user = result;
        req.auth = self.accessToken;
        req.auth.authenType = cst.AuthenticateTypeOAuth2;
        userBk.getUserPermissions(req, req.user, (err, result)=>{
          req.user.extendPermissions = result;
          return cb();
        })
      }
    });  
  }
}
class OnesnaServerAuthenticator {
  constructor(){
    this.name = 'OnesnaServerAuthenticator';
  }
  auth (req, res, cb) {
    var functionName = '[OnesnaServerAuthenticator.auth] ';
    if (!req.headers || !req.headers.authorization || req.headers.authorization !== 'API_KEY ' + config.ESNA_API_KEY) {
      logger.info(req.id, functionName + 'No api key in header');
      return cb(new esErr.ESErrors(esErr.AuthenticateFailedOnesnaServer));
    }
    return cb();
  }
}

class AnonymousAuthenticator {
  constructor(){
    this.name = 'AnonymousAuthenticator';
  }
  getJwtToken(req, cb) {
    var functionName = '[AnonymousAuthenticator.getJwtToken] ';
    var token;
    if (req.headers && req.headers.authorization) {
      var parts = req.headers.authorization.split(' ');
      if (parts.length == 2) {
        var scheme = parts[0].toLowerCase();
        var credentials = parts[1];

        if (/^jwt$/i.test(scheme)) {
          token = credentials;
        } else {
          return null;
        }
      } else {
        return null;
      }
    }
    return token;
  }

  getAnonymousUser(src, id, callback) {
    var functionName = '[AnonymousAuthenticator.getAnonymousUser] ';
    dbWp.execute(Anonymous, Anonymous.findById, null, id, function(err, anonymousUser){
      if (err || !anonymousUser) {
        logger.error(src.id, functionName + 'Can not get anonymous user from jwt ID ' + id);
        return callback(new esErr.ESErrors(esErr.AuthenticateErrorAnonyJWT));
      }
      logger.info(src.id, functionName + 'Get anonymousUser from JWT');
      return callback(null, anonymousUser);
    });
  }

  auth(req, res, cb){
    var functionName = '[AnonymousAuthenticator.auth] ';
    var token = this.getJwtToken(req);
    this.reqId = req.id;
    if (!token) {
      logger.info(req.id, functionName + 'No jwt token in header');
      return cb(new esErr.ESErrors(esErr.AuthenticateErrorAnonyJWT));
    }
    this.token = token;
    var payload = jwt.decode(token);
    if (!payload){
        logger.warn(req.id, functionName + 'Payload is undefined in token' + token);
        return cb(new esErr.ESErrors(esErr.AuthenticateErrorAnonyJWT));
    }
    if (payload.anonymous_id){
      this.getAnonymousUser({id:req.id}, payload.anonymous_id, function (err, anonymous) {
        if (err) {
          return cb(new esErr.ESErrors(esErr.AuthenticateErrorAnonyJWT));
        }
        var verify = expressJwt({
          secret: anonymous.secret,
          getToken: function () {
            return token;
          }
        });
        verify(req, res, function(err, result){
            if (err){
              logger.warn(req.id, functionName + 'Verify toekn [' + token + '] failed!');
              return cb(new esErr.ESErrors(esErr.AuthenticateErrorAnonyJWT));
            } else{
              req.user = undefined;
              req.anonymousUser = anonymous;
              req.auth = payload;
              req.auth.authenType = cst.AuthenticateTypeAnonymous;
              return cb();
            }
        });
      });
      
    } else{
      logger.warn(req.id, functionName + 'There is no anonymous_id in toekn ' + token);
      return cb(new esErr.ESErrors(esErr.AuthenticateErrorAnonyJWT));
    }
  }
}

class AnonymousAuthenticator4001 extends AnonymousAuthenticator {
  constructor(){
    super();
    this.name = 'AnonymousAuthenticator4001';
  }
  auth(req, res, cb){
    var functionName = '[AnonymousAuthenticator4001.auth] ';
    return super.auth(req, res, function(err){
      if (err) {
        logger.error(req.id, functionName + 'Happen error!', err);
        return cb(new esErr.ESErrors(esErr.AuthenticateErrorAnony4001JWT));
      } else {
        return cb();
      }
    });
  }
}

class EsnaServerAuthenticator{
  constructor(){
    this.name = 'EsnaServerAuthenticator';
  }
  getApiKey(req) {
      var token;
      if (req.headers && req.headers.authorization) {
        var parts = req.headers.authorization.split(' ');
        if (parts.length == 2) {
          var scheme = parts[0].toLowerCase();
          var credentials = parts[1];            
          if (/^API_KEY$/i.test(scheme)) {
            token = credentials;
          } else {
            return null;
          }
        } else {
          return null;
        }
      }
      return token;
  }
  auth(req, res, cb){
    let functionName = '[EsnaServerAuthenticator.auth] ';
    let api_key = this.getApiKey(req);
    if (api_key === config.ESNA_API_KEY){            
        req.user = {
            _id: '__virtual_id',
            username: 'ServerSuperUser',
            aType: cst.TypeUser,
            permissions: ['SITE_ADMIN_PERMISSION_GROUP']
        };
          req.auth = {authenType: cst.AuthenticateTypeEsnaServer};
        userBk.getUserPermissions(req, req.user, (err, result)=>{
          req.user.extendPermissions = result;
          return cb();
        });
    } 
    else{
        logger.info(req.id, functionName + 'api key '+ api_key +' is not avaliable');
          cb(new esErr.ESErrors(esErr.AuthenticateErrorEsnaServer));
    }
  }
}

export default {
    hasRole: hasRole,
    signToken: signToken,
    setTokenCookie: setTokenCookie,
    JwtAuthenticator: JwtAuthenticator,
    Oauth2Authenticator: Oauth2Authenticator,
    AnonymousAuthenticator:AnonymousAuthenticator,
    AnonymousAuthenticator4001: AnonymousAuthenticator4001,
    EsnaServerAuthenticator: EsnaServerAuthenticator
}



