/**
 *  To provide this backend is because aspose is very good at converting documents to pdf (for example page size process)
 *  But aspose is very expensive to split pdf to svg files and cloudconvert is relatively cheap to split pdf to svg file.
 *  So this backend apply the advantages of these two providers aspose converts documents to pdf.
 *  cloudconvert split pdf to svg files
 */

import fileViewCommon from './fileview.common';
import fs from '../../modules/file';
import gcs from 'gcloud/lib/storage';
import gcloudConfig from '../../modules/file/gcs/config.js';
import async from 'async';
import logger from '../../modules/logger';
import TopicMessage from '../message/message.model';
import dbWp from '../../utils/dbwrapper';
import esErr from '../../errors/errors';
import process from 'process';
import request from 'request';
import util from '../../utils/serverHelper';
import ndutil from 'util';
import cst from '../../utils/serverConstants';
import crypto from 'crypto';
import config from '../../config';
import messageEvt from '../message/message.event';
import crtHelp from './converter_helper';

class excelToSvgFileView extends crtHelp.cloudConvertToSvgFileView{
  toPdf(inData, cb){
    let functionName = '[excelToSvgFileView.toPdf] ';
    let fileObj = inData.fileObj
    let self = this;
    let oriDocument = inData.oriDocument || 'oridocument';
    if (fileObj.pages){
      logger.info(self.src.id, functionName + 'The slides file already get page number before. pages=' + fileObj.pages);
      return cb(null, fileObj); 
    }
    let url = crtHelp.getAsposeApiBaseUrl() + 'cells/' + encodeURI(oriDocument + '.' + self.informat) + '/saveAs?' + 
              'storage=' + encodeURI(crtHelp.asposeStorageName) +
              '&newfilename=' + encodeURI(oriDocument + '.pdf') +
              '&folder=' + encodeURI('logan/' + self.fileId + '_convert');
    try{
      url = crtHelp.asposSign(self.src, url);
    }
    catch(err){
      logger.error(self.src.id, functionName + 'When sign aspose url happen error, no retry any more', err);
      self.setConvertStatus(cst.ConvertStatusFailed, ()=>{
        return cb(new esErr.ESErrors(esErr.TaskNoRetryError));
      });
    }
    let postXmlData = "<PdfSaveOptions><SaveFormat>Pdf</SaveFormat><OnePagePerSheet>True</OnePagePerSheet></PdfSaveOptions>";
    logger.info(self.src.id, functionName + 'Query slides pages by url' + url + " with post xml data" + postXmlData);
    let options = {url: url,
      headers: {"Content-Type": "application/xml"},
      body: postXmlData
    };
    request.post(options, (err, response, body)=>{
      if (!err && response.statusCode == 200){
        logger.info(self.src.id, functionName + 'Convert excel to pdf successfully');
        return cb(null, fileObj);
      }
      else{
        logger.warn(self.src.id, functionName + 'Convert excel to pdf faild', err, response.statusCode, body);
        return cb(new esErr.ESErrors(esErr.FileConvertFailed));
      }
    });
  }
}

export default {
  excelToSvgFileView: excelToSvgFileView,
};



