exports.MeInfoView = class MeInfoView extends vw.anyCallView{
  handle(req, res, cb){
    logger.info(req.id , 'begin MeInfoView');
    if (req.anonymousUser){
      req.anonymousUser.secret = undefined;
      return res.json(req.anonymousUser);
    }
    else if (req.user){
	    //var user = req.user.toJSON();	    
	    User.findOne({'_id': req.user._id}, (err, user)=>{
	      if (user){
  	      user.secret = undefined;
  	      user.permissions = req.user.extendPermissions;
  	      return res.json(user);
	      }
	      else{
	        return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.SysUnkownError));
	      }
	    })
	    //user.permissions = req.user.extendPermissions;
	    //return res.json(user);
    }
    else{
      return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.SysUnkownError));
    }
  }
};

exports.SearchColleaguesView = class SearchColleaguesView extends vw.regUserCallView{
  handle(req, res, cb){
    logger.info(req.id, 'begin SearchColleaguesView');
    var str;
    var query = [];
    if (req.query) {
      str = req.query.search;
    }
    if (str) {
      str = escapeStringRegexp(str);
    }
    var src = util.getSrcFromRequest(req);
    var data = {
      search: str,
      user: req.user
    };
    userBz.searchColleagues(src, data, function(err, users){
      return res.status(cst.HttpSuccessStatus).json(users);
    })
  }
};

exports.listTopicsForMe = class listTopicsForMe extends vw.anyCallView{
  handle(req, res, cb){
    var me = req.user || req.anonymousUser;
    logger.info(req.id, me.aType + ' begin listTopicsForMe :' + me.username + ' with userId: ' + me._id);
    var page = Number(req.query.page) || 1,
        size = Number(req.query.size) || 10,
        size = size > 20 ? 20: size,
        src = util.getSrcFromRequest(req),
        apiRoute = '/api/users/me/spaces';

    var data = {
      user: me,
      pagination: {
        skip: (page-1)*size,
        limit: size,
        apiRoute: apiRoute
      },
      search: req.query.search,
      order: req.query.orderBy
    };

    var callback = function(err, result) {
      if (err || !result) {
        return res.status(cst.HttpSuccessStatus).json({data: []});
      }
      var returnData = {
        data: result.data,
        from: (page - 1) * size + 1,
        to: (page - 1) * size + result.data.length
      }
      if (result.hasNext) {
        returnData.nextPageUrl = apiRoute + '?page=' + (page + 1) + '&size=' + size;
        if (data.search) {
          returnData.nextPageUrl += '&search=' + data.search;
        }
        if (data.filterType){
          returnData.nextPageUrl += '&filtertype=' + data.filterType;
        }
      }
      if (page > 1) {
        returnData.previousPageUrl = apiRoute + '?page=' + (page - 1) + '&size=' + size;
        if (data.search) {
          returnData.previousPageUrl += '&search=' + data.search;
        }
        if (data.filterType){
          returnData.previousPageUrl += '&filtertype=' + data.filterType;
        }
      }
      return res.status(cst.HttpSuccessStatus).json(returnData);
    }

    if (data.search && data.search.length > 0) {
      userBz.listTopicsForMe(src, data, callback);
    } else {
      let fillType = req.query.filtertype || 'all';
      data.filterType = fillType;
      userBz.listRecentAccessedTopicsForMe(src, data, callback);
    }    
  }
};

exports.listTopicsForUser = class listTopicsForUser extends vw.DeveloperAdminView{
  handle(req, res, cb){
    
    if (!req.user) {
      return res.status(cst.HttpUnauthorizedStatus).json(new esErr.ESErrors(esErr.AuthenticateFailed));
    }
    logger.info(req.id, ' begin listTopicsForUser with userId: ' + req.params.id);

    if (!req.params || !req.params.id) {
      return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.BadRequestError));
    }

    var page = Number(req.query.page) || 1,
        size = Number(req.query.size) || 10,
        src = util.getSrcFromRequest(req),
        apiRoute = '/api/users/' + req.params.id + '/spaces';

    var data = {
      pagination: {
        skip: (page-1)*size,
        limit: size,
        apiRoute: apiRoute
      },
      search: req.query.search,
      objId: req.params.id
    };

    userBz.listTopicsForMe(src, data, function(err, result){
      if (err || !result) {
        return res.status(cst.HttpSuccessStatus).json([]);
      }
      var returnData = {
        data: result.data,
        from: (page - 1) * size + 1,
        to: (page - 1) * size + result.data.length
      }
      if (result.hasNext) {
        returnData.nextPageUrl = apiRoute + '?page=' + (page + 1) + '&size=' + size;
        if (data.search) {
          returnData.nextPageUrl += '&search=' + data.search;
        }
      }
      if (page > 1) {
        returnData.previousPageUrl = apiRoute + '?page=' + (page - 1) + '&size=' + size;
        if (data.search) {
          returnData.previousPageUrl += '&search=' + data.search;
        }
      }
      return res.status(cst.HttpSuccessStatus).json(returnData);
    });
  }
};

