/**
 * Get list of users using mini-mongo
 * restriction: 'admin'
 */
exports.index = function(req, res) {
  var func = 'user.controller[mongo]';
  try{
    var limit = req.query.limit || 20;
    var skip = req.query.skip || 0;
    limit = {limit: limit};
    skip = {skip: skip};

    var selector = req.query.selector;
    if(selector){
      selector = JSON.parse(selector);
    }

    if(selector._id == 'me'){
      selector._id = req.user._id;
    }

    console.log(func, 'after', 'limit', limit, 'selector', selector);
    if(selector._id){
      User.findById(selector._id, function (err, user) {
        if (err) return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.BadRequestError));
        console.log(func, 'user', user.profile);
        if (!user) return res.status(cst.HttpUnauthorizedStatus).send('Unauthorized');
        return res.status(cst.HttpSuccessStatus).json([user.profile]);
      });
    }
    else {
      User.find(selector, limit, skip, function (err, users) {//, '-salt -hashedPassword'
        console.log(func, 'users', users);
        if(err) return res.status(cst.HttpErrorStatus).send(err);
        res.status(cst.HttpSuccessStatus).json(users);
      })
    }


  }
  catch(err){
    console.error(func, err);
  }

};

/**
 * Get a single user
 */
exports.show = class show extends vw.ViewBase{
    handle(req, res, cb){
        var userId = req.params.id;

        User.findById(userId, function (err, user) {
            if (err) {
              logger.error(err);
              return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.BadRequestError));
            }
            if (!user) return res.status(cst.HttpUnauthorizedStatus).send('Unauthorized');
            res.json(user.profile);
        });
    };
};
/**
 * Authentication callback
 */
exports.authCallback = function(req, res, next) {
  res.redirect('/');
};

exports.listTasksForMe = class listTasksForMe extends vw.regUserCallView{
  _listTopic(req, queryData, cb){
    var self = this;    
    userBz.listTasksByUserId(req, queryData, (err, results) =>{
      if (err){
        logger.error(req.id, 'listTasksForMe call _listTopic happen error', err.message);
        return cb(err);
      }
      if (queryData.prevRefObjId && results.results.length < queryData.size){
        delete queryData.prevRefObjId;
        queryData.page = 1;
        return self._listTopic(req, queryData, cb);
      }
      else if(results.results.length === 0 && queryData.nextRefObjId){
        queryData.prevRefObjId = queryData.nextRefObjId;
        delete queryData.nextRefObjId;
        queryData.page -= 1;
        queryData.includeEqual = true;
        return self._listTopic(req, queryData, (err, data) => {
          if (data.results){
            data.results.havingNextPage = false;
          }
          return cb(null, data);
        });
      }

      delete queryData.sender;
      let data = {
          queryData: queryData,
          results: results
      };
      return cb(null, data);
    });  
  }
  
  handle(req, res, cb){
    let queryData = {
      sender: req.user._id,
      size: parseInt(req.query.size) || 30,
      page: parseInt(req.query.page) || 1,
      nextRefObjId: req.query.nextRefObjId,
      prevRefObjId: req.query.prevRefObjId,
      category: MessageConstants.ACTIVITY_CATEGORY_TYPES.TASK
    };

    this._listTopic(req, queryData, (err, data) => {
      messageBk.toDownloadableClientFormatMessages(req, data.results.results, (err, convertedResults) => {
        messageBk.fillTopicTitleInMsgs(req, convertedResults, (err, convertedResults)=>{
          if (err){
            return res.json({data:[], total:0, nextPageUrl:'', previousPageUrl:''});
          }
          data.results.results = convertedResults;
          return res.json(util.createPagination(req, data));
        })        
      });
    });
  }
};

