'use strict';

//Version 1.0

import { Router } from 'express';
import controller from './topic.controller';
import auth from '../../auth/auth.service';
import {asView} from '../viewBase/viewBase';
const router = new Router();

router.get('/', asView(controller.index));
router.post('/invite', asView(controller.newTopic));
router.get('/invites/:inviteId/join', asView(controller.joinTopicFromInvite));
router.get('/invites/:inviteId', asView(controller.getInvite));
router.post('/invites/:inviteId', asView(controller.updateInvite));
router.delete('/invites/:inviteId', asView(controller.deleteInvite));
router.post('/:topicId/invite', asView(controller.addToTopic));
router.get('/:topicId/members', asView(controller.getMembersOfTopic));
//router.get('/search', asView(controller.searchTopics));
router.get('/:topicId/join', asView(controller.join));
router.get('/:topicId', asView(controller.show));
router.post('/', asView(controller.create));
router.post('/:topicId', asView(controller.update));
//router.patch('/:topicId', asView(controller.update));

router.get('/:topicId/messages/byref', asView(controller.ListMessageByRefView));
router.get('/:topicId/tasks', asView(controller.ListOfTopicTasksView));
router.post('/:topicId/tasks', asView(controller.addTaskView));
router.get('/:topicId/ideas', asView(controller.ListOfTopicIdeasView));
router.post('/:topicId/ideas', asView(controller.addIdeaView));

export default router;



