import { Router } from 'express';
import controller from './idea.controller';
import {asView} from '../viewBase/viewBase';
const router = new Router();

router.get('/:ideaId/messages', asView(controller.listMessagesView));
router.delete('/:ideaId', asView(controller.deleteIdea));

export default router;


