'use strict';

var uuid =require('node-uuid');
var logger = require('../modules/logger');
var morgan = require('morgan');
var os = require("os");
var errorHandler = require('errorhandler');
var config = require('./environment');
var async = require('async');
var pmx = require('pmx');
var winstonStream = {
        write: function(message, encoding){
            logger.info(message);
        }
    };

const WORLDWIDEWEBHEADER = 'www.';
morgan.token('id', function getId(req){
        return req.id;
    })

morgan.format('logan-onServer', function developmentFormatLine(tokens, req, res) {
// get the status code if response written
    var status = res._header
      ? res.statusCode
      : undefined

    // get status color
    var color = status >= 500 ? 31 // red
      : status >= 400 ? 33 // yellow
      : status >= 300 ? 36 // cyan
      : status >= 200 ? 32 // green
      : 0 // no color

    // get colored function
    var fn = developmentFormatLine[color]

    if (!fn) {
      // compile
      fn = developmentFormatLine[color] = morgan.compile('MorganRequest :id :remote-addr - :remote-user [:date] \x1b[0m:method :url \x1b['
        + color + 'm:status \x1b[0m:response-time ms - :res[content-length]\x1b[0m :referrer :user-agent')
    }

    return fn(tokens, req, res);
});

 
morgan.format('logan-dev', function developmentFormatLine(tokens, req, res) {
// get the status code if response written
    var status = res._header
      ? res.statusCode
      : undefined

    // get status color
    var color = status >= 500 ? 31 // red
      : status >= 400 ? 33 // yellow
      : status >= 300 ? 36 // cyan
      : status >= 200 ? 32 // green
      : 0 // no color

    // get colored function
    var fn = developmentFormatLine[color]

    if (!fn) {
      // compile
      fn = developmentFormatLine[color] = morgan.compile(':id \x1b[0m:method :url \x1b['
        + color + 'm:status \x1b[0m:response-time ms - :res[content-length]\x1b[0m')
    }

    return fn(tokens, req, res)
});

var appendReqId = function (req, res, next) {
    req.id = uuid.v4();
    if (next) {
      next();
    }
};

var appendHost = function (req, res, next) {
    res.header('Logan-Host',os.hostname());
    if (next) {
      next();
    }
};

var appendVersion = function (req, res, next) {
    res.header('Logan-Version',config.version);
    if (next) {
      next();
    }
};

var getDomainFromHost = function(req, res, next){
    var domain = req.hostname;
    if (domain.startsWith(WORLDWIDEWEBHEADER)){
        domain = domain.substr(WORLDWIDEWEBHEADER.length);
    }
    req.esDomain = domain;
    if (next) {
        next();
    }
};

var getProcessMeta = function(req, res, next){
    var meta = {
        host: os.hostname(),
        version: config.version,
        pid: process.pid,
        platform: process.platform,
        deployment_type: config.env
    };
   
    req.meta = meta;
    if (next) {
        next();
    }
};

var checkSysUA = function (req, res, next) {
    if (process.env['mongoUA'] === 'disconnected') {
        logger.error(req.id, 'Database connection stopped');
        return res.status(cst.HttpCriticalErrorStatus).json('Database connection Error');
    }
    if (next) {
        next();
    }
};

function parallel(middlewares) {
  return function (req, res, next) {
    async.each(middlewares, function (mw, cb) {
      mw(req, res, cb);
    }, next);
  };
};

module.exports = function(app) {
    var env = app.get('env');
    app.use(parallel([
      appendReqId,
      appendHost,
      appendVersion,
      getDomainFromHost,
      getProcessMeta
      
    ]));
    app.use(checkSysUA);
    app.set('appPath', config.root + '/build');

    
    // CORS configuration
    // -----------------------------------------------------------------------------
    
    // var cors = {
    //   origin: config.allowOrigins
    // };

    // app.use(function(req, res, next) {
    //     var origin = cors.origin.indexOf(req.header('origin').toLowerCase()) > -1 ? req.headers.origin : cors.origin[0];
    //     res.header('Access-Control-Allow-Origin', origin);
    //     res.header('Access-Control-Allow-Methods', 'GET,PUT,POST,DELETE');
    //     res.header('Access-Control-Allow-Headers', 'Content-Type');
    //     next();
    // });

    app.use(morgan('logan-onServer',{stream:winstonStream}));
    if ('logan-production' === env || 'logan-staging' === env) {
        // app.use(pmx.expressErrorHandler());
    }
    if ('development' === env || 'logan-testing' === env) {
        app.use(pmx.expressErrorHandler());
    }

};



