exports.addRelations = function(src, data, cb){
  //Comment out the function
  return cb(null);
  //The input data is array of relationship
  let failedAddedRelations = [];
  async.each(data, (relationItem, interCallback)=> {
    //relationItem._id = new mongoose.Types.ObjectId();
    dbWp.execute(Relation, Relation.create, src.id, relationItem, (err, addedrelationItem) => {
      if (err || (!addedrelationItem)){
        if (err){
          logger.warn(src.id, "addRelations with object failed for reason", err);
        }
        logger.warn(src.id, "addRelations with object failed", relationItem);
        failedAddedRelations.push(relationItem);
        interCallback();
      }
      else{
        //Trigger Event to update initiator or target
        rebuildRelationGraphs(src,
                              addedrelationItem, 
                              (err, result) => {interCallback()});
      }      
    });
  }, function(){
    return cb(null, failedAddedRelations);
  });
};

exports.delRelations = function(src, data, cb){
  //The input data is array of relationship
  let failedRemovedRelations = [];
  async.each(data, (relationItem, interCallback) => {
    dbWp.execute(Relation,
      Relation.findOneAndRemove,
      src.id,
      {relationdef_id: relationItem.relationdef_id},
      (err, removedItem) => {
        if (err || (!removedItem)){
          if (err){
            logger.warn(src.id, "delRelations with object failed for reason", err);
          }
          logger.warn(src.id, "delRelations with object failed", relationItem);
          failedRemovedRelations.push(relationItem);
          interCallback();
        }
        else{
          //Trigger Event to update initiator or target
          rebuildRelationGraphs(src,
                                addedrelationItem,
                                (err, result) => {interCallback()});
        }
    });
    },function(){
      return cb(null, failedRemovedRelations);
  });
};

exports.deleteTargetObj = function(src, targetObj, targetType, cb){
  async.parallel([
    (interCallback) => {
      let deleteCondition = {destinationId: targetObj._id,
                             destinationType: targetType};
      let remover = RelationGraph.remove(deleteCondition);
      dbWp.execute(remover,
        remover.exec,
        src.id,
        interCallback);
    },
    (interCallback) => {
      let deleteCondition = {$or:[{target_id: targetObj._id, target_type: targetType},
                                  {initiator_id: targetObj._id, initiator_type: targetType}]};
      
      dbWp.execute(Relation,
        remover.remove,
        src.id,
        deleteCondition,
        interCallback);
    },
  ], (err, results) => {
    if (err){
      logger.error(src.id, "happen some error when remove data about relation and relationGraph", err);
    }
    return interCallback(err, results);
  });
};

