var relationGraphsBuildTypesSet = new Set([cst.TypeUser, 
                                           //cst.TypeAnonymous
                                           ]);
var rebuildRelationWaitingTimeInSec = 10;

function emitUserCreatedEvent(src, userid, cb){
  async.waterfall([
    (interCallback) => {
      dbWp.execute(User, User.findOne, src.id, {_id: userid}, interCallback);
    },
    (userObj, interCallback) => {
      if (userObj){
        userEvent.emitUserCreated(src, userObj);
        interCallback(new esErr.ESErrors(esErr.TaskqueueRetry));
      }
      else{
        interCallback();
      }
    }
  ], (err, result) => {
      cb(err, result)
  });
}

