var buildTypesEvtDict = {};
buildTypesEvtDict[cst.TypeUser] = emitUserCreatedEvent;
//buildTypesEvtDict[cst.TypeAnonymous] = emitAnonymousCreatedEvent;

function rebuildTargetRelationGraphs(src, statusObj, cb){
  logger.info(src.id, "Begin runing rebuildInitiatorRelationGraphs " + JSON.stringify(statusObj));
  async.waterfall([
    (interCallback) => {
      let memkeyval = 'relrebuildstatus_'+ statusObj.destinationId +'_'+ statusObj.destinationType;
      memcache.del(src, memkeyval);
      interCallback(null, statusObj);
    },
    (statusObj, interCallback) => {
      let qryCondition = {target_id: statusObj.destinationId,
                          target_type: statusObj.destinationType};
      let prj = 'relationdef_id initiator_id initiator_type relation_type';
      dbWp.execute(Relation, Relation.find, src, qryCondition, prj, interCallback);
    },
    (relation_graphs, interCallback) => {
      if (relationGraphsBuildTypesSet.has(statusObj.destinationType)){
        let qryCondition = {destinationId: statusObj.destinationId, 
                            destinationType: statusObj.destinationType};
        let setVal = {'relation_graphs': relation_graphs};        
        dbWp.execute(RelationGraph, RelationGraph.update, src.id, qryCondition, setVal, interCallback);
      }
      else{
        interCallback();
      }
    },
    (updateResult, interCallback) => {
      if (updateResult && !updateResult.n){
        buildTypesEvtDict[statusObj.destinationType](src, statusObj.destinationId, interCallback);
      }
      else{
        interCallback();
      }
    }
  ], (err, result) => {
    if (err){
      logger.warn(src.id, "rebuildTargetRelationGraphs happen error", err);
    }
    logger.info(src.id, "End running rebuildInitiatorRelationGraphs");
    if (cb){
      return cb(err, result);
    }
  });
}

taskqueue.registerDeferHandle('rebuildTargetRelationGraphs', rebuildTargetRelationGraphs);

function rebuildRelationGraphs(src, relation, cb){
  if (relationGraphsBuildTypesSet.has(relation.target_type)){
    let statusObj = {
        destinationId: relation.target_id,
        destinationType: relation.target_type
    };
    //dbWp.execute(RelRebuildStatus, RelRebuildStatus.create, src.id, statusObj, (err, addedObj) => {
    let keyVal = 'relrebuildstatus_'+ relation.target_type+'_'+relation.target_id;
    //let dataVal = json.stringify(statusObj);
    memcache.add(src, keyVal, '',  rebuildRelationWaitingTimeInSec, (err, result) =>{
      if ((!err) && result){
        logger.info(src.id,
                    "Trigger a task to rebuild Relation Graph after " + 
                    rebuildRelationWaitingTimeInSec.toString() + 
                    ' seconds', keyVal);
        taskqueue.launchDefer(src,
                              'rebuildTargetRelationGraphs',
                              statusObj,
                              {defferOption: true,
                               delay: rebuildRelationWaitingTimeInSec,
                               backoff_seconds: 300,
                               attempts: 3,
                               callback:cb
                              });
        
      }
      else{
        return cb();
      }
    });
  }  
}

var SpecialDomainPermConfig = {};
SpecialDomainPermConfig[cst.TypeMessage] = {collectionDB: require('../message/message.model')};

var getinitiatorInfo = function (src, relType, object, cb){
  if (relType == cst.relationCreator){
    if (object.creator && object.creator._id && object.creator.aType){
      cb (null, {initiator_id: object.creator._id, initiator_type: object.creator.aType});
    }
    else{
      cb (new esErr.ESErrors(esErr.NotCreatorError));
    }
  }
  else{
    if (object.parent && object.parent._id && object.parent.aType){
      cb (null, {initiator_id: object.parent._id, initiator_type: object.parent.aType});
    }
    else{
      cb (new esErr.ESErrors(esErr.NotParentError));
    }
  }
};

var specialDomainHanle = function(src, relType, object, objectType, cb){
  let initiator_id = object;
  if (typeof(object) === 'object' && object._id){
    getinitiatorInfo(src, relType, object, cb);
  }
  else{
    var collectionDB = SpecialDomainPermConfig[objectType].collectionDB;
    if (src._cachedActivityDomains && (object + '_'+objectType) in src._cachedActivityDomains){
      let tempCheckObj = src._cachedActivityDomains[object + '_'+objectType];
      return getinitiatorInfo(src, relType, tempCheckObj, cb);
    }
    dbWp.execute(collectionDB, collectionDB.findOne, src.id, {_id: object}, (err, resultObj)=>{
      if (!err && resultObj){
        if (!src._cachedActivityDomains){
          src._cachedActivityDomains = {};
        }
        src._cachedActivityDomains[object + '_'+objectType] = resultObj;
        getinitiatorInfo(src, relType, resultObj, cb);
      }
      else{
        cb(new esErr.ESErrors(esErr.NotExsistedError));
      }
    });
  }
};

var MaxRecursionDepth = 4;
exports.hasPerm = function(src, userobj, relType, object, objectType, cb, recursionDepth=0){
  let initiator_id = object;
  let initiator_type = objectType;
  if (typeof(object) === 'object' && object._id){
    initiator_id = object._id.toString();
  }
  if (typeof(objectType) != 'string'){
    return cb(new esErr.ESErrors(esErr.NoValidateObjectType));
  }
  
  if (initiator_id == userobj._id && initiator_type == userobj.aType){
    return cb(null, true);
  }
  
  if (objectType in SpecialDomainPermConfig){
    specialDomainHanle(src, relType, object, objectType, (err, result)=>{
      if (err || (!result)){
        return cb(null, false);
      }
      else{
        recursionDepth += 1;
        if (recursionDepth < MaxRecursionDepth){
          return exports.hasPerm(src, userobj, relType, result.initiator_id, result.initiator_type, cb, recursionDepth);
        }
        else{
          return cb(null, false);
        }
      }
    });
  }
  else{
    if (relType == cst.relationCreator){
      if (initiator_type == userobj.aType && initiator_id.toString() == userobj._id.toString()){
        return cb(null, true); 
      }
    }
    
    let query = {
        initiator_id: initiator_id,
        initiator_type: initiator_type,
        target_id: userobj._id,
        target_type: userobj.aType,
        relation_type: relType
    };
    dbWp.execute(Relation, Relation.findOne, src.id, query, (err, result)=>{
      if (err){
        return cb(err, false);
      }
      if (!result){
        return cb(null, false);
      }
      else{
        return cb(null, true);
      }
    });
    
  }
};


