import { Router } from 'express';
import controller from './task.controller';
import {asView} from '../viewBase/viewBase';
const router = new Router();

router.get('/:taskId/messages', asView(controller.listMessagesView));
router.delete('/:taskId', asView(controller.deleteTask));

export default router;


