import vw from '../viewBase/viewBase';
import authorize from '../../auth/authorizers';
import migrateBK from './migrate.backend';
import esErr from '../../errors/errors';
import cst from '../../utils/serverConstants';
import querystring from 'querystring';
import util from '../../utils/serverHelper';

exports.migrate = class migrateView extends vw.ViewBase{
  static AUTHORIZERS = [new authorize.PermissionAuthorizer(authorize.pm.PERM_MIGRATE)];
  handle(req, res, cb){
    let data = req.body;
    var src = util.getSrcFromRequest(req);
    migrateBK.lauchMigrate(src, data, (err, result)=>{
      if (err){
        return res.status(cst.HttpErrorStatus).json(err);
      }
      return res.status(cst.HttpSuccessStatus).json({url: querystring.escape(result)});
    })
  }
};

exports.migrateQuery = class migrateQueryView extends vw.ViewBase{
  static AUTHORIZERS = [new authorize.PermissionAuthorizer(authorize.pm.PERM_MIGRATE)];
  handle(req, res, cb){
    var src = util.getSrcFromRequest(req);
    let data = req.params['id'];
    migrateBK.migrateQuery(src, data, (err, result)=>{
      if (err){
        return res.status(cst.HttpErrorStatus).json(err);
      }
      return res.status(cst.HttpSuccessStatus).json(result);
    })
  }
}



