import mongoose from 'mongoose';
import dbWp from '../../utils/dbwrapper';
import TopicMessage from '../message/message.model';

exports.listMessagesByIdea = function(src, data, cb){
  let matchCondition = {
    "parentMsg._id": mongoose.Types.ObjectId(data.ideaId)
  };

  let sort = {_id: -1};
  if (data.page <= 1){
    delete data.nextRefObjId;
    delete data.prevRefObjId;
    data.page = 1;
  }
  let includeEqual = data.includeEqual || false;
  delete data.includeEqual;
  if (data.nextRefObjId){
    if (includeEqual){
      matchCondition["_id"] = {$lte: mongoose.Types.ObjectId(data.nextRefObjId)};
    }
    else{
      matchCondition["_id"] = {$lt: mongoose.Types.ObjectId(data.nextRefObjId)};
    } 
    sort = {_id: -1};
  }
  else if(data.prevRefObjId){
    if (includeEqual){
      matchCondition["_id"] = {$gte: mongoose.Types.ObjectId(data.prevRefObjId)};
    }
    else{
      matchCondition["_id"] = {$gt: mongoose.Types.ObjectId(data.prevRefObjId)};
    }
    sort = {_id: 1};
  }  
  
  var exeobj = TopicMessage.find(matchCondition).sort(sort).limit(data.size + 1);
  dbWp.execute(exeobj,
    exeobj.exec,
    src.id,
    function(err, results){
      if (err){
        logger.error(src.id, 'listCommentsByIdea happen error', err.message);
        return cb(null, {results:[]});
      }
      
      if (results.length > data.size){
        if (data.prevRefObjId){
          results = results.reverse();          
          return cb(null, {results:results.slice(1, data.size + 1), havingNextPage: true});
        }
        return cb(null, {results:results.slice(0, data.size), havingNextPage: true});
      }
      else{
        if (data.prevRefObjId){
          results = results.reverse();        
        }        
        return cb(null, {results:results, havingNextPage: false});
      }
    });
};


