import vw from '../viewBase/viewBase';
import messageBk from '../message/message.backend';
import taskBk from './task.backend';
import util from '../../utils/serverHelper';
import cst from '../../utils/serverConstants';
import esErr from '../../errors/errors';
import TopicMessage from '../message/message.model';
import async from 'async';
import dbWp from '../../utils/dbwrapper';
import logger from '../../modules/logger/index';
import MessageConstants from '../../flux/constants/MessageConstants';
import MessageEvent from '../message/message.event';
import authorize from '../../auth/authorizers';

exports.deleteTask = class deleteTask extends vw.ViewBase{
  static AUTHORIZERS = [new authorize.PermissionAuthorizer(authorize.pm.PERM_TOPIC_UPDATE)];
  handle(req, res, cb){
    
    var src = util.getSrcFromRequest(req);
    var task = req.message;
    req.relPermChecker.checkRelation(src, task.topicId, cst.TypeTopic, (err)=>{
      if (err){
        return cb(err);
      }
      task.remove(function (err, result) {
        if (err) {
          logger.error(req.id, 'Error deleting task', err);
          return cb(res, new esErr.ESErrors(esErr.ServerInternalError));
        }
        MessageEvent.emitCardDeleted(src, task);
        return res.status(cst.HttpSuccessStatus).send();
      });
    });
  };
};

class listMessagesView extends vw.anyCallView{
  static AUTHORIZERS = [new authorize.PermissionAuthorizer(authorize.pm.PERM_TOPIC_TASK_READ)];
  _listMessages(req, queryData, cb){
    var self = this;
    taskBk.listMessagesByTask(req, queryData, (err, results) =>{
      if (err){
        logger.error(req.id, 'listCommentsView call _listComments happen error', err.message);
        return cb(err)
      }
      if (queryData.prevRefObjId && results.results.length < queryData.size){
        delete queryData.prevRefObjId;
        queryData.page = 1;
        return self._listMessages(req, queryData, cb);
      }
      else if(results.results.length === 0 && queryData.nextRefObjId){
        queryData.prevRefObjId = queryData.nextRefObjId;
        delete queryData.nextRefObjId;
        queryData.page -= 1;
        queryData.includeEqual = true;
        return self._listMessages(req, queryData, (err, data) => {
          if (data.results){
            data.results.havingNextPage = false;
          }
          return cb(null, data);
        });
      }
      delete queryData.taskId;
      let data = {
          queryData: queryData,
          results: results
      };
      return cb(null, data);
    });  
  }
  
  handle(req, res, cb){
    //Be care don't need embed parent message
    let queryData = {
      taskId: req.params.taskId,
      size: util.getListOfItemCaps(req.query.size, 30),
      page: parseInt(req.query.page) || 1,
      nextRefObjId: req.query.nextRefObjId,
      prevRefObjId: req.query.prevRefObjId,
    };
    
    var self = this;
    async.waterfall([
      (internalCallback) => {
        dbWp.execute(TopicMessage, TopicMessage.findOne, req.id, {_id: req.params.taskId}, (err, taskMsg) => {
          if (err){
            logger.error(req.id, err.message);
          }
          return internalCallback(err, taskMsg);
        });
      },
      (taskMsg, internalCallback) => {
        if (!taskMsg){
          logger.error(req.id, "task with _id=" +  req.params.taskId + " not exited");
          return internalCallback(new esErr.ESErrors(esErr.NotExsistedError));
        }
        if (taskMsg.category !== MessageConstants.ACTIVITY_CATEGORY_TYPES.TASK){
          return internalCallback(new esErr.ESErrors(esErr.MessageUnexpectedCategory));
        }
        req.relPermChecker.checkRelation(req, taskMsg.topicId, cst.TypeTopic, (err)=>{
          return internalCallback(err, taskMsg);
        });
      },
      (taskMsg, internalCallback) => {
        self._listMessages(req, queryData, internalCallback);
      },        
      (data, internalCallback) => {
        messageBk.toDownloadableClientFormatMessagesIngoreParentMsg(req, data.results.results, (err, convertedResults) => {
          if (err){
            return internalCallback(err);
          }
          data.results.results = convertedResults;
          return internalCallback(null, data);
        });
      }
    ],(err, data) => {
      if (err){
        logger.error(req.id, "listCommentsView happen error", err)
        if (err.code == esErr.AuthorizeErrorPermission){
          return cb(err);
        }
        return res.json({data:[], total:0, nextPageUrl:'', previousPageUrl:''});
      }
      return res.json(util.createPagination(req, data));
    });
  }
  
}
exports.listMessagesView = listMessagesView;


