var cleanUserForTopicInvite = function (invitee, cb) {
    if (invitee.inviteeType === 'email') {
       return cb(null, {email: invitee.invitee,  emailType: 'external'});
    } else if (invitee.inviteeType === 'userId') {
        dbWp.execute(User, User.findById, null, invitee.invitee, function (err, user){
            if (err || !user) {
                return cb(new esErr.ESErrors(esErr.ServerInternalError));
            } else {
                return cb(null, {email: user.username, name: user.displayname, emailType: 'internal'});
            }
        });
    } else {
        return cb(new esErr.ESErrors(esErr.BadRequestError));
    };
}

exports.inviteUsers = function (src, data, cb) {
	var processUsers = function (index, ul, callback) {
		if (!ul) {
			return callback(new esErr.ESErrors(esErr.BadRequestError));
		}

		var cur_user = ul[index];
		if (!cur_user) {
			return callback(null);
		} else {
       cleanUserForTopicInvite(cur_user, function (err, result) {
          if (err) {
              logger.error(src.id, 'Error cleaning user object for topicInvite with user', err, cur_user);
              return callback(new esErr.ESErrors(esErr.BadRequestError));
          } else {
              logger.info(src.id, 'sending invitation email to  ' + result.email);
              userEvent.emitUserInvited(src, {
                title: data.topic.title,
                user: result,
                sender: data.creator,
                invite: data.topicInvite
              });
              return processUsers(index+1, ul, callback);
          }
      });
    }
		
	};

	processUsers(0, data.invitees, function(err) {
		if (err) {
			logger.error(src.id, err);
			return cb(err);
		}

		return cb(null);
	});
};
