var listTopicsForUserId = function (src, data, cb) {
  src.fn += '[listTopicsForUserId]';
  let ret = {};
  var skip = data.pagination.skip,
      limit = data.pagination.limit + 1,

      query = {
        userId: data.user._id,
        userType: data.user.aType
      };

  if (data.search) {
    var str = escapeStringRegexp(data.search);
//    query.$and.push(
//        { title: {$regex: str, $options: 'i'} }      
//    );
    query.title = {$regex: str, $options: 'i'};
  }

  TopicUser.find(query).skip(skip).limit(limit).sort({title: 1}).exec(function (err, topicUserObjs) {
    if (err) {
      logger.error(src.id, src.fn + 'Error runs query on Topic user table', err);
      return cb(err);
    }
    getTopicListFromTopicUserList(src, topicUserObjs, (err, topicObjs)=>{
      if (topicObjs.length === limit) {
        ret.hasNext = true;
        topicObjs = topicObjs.slice(0, -1);
      } else {
        ret.hasNext = false;
      }
      ret.data = topicObjs;
      return cb(null, ret);
    });  
  });
};

