exports.getUserPermissions = function(src, userObj, cb){
  let query = {
    $or:[]
  };
  for (let permItem of userObj.permissions){
    query.$or.push({"permission_name": permItem});
  }
  if ( query.$or.length > 0){
    let execObj = PermissionGroup.find(query).lean();
    dbWp.execute(execObj, execObj.exec, src, (err, result)=>{
      if (err){
        logger.warn(src, 'getUserPermissions happes error', err);
        return cb(null, []);
      }
      let retArray = [];
      for (let permgp of result){
        //retArray = _.concat(retArray, permgp.permissions);
        for (let permItm of permgp.permissions){
          retArray.push(permItm);
        }
      }
      return cb(null, retArray);
    });
  }
  else{
    return cb(null, []);
  }
};

var afterInsertUpdateUser = function(src, userObj, cb){
  logger.info(src.id, "begin call function afterInsertUpdateUser")
  let tagList = [userObj.username];
  let data = {username: userObj.username,
              displayname: userObj.displayname};
      
  for (let emailItem of userObj.emails){
    tagList.push(emailItem.value);
    let emailParts = emailItem.value.split('@');
    if (emailParts.length > 1){
      tagList.push(cst.domainTagPrefix + emailParts[0]);
    }
  }
  
  tagList = Array.from(new Set(tagList));
  
  let savedData = {
      destinationId: userObj._id,
      destinationType: cst.TypeUser,
      tags: tagList,
      data: data
  };
  
  async.waterfall([
    (interCallback) => {
      let queryCondition = {target_id: userObj._id,
                            target_type: cst.TypeUser,
                            initiator_type: cst.TypeCompany};
      let project = 'relationdef_id initiator_id initiator_type relation_type';
      dbWp.execute(Relation, Relation.find, src.id, queryCondition, project,(err, relations) => {
        if (err){
          logger.warn(src.id, 'Get list of relation of user happen error, keep orignial relation');
          savedData.relation_graphs = userObj.relation_graphs;
          relations = [];
        }       
        let add_relations = _.differenceWith(userObj.relation_graphs,
            relations,
            (lRelVal, rRelVal) => {return lRelVal.relationdef_id == rRelVal.relationdef_id;
        });
        let remove_relations = _.differenceWith(relations,
            userObj.relation_graphs,
            (lRelVal, rRelVal) => {return lRelVal.relationdef_id == rRelVal.relationdef_id;
        });
        interCallback(null, userObj, add_relations, remove_relations);
      });
    },
    (userObj, add_relations, remove_relations, interCallback) => {
      async.parallel([
        (interCallback) => {
          let updateCondition = {destinationId: userObj._id,
              destinationType: cst.TypeUser};
          dbWp.execute(RelationGraph,
                       RelationGraph.update,
                       src.id,
                       updateCondition,
                       savedData,
                       {upsert: true},
                       interCallback);
        },
        (interCallback) => {
          var added_rels = [];
          for (let add_rel_item of add_relations){
            let added_rel = {
                target_id: userObj._id,
                target_type: cst.TypeUser,
                initiator_id: add_rel_item.initiator_id,
                initiator_type: add_rel_item.initiator_type,
                relation_type: add_rel_item.relation_type,
                relationdef_id: add_rel_item.relationdef_id
            };
            added_rels.push(added_rel);
          }          
          relationBk.addRelations(src, added_rels, interCallback);
        },
        (interCallback) => {
          relationBk.delRelations(src, remove_relations, interCallback);
        },
      ], (err, results) => {
        if (err){
          logger.warn(src.id, "happen some error when save data about relation and relationGraph");
        }
        return interCallback(err, results);
      });
    },
  ], (err, results) => {
    if (err){
      logger.warn(src.id, "afterInsertUpdateUser happen some error");
    }
    logger.info(src.id, "End call function afterInsertUpdateUser");
    return cb(err, results);
  });  
};

