userEvent.onUserUpdated((src, newUser, oldUser) => {
  let functionName = '[userEvent.onUserUpdated]';
  if (oldUser){
    //If username, picturefile and displayname is changed, update all cached sender info in topic message
    let updateFields = {};
    if (oldUser.username != newUser.username){
      updateFields['sender.username'] = newUser.username;      
    }
    if (oldUser.pictureurl != newUser.pictureurl){
      updateFields['sender.picture_url'] = newUser.picture_url;
    }
    if (oldUser.displayname != newUser.displayname){
      updateFields['sender.displayname'] = newUser.displayname;
    }
    let data = {};
    data.senderId = newUser._id.toString();
    data.senderType = cst.TypeUser;
    data.senderData = updateFields;
    taskqueue.launchDefer(src, 
      'syncMessageSenderDefer',
      data,
      {defferOption: true,
       backoff_seconds: 300,
       attempts: 3,
       callback:(err, result)=>{
         if(!err){
           logger.info(src.id, ndutil.format('%s Trigger a task to sync topicmessage sender successfully', functionName), data);
         }
         else{
           logger.info(src.id, ndutil.format('%s Trigger a task to sync topicmessage sender failed', functionName), data);
         }         
       }
    });
    
  }
});
