'use strict';

import { Router } from 'express';
import controller from './enroll.controller';
import {asView} from '../viewBase/viewBase';

const router = new Router();

router.get('/check', asView(controller.check));
router.get('/search', asView(controller.search));
router.post('/admins/create', asView(controller.adminCreateEnroll));

router.post('/admins/delete', asView(controller.deleteEnrollAdmins));
router.post('/admins/', asView(controller.addEnrollAdmins));

router.get('/:enrollId', asView(controller.get));
router.post('/:enrollId', asView(controller.updateStatus));

router.post('/', asView(controller.create));

router.delete('/:enrollId', asView(controller.remove));

export default router;


