'use strict';

import vw from '../viewBase/viewBase';
import fileBackend from './file.backend';
import logger from '../../modules/logger';
import esErr from '../../errors/errors';
import cst from '../../utils/serverConstants';
import util from '../../utils/serverHelper';
import uuid from'node-uuid';
import jwt from 'jsonwebtoken';
import config from '../../config';
import messageBk from '../message/message.backend';

exports.getUploadUrl = class getUploadUrl extends vw.anyCallView{
  handle(req, res, cb){
    var src = util.getSrcFromRequest(req);

    if (!req.body || !req.body.files) {
      logger.error(src.id, 'Bad request error ');
      return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.BadRequestError));
    }
    let data = {files: req.body.files};
    for(var i in data){
      data[i].fileKey = uuid.v4(); //enforce key;
    }
    fileBackend.getUploadUrls(src, data, function (err, results) {
      if (err){
        logger.error(src.id, 'getUploadUrl happen error ', err);
        return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.UploadUrlCreateFailed));
      }
      return res.status(cst.HttpSuccessStatus).send({data: results});
    });
  }
};

exports.getProfileImageUploadUrl = class getProfileImageUploadUrl extends vw.anyCallView{
  static USECACHE = false;

  handle(req, res, cb){
    var src = util.getSrcFromRequest(req);

    if (!req.body || !req.body.files) {
      logger.error(src.id, 'Bad request error ');
      return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.BadRequestError));
    }
    let data = {files: req.body.files};
    if (req.anonymousUser){
      data.user = req.anonymousUser;
    } else if (req.user){
      data.user = req.user;
    }

    fileBackend.getProfileImageUploadUrl(src, data, function (err, results) {
      if (err){
        logger.error(src.id, 'getUploadUrl happen error ', err);
        return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.UploadUrlCreateFailed));
      }
      return res.status(cst.HttpSuccessStatus).send({data: results});
    });
  }
};

