var verifyTokenPreviewFileUrl = function(src, data, cb){
  let functionName = '[verifyTokenPreviewFileUrl] ';
  logger.info(src.id, functionName + 'Verify token for preview file Url token=' + data);
  jwt.verify(data, config.secrets.session, (err, payload)=>{
    if (err){
      logger.error(src.id, functionName + 'Verify token for preview file Url token=' + data + ' failed!', err);
      return cb(new esErr.ESErrors(esErr.VerifyJWTFailed));
    }
    else{
      logger.info(src.id, functionName + 'Verify token for preview file Url token=' + data + ' successfully!');
      return cb(null, payload);
    }
  });
}

exports.getviewUrls = class getviewUrls extends vw.ViewBase{
  static AUTHENTICATORS = [];
  static AUTHORIZERS = [];
  handle(req, res, cb){
    let maxlistSize = 50;
    let functionName = '[getviewUrls.handle] ';
    let token = req.params.token;
    let size = util.getListOfItemCaps(req.query.size);
    let offset = parseInt(req.query.offset || 0);
    var src = util.getSrcFromRequest(req);
    logger.info(src.id, functionName + 'Get view urls by token=' + token + ' size=' + size + ' offset=' + offset);
    verifyTokenPreviewFileUrl(src, token, (err, jwtPlaylod)=>{
      if (err){
        logger.warn(src.id, functionName + 'Verify token for preview file Url token=' + token + ' failed!', err);
        return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.GetviewUrlsFailed));
      }
      else{
        jwtPlaylod.offset = offset;
        jwtPlaylod.size = size;
        messageBk.getViewUrls(src, jwtPlaylod, (err, result)=>{
          if (err){
            logger.info(src.id, functionName + 'Get view urls by token=' + token + ' size=' + size + ' offset=' + offset + ' failed!', err);
            return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.GetviewUrlsFailed)); 
          }
          else{
            let nextUrl = '';
            let prevUrl = '';
            if (size > 0){
              if (result.data.length + jwtPlaylod.offset < result.totalPages){
                nextUrl = '/api/files/viewUrls/'+ token + '?size=' + size + '&offset=' + (result.data.length + jwtPlaylod.offset);
              }
              if (jwtPlaylod.offset > 0){
                if (jwtPlaylod.offset - size > 0){
                  prevUrl = '/api/files/viewUrls/'+ token + '?size=' + size + '&offset=' + (jwtPlaylod.offset - size);
                }
                else{
                  prevUrl = '/api/files/viewUrls/'+ token + '?size=' + size;
                }
              }
            }
            result.nextUrl = nextUrl;
            result.prevUrl = prevUrl;
            return res.status(cst.HttpSuccessStatus).json(result);
          }
        });
      }
    });
  }
};



