//Currently we use redis as memcache backend
import esErr from '../../errors/errors'; 
import redis from 'redis';
import config from  '../../config-env/environment/index';
import logger from '../../modules/logger';

var redisMemcahe = null;
function initRedisMemcahe(){
  console.log('initRedisMemcahe begin');
  var tempRedisMemcahe = redis.createClient(config.redis.port,
                                            config.redis.host, 
                                            { tls: config.redis.ssl,
                                              auth_pass:config.redis.auth,
                                              retry_strategy: function (options) {
                                                if (options.error.code === 'ECONNREFUSED') {
                                                    // End reconnecting on a specific error and flush all commands with a individual error
                                                    return new Error('The server refused the connection');
                                                }
                                                if (options.total_retry_time > 1000 * 60 * 60) {
                                                    // End reconnecting after a specific timeout and flush all commands with a individual error
                                                    return new Error('Retry time exhausted');
                                                }
                                                if (options.times_connected > 10) {
                                                    // End reconnecting with built in error
                                                    return undefined;
                                                }
                                                // reconnect after
                                                return Math.max(options.attempt * 100, 3000);
                                            }
  });
  tempRedisMemcahe.on('ready', function(){
    redisMemcahe = tempRedisMemcahe;
    console.log('initRedisMemcahe ready now');
  });
  tempRedisMemcahe.on('error', function(err){
    console.log('Happen connection error', err);
    redisMemcahe = null;
  });
  tempRedisMemcahe.on('reconnecting', function(err){
    console.log('Redis is reconnecting ...');
  });
}
setTimeout(initRedisMemcahe, 0);

exports.add = function(src, key, val, timeout, cb){
  if (redisMemcahe){
    redisMemcahe.set(key, val, 'NX', 'EX', timeout, (err, reply) => {
      if (err){
        logger.warn(src.id, 'Memcahe add happen error' + err.toString());
        return cb(err)
      }
      else{
        if (reply == 'OK'){
          return cb(null, true);
        }
        else{
          return cb(null, false);
        }
      }
    });
  }
  else{
    cb(new esErr.ESErrors(esErr.MemcacheNotReady))
  }
}

exports.set = function(src, key, val, timeout, cb){
  if (redisMemcahe){
    redisMemcahe.set(key, val, 'EX', timeout, (err, reply) => {
      if (err){
        logger.warn(src.id, 'Memcahe add happen error' + err.toString());
        return cb(err)
      }
      else{
        if (reply == 'OK'){
          return cb(null, true);
        }
        else{
          return cb(null, false);
        }
      }
    });
  }
  else{
    cb(new esErr.ESErrors(esErr.MemcacheNotReady))
  }
}

exports.get = function(src, key, cb){
  if (redisMemcahe){    
    redisMemcahe.get(key, (err, reply) => {
      if (err){
        logger.warn(src.id, 'Memcahe get happen error' + err.toString());
        return cb(err);
      }
      else{
        return cb(null, reply);
      }
    });
  }
  else{
    cb(new esErr.ESErrors(esErr.MemcacheNotReady))
  }
}

exports.del = function(src, key, cb){
  if (redisMemcahe){
    redisMemcahe.del(key, (err, reply) => {
      if (err){
        logger.warn(src.id, 'Memcahe del happen error' + err.toString());
        if (cb){
          return cb(err);
        }
      }
      else{
        if (reply == 0){
          if (cb){
            return cb(null, false);
          }
        }
        else{
          if (cb){
            return cb(null, true);
          }
        }
      }
    });
  }
  else{
    cb(new esErr.ESErrors(esErr.MemcacheNotReady))
  }
}



