import fileViewCommon from './fileview.common';
import fs from '../../modules/file';
import gcs from 'gcloud/lib/storage';
import gcloudConfig from '../../modules/file/gcs/config.js';
import async from 'async';
import logger from '../../modules/logger';
import TopicMessage from '../message/message.model';
import dbWp from '../../utils/dbwrapper';
import esErr from '../../errors/errors';
import process from 'process';
import request from 'request';
import util from '../../utils/serverHelper';
import ndutil from 'util';
import cst from '../../utils/serverConstants';
import crypto from 'crypto';
import config from '../../config';
import messageEvt from '../message/message.event';
import crtHelp from './converter_helper';

class convertListFileView extends fileViewCommon.FileViewer{
  cleanWork(cb){
    let inData = {
        convertObj:this,
    };        
    crtHelp.cleanWork(this.src, inData, cb);   
  }
  
  setConvertList(convertlist){
    this.convertlist = convertlist;
  }
  
  convert(cb){
    let functionName = '[convertListFileView.convert] ';
    let num = this.convertlist.length;
    let self = this;
    function processOneConvert(idxVal){
      if (idxVal >= num){
        logger.info(self.src.id, ndutil.format('%s No more convert avaliable!', functionName));
        return cb(new esErr.ESErrors(esErr.FileConvertFailed));
      }
      let convertObj = new self.convertlist[idxVal]();
      convertObj.setProperties(self.getProperties());
      convertObj.convert((err, result)=>{
        if (!err){
          logger.info(self.src.id, ndutil.format('%s Convert index=%d successfully!', functionName, idxVal));
          self.convertedFileObj = convertObj.convertedFileObj;
          return cb(null, result);
        }
        if (err.code == esErr.NoSuchConverter || err.code == esErr.TaskqueueEndBeforeTimout){
          logger.info(self.src.id, ndutil.format('%s Convert index=%d ask for end request!', functionName, idxVal));
          return cb(err, result);
        }
        else{
          idxVal = idxVal + 1;
          logger.warn(self.src.id, ndutil.format('%s Try next converter with index=%d!', functionName, idxVal));
          return processOneConvert(idxVal);
        }
      })
    }
    
    processOneConvert(0);
  }
}

export default {
  convertListFileView: convertListFileView,
};



