import jwtClient from './config';
import logger from '../../logger';
import config from '../../../config';
import request from 'request';
import gaCst from './constants';
import MessageConstants from '../../../flux/constants/MessageConstants';

var options = {
  url: 'https://www.google-analytics.com/collect',
  accept: '*/*',
  form: {
  	v: 1,
  	tid: config.googleAnalyticsId,
  	cid: 555
  },
  headers: {
     "Content-Type": "application/x-www-form-urlencoded"
  }
};


exports.postEvent = function (data) {
	let postOption = options;
	postOption.form.t = "event";
	postOption.form.ec = data.category;
	postOption.form.ea = data.action;
	postOption.form.el = data.label;
	postOption.form.ev = data.value;
	request.post(options, function(err, response) {
      if (err || response.statusCode !== 200) {
        logger.error(err, 'Wrong response!: ');
      // } else {
      //   logger.info("ga event sent");
      }
    });
};

exports.postMessageEvent = function (msg) {
  var data = {
    category: gaCst.c_Message,
    label: msg.sender.type
  };
  if (msg.category === MessageConstants.ACTIVITY_CATEGORY_TYPES.TASK) {
    data.action = gaCst.a_sendTask;
  }
  if (msg.category === MessageConstants.ACTIVITY_CATEGORY_TYPES.CHAT) {
    if (msg.content.data && msg.content.data.length > 0) {
      data.action = gaCst.a_sendMedia;
    } else {
      data.action = gaCst.a_sendText;
    }
  }
  if (msg.category === MessageConstants.ACTIVITY_CATEGORY_TYPES.IDEA) {
    data.action = gaCst.a_sendIdea;
  }
  exports.postEvent(data);
};



