  import Is from './Is';


export default {
  clone: (obj) => {
    return !Is.$object(obj) || obj === null ? null : JSON.parse(JSON.stringify(obj));
  },
  GUID: () => {
    var d = new Date().getTime();
    var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
      var r = (d + Math.random() * 16) % 16 | 0;
      d = Math.floor(d / 16);
      return (c == 'x' ? r : (r & 0x7 | 0x8)).toString(16);
    });
    return uuid;
  },
	findBy: (items, field, value) => {
		return items.filter((item) => {
			return item[field] === value
		})
	},
  getSecondsFromNow: (sec) => {
    return new Date(Date.now() + (sec * 1000));
  },
  stringify: (obj) => {
   return JSON.stringify(obj)
  },
  tryParse: (str) => {
    try{
      return JSON.parse(str);
    }
    catch(e){
      return str;
    }
  },
  urlify: (text, target, className) => {
    var urlRegex = /(https?:\/\/[^\s]+)/g;
    target = target || '_self';
    return text.replace(urlRegex, function(url) {
      return '<a href="' + url + '" target="' + target + '">' + url + '</a>';
    });
    // or alternatively
    // return text.replace(urlRegex, '<a href="$1">$1</a>')
  },
  stringExtractUrls: (text) => {
    var urlRegex = /(https?:\/\/(?:www\.|(?!www))[^\s\.]+\.[^\s]{2,}|www\.[^\s]+\.[^\s]{2,})/g;
    var links = [];
    var findStr = text;
    findStr.replace(urlRegex, function(url) {
      links.push(url);
      return url;
    });


    return links;
    // or alternatively
    // return text.replace(urlRegex, '<a href="$1">$1</a>')
  },
	stripHTMLTags: (html) => {
		var tmp = document.createElement('DIV');
		tmp.innerHTML = html;
		return tmp.textContent || tmp.innerText || "";
	},
	includesHTMLTags: (text) => {
		return /<[a-z][\s\S]*>/i.test(text)
		//return text.includes('<') || text.includes('>');
	}
}



