MessageConstants.isMessageEventType = function (message_type) {
	if (AllEventCategories.length == 0){
		getAllValues(MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS, AllEventCategories);
	}
	if (AllEventCategories.indexOf(message_type) >= 0){
		return true;
	}
	return false;


	if (message_type == MessageConstants.ACTIVITY_CATEGORY_TYPES.VIDEO_EVENT_TYPE
		|| message_type in MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS) {
		return true;
	}
	return false;
};

MessageConstants.isInteractiveEvent = function (messageObj) {
	if ((MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS &&
			(messageObj.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS.RequestToJoinRoom
			|| messageObj.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS.AddedNewParty
			|| messageObj.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS.RemovedParty)
		)
//        || (messageObj.message_type == MeetingConstants.MSG_TYPES.VIDEO_EVENT_TYPE
//         && (messageObj.message_text.category == MeetingConstants.APP_EVENTS.RequestVideoChat)
//        )
	) {
		return true;
	}
	return false;
};

MessageConstants.isPresenceMessageType = function (messageObj) {
	if (messageObj.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS
		|| messageObj.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS.RequestPartiesPresence
		|| messageObj.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS.PartyOnline
		|| messageObj.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS.PartyLeaves
	) {
		return true;
	}
	return false;
};

MessageConstants.isValidCategory = function (messageObj){
  if (AllCategories.length == 0){
    getAllValues(MessageConstants.ACTIVITY_CATEGORY_TYPES, AllCategories);
  }
  if (AllCategories.indexOf(messageObj.category) >= 0){
    return true;
  }
  return false;
}

