MessageConstants.isStorableEventType = function (messageObj) {
	if (messageObj.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.CHAT
		|| messageObj.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.IDEA
		|| messageObj.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.TASK
		|| messageObj.category == MessageConstants.ACTIVITY_CATEGORY_TYPES.LIKE
		|| MessageConstants.isInteractiveEvent(messageObj)) {
		return true;
	}
	return false;
};

MessageConstants.isNotifiableMessageType = function (messageObj) {
	return !MessageConstants.isMessageEventType(messageObj.category);
};

