MessageConstants.getMessageFileTypeByMime = function(mimeType) {
	if(!mimeType){
		return '';
	}
	if(Is.$imageMime(mimeType)){ //return (mime=='image/jpeg' || mime=='image/png');
		return MessageConstants.MSG_FILE_TYPES.IMAGE;
	}
	if(Is.$videoMime(mimeType)){ //return (mime=='image/jpeg' || mime=='image/png');
		return MessageConstants.MSG_FILE_TYPES.VIDEO;
	}
	if(MessageConstants.MIME_TYPES.DOCUMENT.indexOf(mimeType) > -1){ //return (mime=='image/jpeg' || mime=='image/png');
		return MessageConstants.MSG_FILE_TYPES.DOCUMENT;
	}
	if(MessageConstants.MIME_TYPES.SPREADSHEET.indexOf(mimeType) > -1){ //return (mime=='image/jpeg' || mime=='image/png');
		return MessageConstants.MSG_FILE_TYPES.SPREADSHEET;
	}
	if(MessageConstants.MIME_TYPES.PRESENTATION.indexOf(mimeType) > -1){ //return (mime=='image/jpeg' || mime=='image/png');
		return MessageConstants.MSG_FILE_TYPES.PRESENTATION;
	}
	return '';
}


