import fileViewCommon from './fileview.common';
import fs from '../../modules/file';
import gcs from 'gcloud/lib/storage';
import gcloudConfig from '../../modules/file/gcs/config.js';
import async from 'async';
import logger from '../../modules/logger';
import TopicMessage from '../message/message.model';
import dbWp from '../../utils/dbwrapper';
import esErr from '../../errors/errors';
import process from 'process';
import request from 'request';
import util from '../../utils/serverHelper';
import ndutil from 'util';
import cst from '../../utils/serverConstants';
import crypto from 'crypto';
import config from '../../config';
import messageEvt from '../message/message.event';
import crtHelp from './converter_helper';

class PdfToSvgFileView extends fileViewCommon.FileViewer{
  cleanWork(cb){
    let functionName = '[PdfToSvgFileView.cleanWork] ';
    logger.info(this.src.id, functionName + 'Begin do clean work');
    var self = this;
    async.waterfall([
    (interCallback)=>{
      if(self.convertedFileObj){
        let convertStatus = self.convertedFileObj.convertStatus || 0;
        if (convertStatus == cst.ConvertStatusSuccess || convertStatus == cst.ConvertStatusFailed){
          logger.info(self.src.id, functionName + 'Update all files in topic message with fileId=' + self.fileId + ' with status=' + convertStatus);
          dbWp.execute(TopicMessage,
            TopicMessage.update,
            self.src.id,
            {'content.data.fileId': self.fileId},
            {'$set': {'content.data.$.metaData': {pages: self.convertedFileObj.pages},
                     'content.data.$.convertStatus': convertStatus,
                     'content.data.$.thumbnailFile': self.fileId +'_convert/page_1.svg',
                     'modified': Date.now()}},
            {multi: true, new: true},
            (err, updateResults) => {
              if (err){
                logger.error(self.src.id, functionName + 'Update all files with same fileId failed', err);
                return interCallback(new esErr.ESErrors(esErr.ConvertFileCleanWorkFailed));
              }
              else{
                if (convertStatus == cst.ConvertStatusSuccess){
                  crtHelp.notifyClientsMessageUpdate(self.src, self);
                }
              }
          });
        }
      } 
      return interCallback(null);
    },
    (interCallback)=>{
      crtHelp.removeTempfolder(self.src, self, (err)=>{
        if (err){
          logger.warn(self.src.id, functionName + 'Remove temp folder failed', err);
        }
        else{
          logger.info(self.src.id, functionName + 'Remove temp folder failed successfully');
        }
        return cb(err);
      });
    },
    ],(err, result)=>{
      
    });
  }
  
  convert(cb){
    var self = this;
    var functionName = '[PdfToSvgFileView.convert] ';
    var endRequestForRequestEndSoon = false;
    async.waterfall([
        //Query file information from database
        (interCallback) => {
          logger.info(self.src.id, functionName + 'Get file object from message by messageId=' + self.messageId + ' fileId=' + self.fileId + '.');
          dbWp.execute(TopicMessage,
              TopicMessage.findOne,
              self.src.id,
              {'_id': self.messageId,
               'content.data.fileId':self.fileId},
               {"content.data.$": 1},
              (err, msgObj)=>{
                if (!err && msgObj){
                  logger.info(self.src.id, functionName + 'Get file object from message by messageId=' + self.messageId + ' fileId=' + self.fileId + ' successfully.');
                  return interCallback(err, msgObj.content.data[0].toJSON());
                }
                logger.info(self.src.id, functionName + 'Get file object from message by messageId=' + self.messageId + ' fileId=' + self.fileId + ' failed.', err);
                return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
              });
        },
        //copy the file to that folder
        (fileObj, interCallback) => {
          self.convertedFileObj = fileObj;
          if (fileObj.metaData && fileObj.metaData.paging){
            logger.info(self.src.id, functionName + 'The file already converted ' + fileObj.metaData.paging + ' pages');
            return interCallback(null, fileObj);
          }
          logger.info(self.src.id, functionName + 'Copy file to temp convert folder with bucket=' + gcloudConfig.tempExtneralBucket + ' Folder=logan/' + self.fileId + '_convert/');
          fs.copyFile(self.src, 
            {
              srcBucketName: gcloudConfig.bucket,
              srcFileName: self.fileId,
              destBucketName: gcloudConfig.tempExtneralBucket,
              destFileName: self.fileId + '_convert/' + self.fileId,              
            },
            (err, result)=>{
              if (err){
                logger.warn(self.src.id, functionName + 'Copy file failed', err);
              }
              else{
                logger.info(self.src.id, functionName + 'Copy file successfully');
              }
              return interCallback(err, fileObj);
          });
        },
        //get the page number of the pdf
        (fileObj, interCallback) => {
          if (fileObj.pages){
            logger.info(self.src.id, functionName + 'The pdf file already get page number before. pages=' + fileObj.pages);
            return interCallback(null, fileObj); 
          }
          let url = crtHelp.getAsposeApiBaseUrl() + 'pdf/' + encodeURI(self.fileId) + '/pages?' +
                    'folder=' + encodeURI('logan/' + self.fileId + '_convert') +
                    '&storage=' + encodeURI(crtHelp.asposeStorageName);
          try{
            url = crtHelp.asposSign(self.src, url);
          }
          catch(err){
            logger.error(self.src.id, functionName + 'When sign aspose url happen error, no retry any more', err);
            self.setConvertStatus(cst.ConvertStatusFailed, ()=>{
              return interCallback(new esErr.ESErrors(esErr.TaskNoRetryError));
            });
          }
          logger.info(self.src.id, functionName + 'Query pdf pages by url' + url);
          let options = {url: url, json:true};
          request.get(options, (err, response, body)=>{
            if (!err && response.statusCode == 200){
              let pages = 0; 
              if (body && body.Pages && body.Pages.List){
                pages = body.Pages.List.length;
              }
              logger.info(self.src.id, functionName + 'Query pdf pages by url' + url + ' successfully with data:', body);
              crtHelp.setPagesToMetaData(self.src, self.messageId, self.fileId, pages, (err, result)=>{
                if (err){
                  logger.error(self.src.id, functionName + 'Set page to file failed', err);
                  return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
                }
                else{
                  logger.info(self.src.id, functionName + 'Set page to file successfully');
                  fileObj.pages = pages;
                  return interCallback(null, fileObj);
                }
              });
            }
            else{
              logger.warn(self.src.id, functionName + 'Query pdf pages by url' + url + ' faild', err, response.statusCode, body);
              return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
            }            
          });
        },
        //use aspose api split document into doc for every 5 pages
        (fileObj, interCallback) => {
          let currentPage = fileObj.metaData.paging || 0;
          let pages = fileObj.pages;
          let stepPage = 5;
          async.whilst(
            ()=>{
              crtHelp.setPagingToMetaData(self.src, self.messageId, self.fileId, currentPage, (err)=>{
                if (err){
                  logger.warn(self.src.id, functionName + 'Set paging of convert progress failed', err);
                }
              });
              if (util.requestWillEndSoon(self.src)){
                logger.info(self.src.id, 'This request will end soon, create another taskqueue to continue working');
                endRequestForRequestEndSoon = true;
                return false;
              }
              return (currentPage + 1) <= pages;
            },
            (interCallback2)=>{
              let startPage = currentPage + 1;
              if (startPage > pages){
                return interCallback2(null, currentPage);
              }
              let endPage = currentPage + stepPage;
              if (endPage > pages){
                endPage = pages;
              }
              currentPage = endPage;
              let url =  crtHelp.getAsposeApiBaseUrl() + 'pdf/' + encodeURI(self.fileId) + '/split?' + 
                         'format=' + encodeURI('doc') +
                         '&from=' + startPage +
                         '&to=' + endPage +
                         '&folder=' + encodeURI('logan/' + self.fileId + '_convert') +
                         '&storage=' + encodeURI(crtHelp.asposeStorageName);
              try{
                url = crtHelp.asposSign(self.src, url);
              }
              catch(err){
                logger.error(self.src.id, functionName + 'When sign aspose url happen error, no retry any more', err);
                self.setConvertStatus(cst.ConvertStatusFailed, ()=>{
                  return interCallback2(new esErr.ESErrors(esErr.TaskNoRetryError));
                });
              }
              logger.info(self.src.id, functionName + 'Split pdf into docs by url' + url);
              let options = {url: url, json: true};
              request.post(options, (err, response, body)=>{
                if (!err && response.statusCode == 200){
                  logger.info(self.src.id, functionName + 'Split pdf document into docs by url' + url + ' successfully', body);
                  //use aspose api convert doc page to svg
                  var docsToSvgUrls = [];
                  if (body.Result && body.Result.Documents){
                    for (let docIdx in body.Result.Documents){
                      let docItem = body.Result.Documents[docIdx];
                      let href = docItem.Href;
                      let cutPos = href.lastIndexOf('/');
                      let docfileName = href.substring(cutPos+1);
                      let docfileNamePage = 'page_' + (parseInt(startPage) + parseInt(docIdx)).toString() + '.svg';
                      let url =  crtHelp.getAsposeApiBaseUrl() + 'words/'+ encodeURI(docfileName) + '?format=svg' +
                                 '&folder=' + encodeURI('logan/' + self.fileId + '_convert') +
                                 '&storage=' + encodeURI(crtHelp.asposeStorageName) +
                                 '&outPath=' + encodeURI('logan/' + self.fileId + '_convert/' + docfileNamePage);
                      try{
                        url = crtHelp.asposSign(self.src, url);
                      }
                      catch(err){
                        logger.error(self.src.id, functionName + 'When sign aspose url happen error, no retry any more', err);
                        self.setConvertStatus(cst.ConvertStatusFailed, ()=>{
                          return interCallback2(new esErr.ESErrors(esErr.TaskNoRetryError));
                        });
                      }
                      docsToSvgUrls.push(url);
                    }
                  }
                  //convert docs to svgs
                  async.forEachOf(docsToSvgUrls, (url, key, interCallback3)=>{
                    let options = {url: url, json:true};
                    logger.info(self.src.id, functionName + 'Convert doc to svg by url' + url);
                    request.get(options, (err, response, body)=>{
                      if (!err && response.statusCode == 200){
                        logger.info(self.src.id, functionName + 'Convert doc to svg by url' + url + ' successfully!');
                        return interCallback3(null);
                      }
                      else{
                        logger.error(self.src.id, functionName + 'Convert doc to svg by url' + url + ' faild!', err, response.statusCode, body);
                        return interCallback3(new esErr.ESErrors(esErr.FileConvertFailed));
                      }
                    });
                  }, (err)=>{
                    if (err){
                      logger.warn(self.src.id, functionName + 'Convert docs to svgs failed');
                      return interCallback2(err);
                    }
                    else{
                      logger.info(self.src.id, functionName + 'Convert docs to svgs success');
                      return interCallback2(null, endPage);
                    }                    
                  });
                }
                else{
                  logger.warn(self.src.id, functionName + 'Split pdf document into docs by url' + url + ' faild', err, response.statusCode, body);
                  return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
                }
              });
            },
            (err, n)=>{
              if (err){
                logger.warn(self.src.id, functionName + 'Convert to svg page by page happen error', err);
              } 
              return interCallback(err, fileObj);
          });
        },
        //Copy converted folder back to real official folder
        (fileObj, interCallback) => {
          logger.info(self.src.id, functionName + 'List all svg files from converted folder');
          fs.listFiles(self.src,
            {bucketName: gcloudConfig.tempExtneralBucket,
             prefix: self.fileId + '_convert/page_',
          },(err, results)=>{
            logger.info(self.src.id, functionName + 'List all svg files from converted folder with files number=' + results.length);
            if (results.length < fileObj.pages){
              logger.warn(self.src.id, functionName + 'Happen some errors for list pages. pagenumber should be ' + fileObj.pages);
              return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
            }
            async.forEachOf(results, (aSrcFile, idx, interCallback2)=>{
              let cutPosString = 'logan/';
              let cutPos = aSrcFile.metadata.name.indexOf(cutPosString);
              let path = null;
              if (cutPos >= 0){
                path = aSrcFile.metadata.name.substring(cutPos + cutPosString.length);
              }
              else{
                logger.warn(self.src.id, functionName + 'Failed to get path from name=' + aSrcFile.metadata.name);
              }
              if (path){
                logger.info(self.src.id, functionName + 'Will copy file to bucket=' + gcloudConfig.bucket + ' path='+path);
                fs.createFileObj(self.src,
                  {bucketName: gcloudConfig.bucket,
                   path: path
                },(err, destFileObj) => {
                  aSrcFile.copy(destFileObj, (err, result)=>{
                    if (err){
                      logger.warn(self.src.id, functionName + 'Copy file failed', err);
                      return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
                    }
                    result.setMetadata({contentType: 'image/svg+xml'}, (err, apiResponse)=>{
                      if (err){
                        logger.error(self.src.id, functionName + 'Set mimetype image/svg+xml failed', err);
                        return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
                      }
                      else{
                        return interCallback2(null);
                      }
                    });                    
                  });
                });
              }
              else{
                return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
              }
            }, (err)=>{
              if (err){
                logger.warn(self.src.id, functionName + 'Copy converted file back to real folder happen error', err);
              }
              else{
                logger.info(self.src.id, functionName + 'Copy converted file back to real folder successfully');
              }
              return interCallback(err, fileObj);
            });
          });
        },        
      ],(err, result)=>{
        if (endRequestForRequestEndSoon){
          fileViewCommon.endRequestAndLauchAnotherDefer(self.src, self, (err)=>{
            return cb(err);
          });
        }
        else{
          return cb(err, result);
        }
    });    
  }
}

class pptToSvgFileView extends fileViewCommon.FileViewer{
  cleanWork(cb){
    let functionName = '[pptToSvgFileView.cleanWork] ';
    logger.info(this.src.id, functionName + 'Begin do clean work');
    var self = this;
    async.waterfall([
    (interCallback)=>{
      if(self.convertedFileObj){
        let convertStatus = self.convertedFileObj.convertStatus || 0;
        if (convertStatus == cst.ConvertStatusSuccess || convertStatus == cst.ConvertStatusFailed){
          logger.info(self.src.id, functionName + 'Update all files in topic message with fileId=' + self.fileId + ' with status=' + convertStatus);
          dbWp.execute(TopicMessage,
            TopicMessage.update,
            self.src.id,
            {'content.data.fileId': self.fileId},
            {'$set': {'content.data.$.metaData': {pages: self.convertedFileObj.pages},
                     'content.data.$.convertStatus': convertStatus,
                     'content.data.$.thumbnailFile': self.fileId +'_convert/page_1.svg',
                     'modified': Date.now()}},
            {multi: true, new: true},
            (err, updateResults) => {
              if (err){
                logger.error(self.src.id, functionName + 'Update all files with same fileId failed', err);
                return interCallback(new esErr.ESErrors(esErr.ConvertFileCleanWorkFailed));
              }
              else{
                if (convertStatus == cst.ConvertStatusSuccess){
                  crtHelp.notifyClientsMessageUpdate(self.src, self);
                }
              }
          });
        }
      } 
      return interCallback(null);
    },
    (interCallback)=>{
      crtHelp.removeTempfolder(self.src, self, (err)=>{
        if (err){
          logger.warn(self.src.id, functionName + 'Remove temp folder failed', err);
        }
        else{
          logger.info(self.src.id, functionName + 'Remove temp folder failed successfully');
        }
        return cb(err);
      });
    },
    ],(err, result)=>{
      
    });
  }
  convert(cb){
    var self = this;
    var functionName = '[pptToSvgFileView.convert] ';
    var endRequestForRequestEndSoon = false;
    async.waterfall([
      //Query file information from database
      (interCallback)=>{
        logger.info(self.src.id, ndutil.format(functionName + 'Get file object from message by messageId=%s fileId=%s.', self.messageId, self.fileId));
        dbWp.execute(TopicMessage,
            TopicMessage.findOne,
            self.src.id,
            {'_id': self.messageId,
             'content.data.fileId':self.fileId},
             {"content.data.$": 1},
            (err, msgObj)=>{
              if (!err && msgObj){
                logger.info(self.src.id, ndutil.format(functionName + 'Get file object from message by messageId=%s fileId=%s successfully.', self.messageId, self.fileId));
                return interCallback(err, msgObj.content.data[0].toJSON());
              }
              logger.info(self.src.id, ndutil.format(functionName + 'Get file object from message by messageId=%s fileId=%s failed.', self.messageId, self.fileId), err);
              return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
            });
      },
      //copy the file to that folder
      (fileObj, interCallback) => {
        self.convertedFileObj = fileObj;
        if (fileObj.metaData && fileObj.metaData.paging){
          logger.info(self.src.id, functionName + 'The file already converted ' + fileObj.metaData.paging + ' pages');
          return interCallback(null, fileObj);
        }
        logger.info(self.src.id, functionName + 'Copy file to temp convert folder with bucket=' + gcloudConfig.tempExtneralBucket + ' Folder=logan/' + self.fileId + '_convert/');
        fs.copyFile(self.src, 
          {
            srcBucketName: gcloudConfig.bucket,
            srcFileName: self.fileId,
            destBucketName: gcloudConfig.tempExtneralBucket,
            destFileName: self.fileId + '_convert/' + self.fileId,
          },
          (err, result)=>{
            if (err){
              logger.warn(self.src.id, functionName + 'Copy file failed', err);
            }
            else{
              logger.info(self.src.id, functionName + 'Copy file successfully');
            }
            return interCallback(err, fileObj);
        });
      },
      //get the page number of the slides
      (fileObj, interCallback) => {
        if (fileObj.pages){
          logger.info(self.src.id, functionName + 'The slides file already get page number before. pages=' + fileObj.pages);
          return interCallback(null, fileObj); 
        }
        let url = crtHelp.getAsposeApiBaseUrl() + 'slides/' + encodeURI(self.fileId) + '/slides?' +
                  'folder=' + encodeURI('logan/' + self.fileId + '_convert') +
                  '&storage=' + encodeURI(crtHelp.asposeStorageName);
        try{
          url = crtHelp.asposSign(self.src, url);
        }
        catch(err){
          logger.error(self.src.id, functionName + 'When sign aspose url happen error, no retry any more', err);
          self.setConvertStatus(cst.ConvertStatusFailed, ()=>{
            return interCallback(new esErr.ESErrors(esErr.TaskNoRetryError));
          });
        }
        logger.info(self.src.id, functionName + 'Query slides pages by url' + url);
        let options = {url: url, json:true};
        request.get(options, (err, response, body)=>{
          if (!err && response.statusCode == 200){
            let pages = 0; 
            if (body && body.Slides && body.Slides.SlideList){
              pages = body.Slides.SlideList.length;
            }
            logger.info(self.src.id, functionName + 'Query slides pages by url' + url + ' successfully with data:', body);
            crtHelp.setPagesToMetaData(self.src, self.messageId, self.fileId, pages, (err, result)=>{
              if (err){
                logger.error(self.src.id, functionName + 'Set page to file failed', err);
                return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
              }
              else{
                logger.info(self.src.id, functionName + 'Set page to file successfully');
                fileObj.pages = pages;
                return interCallback(null, fileObj);
              }
            });
          }
          else{
            logger.warn(self.src.id, functionName + 'Query slides pages by url' + url + ' faild', err, response.statusCode, body);
            return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
          }            
        });
      },
      //use aspose api split document into doc for every 5 pages
      (fileObj, interCallback) => {
        let currentPage = fileObj.metaData.paging || 0;
        let pages = fileObj.pages;
        let stepPage = 5;
        async.whilst(
          ()=>{
            crtHelp.setPagingToMetaData(self.src, self.messageId, self.fileId, currentPage, (err)=>{
              if (err){
                logger.warn(self.src.id, functionName + 'Set paging of convert progress failed', err);
              }
            });
            if (util.requestWillEndSoon(self.src)){
              logger.info(self.src.id, 'This request will end soon, create another taskqueue to continue working');
              endRequestForRequestEndSoon = true;
              return false;
            }
            return (currentPage + 1) <= pages;
          },
          (interCallback2)=>{
            let startPage = currentPage + 1;
            if (startPage > pages){
              return interCallback2(null, currentPage);
            }
            let endPage = currentPage + stepPage;
            if (endPage > pages){
              endPage = pages;
            }
            currentPage = endPage;
            let url =  crtHelp.getAsposeApiBaseUrl() + 'slides/' + encodeURI(self.fileId) + '/split?' + 
                       'format=' + encodeURI('pdf') +
                       '&from=' + startPage +
                       '&to=' + endPage +
                       '&folder=' + encodeURI('logan/' + self.fileId + '_convert') +
                       '&storage=' + encodeURI(crtHelp.asposeStorageName) +
                       '&destFolder=' + encodeURI('logan/' + self.fileId + '_convert');
            try{
              url = crtHelp.asposSign(self.src, url);
            }
            catch(err){
              logger.error(self.src.id, functionName + 'When sign aspose url happen error, no retry any more', err);
              self.setConvertStatus(cst.ConvertStatusFailed, ()=>{
                return interCallback2(new esErr.ESErrors(esErr.TaskNoRetryError));
              });
            }
            logger.info(self.src.id, functionName + 'Split slides into pdfs by url' + url);
            let options = {url: url, json: true};
            request.post(options, (err, response, body)=>{
              if (!err && response.statusCode == 200){
                logger.info(self.src.id, functionName + 'Split slides document into pdfs by url' + url + ' successfully', body);
                //use aspose api convert pdf page to doc
                var docsToSvgUrls = [];
                if (body.SplitResult && body.SplitResult.Slides){
                  for (let docIdx in body.SplitResult.Slides){
                    let docItem = body.SplitResult.Slides[docIdx];
                    let href = docItem.Href;
                    let cutPos = href.lastIndexOf('/');
                    let docfileName = href.substring(cutPos+1);
                    let pageNumber = parseInt(startPage) + parseInt(docIdx);
                    let url =  crtHelp.getAsposeApiBaseUrl() + 'pdf/'+ encodeURI(docfileName) + '/split?format=doc' +
                               '&folder=' + encodeURI('logan/' + self.fileId + '_convert') +
                               '&storage=' + encodeURI(crtHelp.asposeStorageName);                               
                    try{
                      url = crtHelp.asposSign(self.src, url);
                    }
                    catch(err){
                      logger.error(self.src.id, functionName + 'When sign aspose url happen error, no retry any more', err);
                      self.setConvertStatus(cst.ConvertStatusFailed, ()=>{
                        return interCallback2(new esErr.ESErrors(esErr.TaskNoRetryError));
                      });
                    }
                    docsToSvgUrls.push({url:url, pageNumber:pageNumber});
                  }
                }
                //convert pdfs to docs
                async.forEachOf(docsToSvgUrls, (urlObj, key, interCallback3)=>{
                  let options = {url: urlObj.url, json:true};
                  logger.info(self.src.id, functionName + 'Convert pdf to doc by url ' + urlObj.url);
                  request.post(options, (err, response, body)=>{
                    if (!err && response.statusCode == 200){
                      logger.info(self.src.id, functionName + 'Convert pdf to doc by url ' + urlObj.url + ' successfully!');
                      //Convert doc to svg
                      let href = body.Result.Documents[0].Href;
                      let cutPos = href.lastIndexOf('/');
                      let docfileName = href.substring(cutPos+1);
                      let docfileNamePage = 'page_' + urlObj.pageNumber.toString() + '.svg';
                      let url =  crtHelp.getAsposeApiBaseUrl() + 'words/'+ encodeURI(docfileName) + '?format=svg' +
                                 '&folder=' + encodeURI('logan/' + self.fileId + '_convert') +
                                 '&storage=' + encodeURI(crtHelp.asposeStorageName) +
                                 '&outPath=' + encodeURI('logan/' + self.fileId + '_convert/' + docfileNamePage);
                      try{
                        url = crtHelp.asposSign(self.src, url);
                      }
                      catch(err){
                        logger.error(self.src.id, functionName + 'When sign aspose url happen error, no retry any more', err);
                        self.setConvertStatus(cst.ConvertStatusFailed, ()=>{
                          return interCallback3(new esErr.ESErrors(esErr.TaskNoRetryError));
                        });
                      }
                      let options = {url: url, json:true};
                      logger.info(self.src.id, functionName + 'Convert doc to svg by url' + url);
                      request.get(options, (err, response, body)=>{
                        if (!err && response.statusCode == 200){
                          logger.info(self.src.id, functionName + 'Convert doc to svg by url' + url + ' successfully!');
                          return interCallback3(null);
                        }
                        else{
                          logger.error(self.src.id, functionName + 'Convert doc to svg by url' + url + ' faild!', err, response.statusCode, body);
                          return interCallback3(new esErr.ESErrors(esErr.FileConvertFailed));
                        }
                      });                      
                    }
                    else{
                      logger.error(self.src.id, functionName + 'Convert pdf to doc by url' + urlObj.url + ' faild!', err, response.statusCode, body);
                      return interCallback3(new esErr.ESErrors(esErr.FileConvertFailed));
                    }
                  });
                }, (err)=>{
                  if (err){
                    logger.warn(self.src.id, functionName + 'Convert docs to svgs failed');
                    return interCallback2(err);
                  }
                  else{
                    logger.info(self.src.id, functionName + 'Convert docs to svgs success');
                    return interCallback2(null, endPage);
                  }                    
                });
              }
              else{
                logger.warn(self.src.id, functionName + 'Split slides document into pdfs by url' + url + ' faild', err, response.statusCode, body);
                return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
              }
            });
          },
          (err, n)=>{
            if (err){
              logger.warn(self.src.id, functionName + 'Convert to svg page by page happen error', err);
            } 
            return interCallback(err, fileObj);
        });
      },
      //Copy converted folder back to real official folder
      (fileObj, interCallback) => {
        logger.info(self.src.id, functionName + 'List all svg files from converted folder');
        fs.listFiles(self.src,
          {bucketName: gcloudConfig.tempExtneralBucket,
           prefix: self.fileId + '_convert/page_',
        },(err, results)=>{
          logger.info(self.src.id, functionName + 'List all svg files from converted folder with files number=' + results.length);
          if (results.length < fileObj.pages){
            logger.warn(self.src.id, functionName + 'Happen some errors for list pages. pagenumber should be ' + fileObj.pages);
            return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
          }
          async.forEachOf(results, (aSrcFile, idx, interCallback2)=>{
            let cutPosString = 'logan/';
            let cutPos = aSrcFile.metadata.name.indexOf(cutPosString);
            let path = null;
            if (cutPos >= 0){
              path = aSrcFile.metadata.name.substring(cutPos + cutPosString.length);
            }
            else{
              logger.warn(self.src.id, functionName + 'Failed to get path from name=' + aSrcFile.metadata.name);
            }
            if (path){
              logger.info(self.src.id, functionName + 'Will copy file to bucket=' + gcloudConfig.bucket + ' path='+path);
              fs.createFileObj(self.src,
                {bucketName: gcloudConfig.bucket,
                 path: path
              },(err, destFileObj) => {
                aSrcFile.copy(destFileObj, (err, result)=>{
                  if (err){
                    logger.warn(self.src.id, functionName + 'Copy file failed', err);
                    return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
                  }
                  result.setMetadata({contentType: 'image/svg+xml'}, (err, apiResponse)=>{
                    if (err){
                      logger.error(self.src.id, functionName + 'Set mimetype image/svg+xml failed', err);
                      return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
                    }
                    else{
                      return interCallback2(null);
                    }
                  });
                });
              });
            }
            else{
              return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
            }
          }, (err)=>{
            if (err){
              logger.warn(self.src.id, functionName + 'Copy converted file back to real folder happen error', err);
            }
            else{
              logger.info(self.src.id, functionName + 'Copy converted file back to real folder successfully');
            }
            return interCallback(err, fileObj);
          });
        });
      },
    ],(err, result)=>{
      if (endRequestForRequestEndSoon){
        fileViewCommon.endRequestAndLauchAnotherDefer(self.src, self, (err)=>{
          return cb(err);
        });
      }
      else{
        return cb(err, result);
      }
  })};
}

class docToSvgFileView extends fileViewCommon.FileViewer{
  cleanWork(cb){
    let functionName = '[docToSvgFileView.cleanWork] ';
    logger.info(this.src.id, functionName + 'Begin do clean work');
    var self = this;
    async.waterfall([
    (interCallback)=>{
      if(self.convertedFileObj){
        let convertStatus = self.convertedFileObj.convertStatus || 0;
        if (convertStatus == cst.ConvertStatusSuccess || convertStatus == cst.ConvertStatusFailed){
          logger.info(self.src.id, functionName + 'Update all files in topic message with fileId=' + self.fileId + ' with status=' + convertStatus);
          dbWp.execute(TopicMessage,
            TopicMessage.update,
            self.src.id,
            {'content.data.fileId': self.fileId},
            {'$set': {
                     'content.data.$.convertStatus': convertStatus,
                     'content.data.$.thumbnailFile': self.fileId +'_convert/page_1.svg',
                     'modified': Date.now()}},
            {multi: true, new: true},
            (err, updateResults) => {
              if (err){
                logger.error(self.src.id, functionName + 'Update all files with same fileId failed', err);
                return interCallback(new esErr.ESErrors(esErr.ConvertFileCleanWorkFailed));
              }
              else{
                if (convertStatus == cst.ConvertStatusSuccess){
                  crtHelp.notifyClientsMessageUpdate(self.src, self);
                }
              }
          });
        }
      } 
      return interCallback(null);
    },
    (interCallback)=>{
      crtHelp.removeTempfolder(self.src, self, (err)=>{
        if (err){
          logger.warn(self.src.id, functionName + 'Remove temp folder failed', err);
        }
        else{
          logger.info(self.src.id, functionName + 'Remove temp folder failed successfully');
        }
        return cb(err);
      });
    },
    ],(err, result)=>{
      
    });
  }
  convert(cb){
    var self = this;
    var functionName = '[docToSvgFileView.convert] ';
    var endRequestForRequestEndSoon = false;
    var oridocname = 'OriginalDoc';
    async.waterfall([
      (interCallback)=>{
        //Query file information from database
        logger.info(self.src.id, ndutil.format('%sGet file object from message by messageId=%s fileId=%s.', functionName, self.messageId, self.fileId));
        dbWp.execute(TopicMessage,
            TopicMessage.findOne,
            self.src.id,
            {'_id': self.messageId,
             'content.data.fileId':self.fileId},
             {"content.data.$": 1},
            (err, msgObj)=>{
              if (!err && msgObj){
                logger.info(self.src.id, ndutil.format('%sGet file object from message by messageId=%s fileId=%s successfully.', functionName, self.messageId, self.fileId));
                return interCallback(err, msgObj.content.data[0].toJSON());
              }
              logger.info(self.src.id, ndutil.format('%sGet file object from message by messageId=%s fileId=%s failed.', functionName, self.messageId, self.fileId), err);
              return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
            });
      },
      //copy the file to that folder
      (fileObj, interCallback) => {
        self.convertedFileObj = fileObj;
        if (fileObj.metaData && fileObj.metaData.paging){
          logger.info(self.src.id, functionName + 'The file already converted ' + fileObj.metaData.paging + ' pages');
          return interCallback(null, fileObj);
        }
        logger.info(self.src.id, functionName + 'Copy file to temp convert folder with bucket=' + gcloudConfig.tempExtneralBucket + ' Folder=logan/' + self.fileId + '_convert/');
        fs.copyFile(self.src, 
          {
            srcBucketName: gcloudConfig.bucket,
            srcFileName: self.fileId,
            destBucketName: gcloudConfig.tempExtneralBucket,
            destFileName: self.fileId + '_convert/' + oridocname,
          },
          (err, result)=>{
            if (err){
              logger.warn(self.src.id, functionName + 'Copy file failed', err);
            }
            else{
              logger.info(self.src.id, functionName + 'Copy file successfully');
            }
            return interCallback(err, fileObj);
        });
      },
      //get the page number of the doc
      (fileObj, interCallback) => {
        if (fileObj.pages){
          logger.info(self.src.id, functionName + 'The doc file already get page number before. pages=' + fileObj.pages);
          return interCallback(null, fileObj); 
        }
        let url = crtHelp.getAsposeApiBaseUrl() + 'words/' + encodeURI(oridocname) + '/statistics?' +
                  'folder=' + encodeURI('logan/' + self.fileId + '_convert') +
                  '&storage=' + encodeURI(crtHelp.asposeStorageName);
        try{
          url = crtHelp.asposSign(self.src, url);
        }
        catch(err){
          logger.error(self.src.id, functionName + 'When sign aspose url happen error, no retry any more', err);
          self.setConvertStatus(cst.ConvertStatusFailed, ()=>{
            return interCallback(new esErr.ESErrors(esErr.TaskNoRetryError));
          });
        }
        logger.info(self.src.id, functionName + 'Query words pages by url' + url);
        let options = {url: url, json:true};
        request.get(options, (err, response, body)=>{
          if (!err && response.statusCode == 200){
            let pages = 0; 
            if (body && body.StatData && body.StatData.PageCount){
              pages = body.StatData.PageCount;
            }
            logger.info(self.src.id, functionName + 'Query word pages by url' + url + ' successfully with data:', body);
            crtHelp.setPagesToMetaData(self.src, self.messageId, self.fileId, pages, (err, result)=>{
              if (err){
                logger.error(self.src.id, functionName + 'Set page to file failed', err);
                return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
              }
              else{
                logger.info(self.src.id, functionName + 'Set page to file successfully');
                fileObj.pages = pages;
                return interCallback(null, fileObj);
              }
            });
          }
          else{
            logger.warn(self.src.id, functionName + 'Query word pages by url' + url + ' faild', err, response.statusCode, body);
            return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
          }
        });
      },
      //use aspose api split document into doc for every 5 pages
      (fileObj, interCallback) => {
        let currentPage = fileObj.metaData.paging || 0;
        let pages = fileObj.pages;
        let stepPage = 5;
        async.whilst(
          ()=>{
            crtHelp.setPagingToMetaData(self.src, self.messageId, self.fileId, currentPage, (err)=>{
              if (err){
                logger.warn(self.src.id, functionName + 'Set paging of convert progress failed', err);
              }
            });
            if (util.requestWillEndSoon(self.src)){
              logger.info(self.src.id, 'This request will end soon, create another taskqueue to continue working');
              endRequestForRequestEndSoon = true;
              return false;
            }
            return (currentPage + 1) <= pages;
          },
          (interCallback2)=>{
            let startPage = currentPage + 1;
            if (startPage > pages){
              return interCallback2(null, currentPage);
            }
            let endPage = currentPage + stepPage;
            if (endPage > pages){
              endPage = pages;
            }
            currentPage = endPage;
            let url =  crtHelp.getAsposeApiBaseUrl() + 'words/' + encodeURI(oridocname) + '/split?' + 
                       'format=' + encodeURI('svg') +
                       '&from=' + startPage +
                       '&to=' + endPage +
                       '&folder=' + encodeURI('logan/' + self.fileId + '_convert') +
                       '&storage=' + encodeURI(crtHelp.asposeStorageName);
            try{
              url = crtHelp.asposSign(self.src, url);
            }
            catch(err){
              logger.error(self.src.id, functionName + 'When sign aspose url happen error, no retry any more', err);
              self.setConvertStatus(cst.ConvertStatusFailed, ()=>{
                return interCallback2(new esErr.ESErrors(esErr.TaskNoRetryError));
              });
            }
            logger.info(self.src.id, functionName + 'Split words into svgs by url' + url);
            let options = {url: url, json: true};
            request.post(options, (err, response, body)=>{
              if (!err && response.statusCode == 200){
                logger.info(self.src.id, functionName + 'Split doc document into svgs by url' + url + ' successfully', body);
                return interCallback2(null, endPage);
              }
              else{
                logger.warn(self.src.id, functionName + 'Split doc document into svgs by url' + url + ' faild', err, response.statusCode, body);
                return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
              }
            });            
          },
          (err, n)=>{
            if (err){
              logger.warn(self.src.id, functionName + 'Convert to svg page by page happen error', err);
            } 
            return interCallback(err, fileObj);
        });
      },
      //Copy converted folder back to real official folder
      (fileObj, interCallback) => {
        logger.info(self.src.id, functionName + 'List all svg files from converted folder');
        let filePrefix = oridocname + '_page';
        fs.listFiles(self.src,
            {bucketName: gcloudConfig.tempExtneralBucket,
             prefix: self.fileId + '_convert/' + filePrefix,
        },(err, results)=>{
          logger.info(self.src.id, functionName + 'List all svg files from converted folder with files number=' + results.length);
          if (results.length < fileObj.pages){
            logger.warn(self.src.id, functionName + 'Happen some errors for list pages. pagenumber should be ' + fileObj.pages);
            return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
          }
          async.forEachOf(results, (aSrcFile, idx, interCallback2)=>{
            let cutPosString = 'logan/';
            let cutPos = aSrcFile.metadata.name.indexOf(cutPosString);
            let path = null;
            if (cutPos >= 0){
              path = aSrcFile.metadata.name.substring(cutPos + cutPosString.length);
            }
            else{
              logger.warn(self.src.id, functionName + 'Failed to get path from name=' + aSrcFile.metadata.name);
            }
            if (path){
              path = path.replace(filePrefix, 'page_');
              path = path.replace('page_0', 'page_');
              logger.info(self.src.id, functionName + 'Will copy file to bucket=' + gcloudConfig.bucket + ' path='+path);
              fs.createFileObj(self.src,
                  {bucketName: gcloudConfig.bucket,
                   path: path
              },(err, destFileObj) => {
                aSrcFile.copy(destFileObj, (err, result)=>{
                  if (err){
                    logger.warn(self.src.id, functionName + 'Copy file failed', err);
                    return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
                  }
                  result.setMetadata({contentType: 'image/svg+xml'}, (err, apiResponse)=>{
                    if (err){
                      logger.error(self.src.id, functionName + 'Set mimetype image/svg+xml failed', err);
                      return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
                    }
                    else{
                      return interCallback2(null);
                    }
                  });
                });
              });
            }
            else{
              return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
            }
          },(err)=>{
            if (err){
              logger.warn(self.src.id, functionName + 'Copy converted file back to real folder happen error', err);
            }
            else{
              logger.info(self.src.id, functionName + 'Copy converted file back to real folder successfully');
            }
            return interCallback(err, fileObj);
          });
        });
      }
    ],(err, result)=>{
      if (endRequestForRequestEndSoon){
        fileViewCommon.endRequestAndLauchAnotherDefer(self.src, self, (err)=>{
          return cb(err);
        });
      }
      else{
        return cb(err, result);
      }
    });
  }
}
export default {
  PdfToSvgFileView: PdfToSvgFileView,
  pptToSvgFileView: pptToSvgFileView,
  docToSvgFileView: docToSvgFileView,
};





