import esErr from '../../errors/errors';
import memcache from '../../modules/memcache';
import cst from '../../utils/serverConstants';
import logger from '../../modules/logger';
import TopicMessage from '../message/message.model';
import dbWp from '../../utils/dbwrapper';
import messageEvt from '../message/message.event';

var ConvertLockTime = 10;

class FileViewer{
  constructor(src, messageId, fileId, handleKey, informat, outformat, extraData){
    this.fileId = fileId;
    this.messageId = messageId;
    this.src = src;
    this.handleKey = handleKey;
    this.convertedFileObj = null;
    this.informat = informat;
    this.outformat = outformat;
    this.extraData = extraData || {};
  }
  
  getProperties(){
    return {
      fileId: this.fileId,
      messageId: this.messageId,
      src: this.src,
      handleKey: this.handleKey,
      informat: this.informat,
      outformat: this.outformat,
      extraData: this.extraData,
    };
  }
  
  setProperties(inData){
    this.fileId = inData.fileId;
    this.messageId = inData.messageId;
    this.src = inData.src;
    this.handleKey = inData.handleKey;
    this.informat = inData.informat;
    this.outformat = inData.outformat;
    this.extraData = inData.extraData;
  }
  
  setConvertStatus(statusValue, cb){
    let functionName = '[FileViewer.setConvertStatus] ';
    var self = this;
    logger.info(this.src.id, functionName + 'Set convert status messageId=' + this.messageId + ' fileId=' + this.fileId + ' status=' + statusValue);
    dbWp.execute(TopicMessage,
      TopicMessage.update,
      this.src.id,
      {"_id": this.messageId, "content.data.fileId": this.fileId},
      {"$set": {"content.data.$.convertStatus": statusValue}},
      (err, numAffected)=>{
        if (!err && numAffected){
          logger.info(self.src.id, functionName + 'Set convert status successfully');
          if(this.convertedFileObj){
            this.convertedFileObj.convertStatus = statusValue;
          }
          messageEvt.emitUpdateModifyTime(self.src, this.messageId);
          return cb(null, statusValue);
        }
        else{
          logger.warn(self.src.id, functionName + 'Set convert status successfully failed', err, numAffected);
          return cb(new esErr.ESErrors(esErr.SetConvertStatusFailed));
        }
    });
    
  }
  
  convert(cb){
    return cb(new esErr.ESErrors(esErr.NoSuchConverter));
  }
  
  validateConvert(cb){
    let functionName = '[validateConvert] ';
    let key = cst.ConvertMemcacheKeyPrefix + this.fileId;
    var self = this;
    logger.info(this.src.id, functionName + 'Add key=' + key + ' to memcache to guareentee only one convert request can process');
    memcache.add(this.src, key, '',  ConvertLockTime, (err, result)=>{
      if ((!err) && result){
        dbWp.execute(TopicMessage,
          TopicMessage.findOne,
          {'content.data' : {'fileId':self.fileId, 'convertStatus': {$ne: cst.ConvertStatusNotStart}}},
          (err, result)=>{
            if (!err && result){
              logger.info(self.src.id, functionName + 'The file begin with fileId=' + self.fileId + ' will begin convert soon');
              return cb(null);
            }
            else{
              logger.warn(self.src.id, functionName + 'The file already begin convert fileId=' + self.fileId);
              return cb(new esErr.ESErrors(esErr.AlreadyStartConvert));
            }
          }
        );
      }
      else{
        logger.warn(self.src.id, functionName + 'Ask for convert same file at same time fileId=' + self.fileId);
        return cb(new esErr.ESErrors(esErr.AlreadyStartConvert));
      }
    });
  }
  
  cleanWork(cb){
    let functionName = '[FileViewer.cleanWork] ';
    logger.info(this.src.id, functionName + 'Null clean work');
    return cb(null);
  }
}

exports.FileViewer = FileViewer;
exports.endRequestAndLauchAnotherDefer = function(src, convertObj, cb){
  let functionName = '[endRequestAndLauchAnotherDefer] ';
  let data = {messageId: convertObj.migrateId,
    fileId:convertObj.fileId,
    handleKey: convertObj.handleKey,
    informat: convertObj.informat,
    outformat: converObj.outformat,
    extraData: convertObj.extraData
  };
  logger.info(src.id, functionName + 'Launch another task to continue convert working!');
  return taskqueue.launchDefer(src,
      'convertAFileDefer',
      data,
      {defferOption: true,
       backoff_seconds: 300,
       attempts: 3,
       callback: (err, result)=>{
         return cb(new esErr.ESErrors(esErr.TaskqueueEndBeforeTimout));
       },
  });  
};


