'use strict';

//Version 1.0

import { Router } from 'express';
import controller from './user.controller';
var config = require('../../config');
import auth from '../../auth/auth.service';
import {asView} from '../viewBase/viewBase';
const router = new Router();

router.get('/me', asView(controller.MeInfoView));
router.get('/me/topics', asView(controller.listTopicsForMe));
router.get('/me/spaces', asView(controller.listTopicsForMe));
//router.get('/:id/topics', asView(controller.listTopicsForUser));
router.get('/:id/colleagues/', asView(controller.SearchColleaguesView));
//router.get('/:id', asView(controller.show));
router.get('/me/tasks', asView(controller.listTasksForMe));
router.get('/me/tasks/assigned', asView(controller.listAssignedTasksForMe));
router.get('/me/ideas', asView(controller.listIdeasForMe));
router.get('/me/attachments/natives', asView(controller.listNativesForMe));

// router.get('/:id/colleagues/topics', asView(controller.listTopicsForColleagues));




export default router;



