        socket.in(channelName).broadcast.emit(SocketConstants.EVENT_NAMESPACE.PRESENCE_EVENT_RESPONSE, payload);

        //msgbk.addMessageWhenNoIdtoDownloadableClientFormatMessage({}, data, function(err, sentMsg){
        //
