/**
 * This is base class of a file viewer
 */
import esErr from '../../errors/errors';
import memcache from '../../modules/memcache';
import cst from '../../utils/serverConstants';
import TopicMessage from '../message/message.model';
import dbWp from '../../utils/dbwrapper';
import logger from '../../modules/logger';
import taskqueue from '../taskqueue/taskqueue.backend';
import asposeFV from './aspose_fileviewer.backend';
import aspose_ccrtFV from './aspose_cloudconvert_fileview.backend';
import ccrtFV from './cloudconvert_fileview.backend';
import crtlistFV from './convertlist_fileview.backend';
import crtHelp from './converter_helper'; 

var convertHandlers={
  'pdf_svg': ccrtFV.PdfToSvgFileView,
  'ppt_svg': ccrtFV.pptToSvgFileView,
  'doc_svg': ccrtFV.docToSvgFileView,
  'excel_svg': ccrtFV.excelToSvgFileView
};

var fileViewMaps=[
  {fileType:'application/pdf', ext:'.pdf', handleKey:'pdf_svg', converted_file_ext: '.svg'},
  {fileType:'application/vnd.openxmlformats-officedocument.presentationml.presentation', ext:'.pptx', handleKey:'ppt_svg', converted_file_ext: '.svg'},
  {fileType:'application/vnd.ms-powerpoint', ext:'.ppt', handleKey:'ppt_svg', converted_file_ext: '.svg'},
  {fileType:'application/msword', ext:'.doc', handleKey:'doc_svg', converted_file_ext: '.svg'},
  {fileType:'application/vnd.openxmlformats-officedocument.wordprocessingml.document', ext:'.docx', handleKey:'doc_svg', converted_file_ext: '.svg'},
  {fileType:'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet', ext:'.xlsx', handleKey:'excel_svg', converted_file_ext: '.svg'},
  {fileType:'application/vnd.ms-excel', ext:'.xls', handleKey:'excel_svg', converted_file_ext: '.svg'},  
];

exports.getConvertedFileExt = function(src, fileObj){
  let functionName = '[getConvertedFileExt] ';
  let fileName = fileObj.name;
  let fileType = fileObj.providerFileType;
  logger.info(src.id, functionName + 'Try to get converted extension name by name=' + fileName + ' fileType=' + fileType);
  for(let item of fileViewMaps){
    if (fileName.toLowerCase().endsWith(item.ext) || item.fileType == fileType){
      logger.info(src.id, functionName + 'Get converted extension name by name=' + fileName + ' fileType=' + fileType + ' and get result=' + item.converted_file_ext);
      return item.converted_file_ext;
    }
  }
  logger.info(src.id, functionName + 'Get converted extension name by name=' + fileName + ' fileType=' + fileType + ' no result');
  return null;
};

exports.convertViewFile = function(src, messageId, fileObj, cb){
  let functionName = '[convertViewFile] ';
  let fileName = fileObj.name;
  let fileType = fileObj.providerFileType;
  let convertHandle = null;
  let handleKey = '';
  logger.info(src.id, functionName + 'Try to convert file with  messageId=' + messageId + ' name=' + fileName + ' fileType=' + fileType);
  let selectedItem = null;
  for(let item of fileViewMaps){    
    if (fileName.toLowerCase().endsWith(item.ext) || item.fileType == fileType){
      convertHandle = convertHandlers[item.handleKey] || null;
      if (convertHandle){
        logger.info(src.id, functionName + 'Has corresponding converter ' + item.handleKey);
        handleKey = item.handleKey;
        selectedItem = item;
        break;
      }
    }
  }
  
  if (convertHandle){
    let converObj = null
    if( Object.prototype.toString.call( convertHandle ) === '[object Array]' ) {
      converObj = new crtlistFV.convertListFileView(src, messageId, fileObj.fileId, handleKey);
      converObj.setConvertList(convertHandle);
    }
    else{
      converObj = new convertHandle(src, messageId, fileObj.fileId, handleKey);
    }
    converObj.validateConvert((err)=>{
      if (!err){
        converObj.setConvertStatus(cst.ConvertStatusProgressing, (err, result)=>{
          if (err){
            logger.warn(src.id, functionName + 'Can convert this file for update status failed', err);
            return cb(null);
          }
          else{
            let data = {messageId: converObj.messageId,
              fileId: converObj.fileId,
              handleKey: converObj.handleKey,
              informat: selectedItem.ext.replace('.', ''),
              outformat: selectedItem.converted_file_ext.replace('.', '')
            };
            converObj.convertedFileObj = {convertStatus: cst.ConvertStatusProgressing};
            crtHelp.notifyClientsMessageUpdate(src, converObj);
            taskqueue.launchDefer(src,
              'convertAFileDefer',
              data,
              {defferOption: true,
               backoff_seconds: 300,
               attempts: 3,
               callback:(err, result)=>{
                 if(!err){
                   logger.info(src.id, functionName + 'Trigger a task to do convert work with messageId=' + messageId + 'fileId=' + fileObj.fileId);
                 }
                 else{
                   logger.info(src.id, functionName + 'Failed to trigger a task to do convert work with messageId=' + messageId + 'fileId=' + fileObj.fileId);
                 }
                 return cb(null);
               }
            });
          }
        });
      }
      else{
        logger.warn(src.id, functionName + 'Can not convert this file.');
        return cb(null);
      }
    });
  }
  else{
    logger.warn(src.id, functionName + 'No corresponding converter.');
    return cb(null);
  }  
};

function convertAFileDefer(src, data, cb){
  let functionName = '[convertAFileDefer] ';
  let messageId = data.messageId;
  let fileId = data.fileId;
  let handleKey = data.handleKey;  
  let informat = data.informat;
  let outformat = data.outformat;
  let extraData = data.extraData || {};
  let taskoptions = data._taskoptions;
  
  logger.info(src.id, functionName + 'Try to convert file', data);
  
  let convertHandle = convertHandlers[handleKey] || null;
  if (!convertHandle){
    logger.warn(src.id, functionName + 'No corresponding converter ' + item.handleKey);
    return cb(new esErr.ESErrors(esErr.TaskNoRetryError));
  }
  let converObj = null
  if( Object.prototype.toString.call( convertHandle ) === '[object Array]' ) {
    converObj = new crtlistFV.convertListFileView(src, messageId, fileId, handleKey, informat, outformat, extraData);
    converObj.setConvertList(convertHandle);
  }
  else{
    converObj = new convertHandle(src, messageId, fileId, handleKey, informat, outformat, extraData);
  }
  converObj.convert((err, result)=>{
    if (err){
      logger.warn(src.id, functionName + 'Happen error', err);
      if (err.code == esErr.NoSuchConverter || err.code == esErr.TaskqueueEndBeforeTimout){
        return cb(new esErr.ESErrors(esErr.TaskNoRetryError));
      }
      else{
        if (taskoptions.attempt_times >= taskoptions.attempts || err.code == esErr.TaskNoRetryError){
          
          logger.warn(src.id, functionName + 'This file has try to convert serval times still failed or get no retry error, system won\'t do any conversion!');
          logger.warn(src.id, functionName + ' attempt_times=' + taskoptions.attempt_times + ' attempts=' + taskoptions.attempts + ' err.code=' + err.code);
          converObj.setConvertStatus(cst.ConvertStatusFailed, (err)=>{
            converObj.cleanWork(()=>{
              return cb(err, result);
            });            
          });
        }
        else{
          return cb(err, result);
        }
      }
    }
    else{
      logger.info(src.id, functionName + 'Finish successfully');
      converObj.setConvertStatus(cst.ConvertStatusSuccess, (err)=>{
        converObj.cleanWork((err)=>{
          return cb(err, result);
        });        
      });   
    }
  });
}
taskqueue.registerDeferHandle('convertAFileDefer', convertAFileDefer);



