var asposSign = function (src, request_url, joinCharVal){
  var functionName = '[asposSign ]';
  var appSID = getAsposeAppSID();
  var appKey = getAsposeAppKey();
  if ((!appSID) || (!appKey)){
    logger.error(src.id, functionName + 'There is no App SID or App Key of Aspose for ' + process.env.NODE_ENV);
    throw new esErr.ESErrors(esErr.AsposeNoSIDOrKey);
  }
  let joinChar = '&';
  if (joinCharVal === ''){
    joinChar = '';
  }
  var unsignedURL = request_url + joinChar +"appsid=" + appSID;
  var signature = crypto.createHmac('sha1', appKey)
  .update(unsignedURL)
  .digest('base64')
  .replace('=', '');
  unsignedURL = unsignedURL + "&signature=" + signature;
  return unsignedURL;
};

