var setPagesToMetaData = function(src, messageId, fileId, pages, cb){
  let functionName = '[setPagesToMetaData] ';
  logger.info(src.id, functionName + 'Set pages messageId=' + messageId, + ' fileId=' + fileId + ' pages=' + pages);
  dbWp.execute(TopicMessage,
    TopicMessage.update,
    src.id,
    {"_id": messageId, "content.data.fileId": fileId},
    {"$set": {"content.data.$.pages": pages}},
    (err, numAffected)=>{
      if (!err && numAffected){
        logger.info(src.id, functionName + 'Set pages successfully');
        messageEvt.emitUpdateModifyTime(src, messageId);
        return cb(null);
      }
      else{
        logger.warn(src.id, functionName + 'Set pages failed', err, numAffected);
        return cb(new esErr.ESErrors(esErr.SetPagesFailed));
      }
  });
};

