var removeTempfolder = function(src, convertObj, cb){
  let functionName = '[removeTempfolder] ';
  logger.info(src.id, functionName + 'Will remove files in bucket=' + gcloudConfig.tempExtneralBucket + 'path=' + convertObj.fileId + '_convert/');
  fs.deleteFiles(src, {
    bucketName: gcloudConfig.tempExtneralBucket,
    prefix: convertObj.fileId + '_convert/',
  },(err, result)=>{
    if (err){
      logger.error(src.id, functionName + 'remove files in bucket=' + gcloudConfig.tempExtneralBucket + 'path=' + convertObj.fileId + '_convert/ failed', err);
      return cb(new esErr.ESErrors(esErr.FileConvertFailed));
    }
    logger.info(src.id, functionName + 'Will remove files in bucket=' + gcloudConfig.tempExtneralBucket + 'path=' + convertObj.fileId + '_convert/ successfully');
    return cb(null);
  });
};

function notifyClientsMessageUpdateSender(src, data){
  let functionName = '[notifyClientsMessageUpdateSender] ';
  var options = {
      method:'POST',
      url: util.getFullUrlByType(src, 'socket') + '/api/fileviewer/notify-callback',
      headers: {
        'Authorization': 'API_KEY ' + config.ESNA_API_KEY
      },
      body: data,
      json:true
  };
  let topicId = data.topicId;
  let msgId = data.payload.msgId;
  
  logger.info(src.id, functionName + 'Send to socket server to notify client update topic=' + topicId + 'messageId=' + msgId, options);
  request(options, (err, response, body) =>{
    if (err){
      logger.error(src.id, functionName + 'Failed to notify client', err);
    }
    else{
      if (response && response.statusCode == cst.HttpSuccessStatus){
        logger.info(src.id, functionName + 'Successful to notify client');
      }
      else{
        logger.info(src.id, functionName + 'Failed to notify client', body);
      }
    }
  });
}

function notifyClientsMessageUpdate(src, convertObj){
  let functionName = '[notifyClientsMessageUpdate] ';
  dbWp.execute(TopicMessage,
    TopicMessage.find,
    src.id,
    {'content.data.fileId': convertObj.fileId},
    {'_id':1, 'topicId':1},
    (err, results)=>{
      if (err){
        logger.error(src.id, functionName + 'Query by fildId=' + convertObj.fileId + 'happen error', err);
        return;
      }
      let thumbnailInfo = thumbnailCreateInfoGetter();
      let convertStatus = convertObj.convertedFileObj.convertStatus || 0;
      for (let msgItem of results){
        if (convertStatus == cst.ConvertStatusSuccess){
          let inData = {key: convertObj.fileId +'_convert/' + thumbnailInfo.fileName, msg: msgItem};        
          fs.getDownloadSignedUrl(src, inData, (err, url) => {
            if (err){
              logger.error(src.id, functionName + 'Get download url of thumbnail failed', err);
            }
            else{
              logger.info(src.id, functionName + 'Get previewUrl=' + url);
              let msgId = msgItem._id.toString();
              let topicId = msgItem.topicId.toString();
              let data={
                topicId: topicId,
                payload: {topicId: topicId, messageId: msgId, content:{data:[{
                  fileId: convertObj.fileId,
                  convertStatus: cst.ConvertStatusSuccess,
                  thumbnailUrl: url,
                }]}}
              };
              notifyClientsMessageUpdateSender(src, data);
            }
          });
        }
        else if(convertStatus == cst.ConvertStatusFailed){
          let msgId = msgItem._id.toString();
          let topicId = msgItem.topicId.toString();
          let data={
            topicId: topicId,
            payload: {topicId: topicId, messageId: msgId, content:{data:[{
              fileId: convertObj.fileId,
              convertStatus: cst.ConvertStatusFailed,              
            }]}}
          };
          notifyClientsMessageUpdateSender(src, data)
        }
        else if (convertStatus == cst.ConvertStatusProgressing){
          let msgId = msgItem._id.toString();
          let topicId = msgItem.topicId.toString();
          let data={
            topicId: topicId,
            payload: {topicId: topicId, messageId: msgId, content:{data:[{
              fileId: convertObj.fileId,
              convertStatus: cst.ConvertStatusProgressing
            }]}}
          };
          if (convertObj.convertedFileObj.pages){
            data.payload.content.data[0].pages = convertObj.convertedFileObj.pages;
          }
          notifyClientsMessageUpdateSender(src, data);
        }
    }});
}

