function fileInfoFromCloudConvert(src, format, path, cb){
  var functionName = '[fileInfoFromCloudConvert] ';
  var cloudconvertobj = new cloudconvert(cloudconvertApiKey);
  logger.info(src.id, ndutil.format('%s Get file information by cloud convert. format=%s, path=%s', functionName, format, path));
  var convertparam = {
    "inputformat": format,
    "mode": "info",
    "input":{
      "googlecloud": {
        "projectid": cloudconvertProjectid,
        "bucket": cloudconvertBucket,
        "credentials": cloudconertCredentials,
      }
    },
    "file": path,
  };
  var process = cloudconvertobj.convert(convertparam, function(err, result){
    if (err){
      logger.error(src.id, ndutil.format('%s Get file information by cloud convert failed', functionName), err, convertparam);
      if (err && err.code === 422){
        err = new esErr.ESErrors(esErr.TaskNoRetryError);
      }
      return cb(err);
    }
    else{
      if (result && result.data && result.data.info && result.data.info.Pages){
        logger.info(src.id, ndutil.format('%s Get page number = %s', functionName, result.data.info.Pages));
        return cb(null, parseInt(result.data.info.Pages));
      }
      else{
        logger.warn(src.id, ndutil.format('%s Can not get page number from file. Exist convert task', functionName));
        return cb(new esErr.ESErrors(esErr.TaskNoRetryError));
      }
    }
  });
  
}

function cloudConvertConvertfile(src, inParams, progresscb, cb){
  var functionName = '[cloudConvertConvertfile] ';
  var cloudconvertobj = new cloudconvert(cloudconvertApiKey);
  var processobj = null;
  var format = inParams.format,
      outformat = inParams.outformat, 
      path = inParams.path, 
      outpath = inParams.outpath, 
      processurl = inParams.processurl,
      converteroptions = inParams.converteroptions;
  if (!processurl){
    logger.info(src.id, ndutil.format('%s Convert file %s from format=%s to path %s with format=%s.', functionName, path, format, outpath, outformat));
    var convertparam = {
      "inputformat": format,
      "outputformat": outformat,
      "input":{
        "googlecloud": {
          "projectid": cloudconvertProjectid,
          "bucket": cloudconvertBucket,
          "credentials": cloudconertCredentials,
        }
      },
      "file": path,
      "timeout": 0,
      "output": {
        "googlecloud":{
          "projectid": cloudconvertProjectid,
          "bucket": cloudconvertBucket,
          "credentials": cloudconertCredentials,
        }
      },
      "path": outpath
    };
    if (converteroptions){
      convertparam.converteroptions = converteroptions;
    }
    processobj = cloudconvertobj.convert(convertparam, (err, result)=>{
      if (err){
        logger.error(src.id, ndutil.format('%s Convert file happens error', functionName), err, convertparam);        
      }
      else{
        logger.info(src.id, ndutil.format('%s Convert file successfully %s', functionName, path));
      }
      if (!cloudconvertobj.convertedCallbacked){
        if (err && err.code === 422){
          err = new esErr.ESErrors(esErr.TaskNoRetryError);
        }
        cloudconvertobj.convertedCallbacked = true;
        return cb(err, result);
      }
    });
  }
  else{
    logger.info(src.id, ndutil.format('%s Convert file %s from format=%s to path %s with format=%s already started query status by url=%s.', functionName, path, format, outpath, outformat, processurl));
    var Process = require('cloudconvert/lib/process');
    processobj = new Process(cloudconvertobj);
    processobj.url = processurl;
    processobj.wait((err, result)=>{
      if (err){
        logger.error(src.id, ndutil.format('%s Convert file happens error', functionName), err);
      }
      else{
        logger.info(src.id, ndutil.format('%s Convert file successfully %s', functionName, processobj.url));
      }
      if (!processobj.convertedCallbacked){   
        if (err && err.code === 422){
          err = new esErr.ESErrors(esErr.TaskNoRetryError);
        }
        processobj.convertedCallbacked = true;
        return cb(err, result);
      }      
    });
  }
  processobj.on('progress', (err, result)=>{
    return progresscb(err, result);
  });
  return processobj;
}

function initilizeConvertingProcess(src, data, cb){
  let functionName = '[initilizeConvertingProcess] ';
  let convertObj = data.convertObj;
  let oriDocument = data.oriDocument || 'oriDocument';
  let endRequestForRequestEndSoon = false;
  async.waterfall([
    //Query file information from database
    (interCallback)=>{
      logger.info(src.id, ndutil.format(functionName + 'Get file object from message by messageId=%s fileId=%s.', convertObj.messageId, convertObj.fileId));
      dbWp.execute(TopicMessage,
          TopicMessage.findOne,
          src.id,
          {'_id': convertObj.messageId,
           'content.data.fileId':convertObj.fileId},
           {"content.data.$": 1},
          (err, msgObj)=>{
            if (!err && msgObj){
              logger.info(src.id, ndutil.format(functionName + 'Get file object from message by messageId=%s fileId=%s successfully.', convertObj.messageId, convertObj.fileId));
              return interCallback(err, msgObj.content.data[0].toJSON());
            }
            logger.info(src.id, ndutil.format(functionName + 'Get file object from message by messageId=%s fileId=%s failed.', convertObj.messageId, convertObj.fileId), err);
            return interCallback(new esErr.ESErrors(esErr.TaskNoRetryError));
          });
    },
    //copy the file to that folder
    (fileObj, interCallback) => {
      if (fileObj.convertStatus == cst.ConvertStatusSuccess || fileObj.convertStatus == cst.ConvertStatusFailed){
         logger.info(src.id, functionName + "The file already converted before with status ", fileObj.convertStatus)
         return interCallback(new esErr.ESErrors(esErr.TaskqueueEndBeforeTimout));
      }
      convertObj.convertedFileObj = fileObj;
      if (fileObj.pages){
        logger.info(src.id, functionName + 'The file already copied');
        return interCallback(null, fileObj);
      }
      logger.info(src.id, functionName + 'Copy file to temp convert folder with bucket=' + gcloudConfig.tempExtneralBucket + ' Folder=logan/' + convertObj.fileId + '_convert/');
      fs.copyFile(src, 
        {
          srcBucketName: gcloudConfig.bucket,
          srcFileName: convertObj.fileId,
          destBucketName: gcloudConfig.tempExtneralBucket,
          destFileName: convertObj.fileId + '_convert/' + oriDocument,
        },
        (err, result)=>{
          if (err){
            logger.warn(src.id, functionName + 'Copy file failed', err);
          }
          else{
            logger.info(src.id, functionName + 'Copy file successfully');
          }
          return interCallback(err, fileObj);
      });
    },
  ],(err, result)=>{
    if (endRequestForRequestEndSoon){
      fileViewCommon.endRequestAndLauchAnotherDefer(src, convertObj, (err)=>{
        return cb(err);
      });
    }
    else{
      return cb(err, result);
    }
  });
}

function cloudConvertPdftoSvg(src, data, cb)
{
  let functionName = '[cloudConvertPdftoSvg] ';
  let convertObj = data.convertObj;
  let fileObj = data.fileObj;
  let oriDocument = data.oriDocument || ('oridocument' + '.pdf');
  let endRequestForRequestEndSoon = false;
  async.waterfall([
    //get the page number of the slides
    (interCallback)=>{
      if (fileObj.pages){
        logger.info(src.id, functionName + 'The slides file already get page number before. pages=' + fileObj.pages);
        return interCallback(null, fileObj); 
      }
      fileInfoFromCloudConvert(src, 
        'pdf',
        'logan/' + convertObj.fileId + '_convert'+'/' + oriDocument,
        (err, pages) => {
          if (err){
            if (err.code==esErr.TaskNoRetryError){
              return interCallback(err);
            }           
            else{
              logger.warn(src.id, ndutil.format('%s Get page from cloudconvert happen error. Try later', functionName));
              return interCallback(esErr.FileConvertFailed);
            }
          }
          else{
            setPagesToMetaData(src, convertObj.messageId, convertObj.fileId, pages, (err, result)=>{
              if (err){
                logger.error(src.id, functionName + 'Set page to file failed', err);
                return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
              }
              else{
                logger.info(src.id, functionName + 'Set page to file successfully');
                fileObj.pages = pages;
                fileObj.convertStatus = cst.ConvertStatusProgressing;
                notifyClientsMessageUpdate(src, convertObj);
                return interCallback(null, fileObj);
              }
            });
          }
      });
    },
    //Use cloud convert api to split pdf file to svg files
    (fileObj, interCallback) => {
      logger.info(src.id, ndutil.format('%s Convert file from pdf to svg',  functionName));
      let options = {
        format: 'pdf',
        outformat: 'svg',
        path: 'logan/' + convertObj.fileId + '_convert'+'/' + oriDocument,
        outpath: 'logan/' + convertObj.fileId + '_convert'+'/',
        processurl: convertObj.extraData.pdfToSvgProcessurl,
        
      };
      if (data.pdftosvgConverteroptions){
        options.converteroptions = data.pdftosvgConverteroptions; 
      }
      let processobj = cloudConvertConvertfile(src,
        options,
        (err, result)=>{
          if (util.requestWillEndSoon(src)){
            logger.info(src.id, 'This request will end soon, create another taskqueue to continue working');
            endRequestForRequestEndSoon = true;
            return interCallback(new esErr.ESErrors(esErr.TaskqueueEndBeforeTimout));
          }
        },
        (err, result)=>{
          delete convertObj.extraData.pdfToSvgProcessurl;
          if(err){
            logger.warn(src.id, functionName + 'Convert pdf to svg happen error', err);
            if (err.code==esErr.TaskNoRetryError){
              return interCallback(err);
            }           
            else{
              return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
            }
          }
          else{
            logger.info(src.id, functionName + 'Convert pdf to svg successfully');
            return interCallback(null, fileObj);
          }
        });
      
      if (processobj && processobj.url && (!convertObj.extraData.pdfToSvgProcessurl)){
        convertObj.extraData.pdfToSvgProcessurl = processobj.url;
      }
    },
  ],(err, result)=>{
    if (endRequestForRequestEndSoon){
      fileViewCommon.endRequestAndLauchAnotherDefer(src, convertObj, (err)=>{
        return cb(err);
      });
    }
    else{
      return cb(err, result);
    }
  });
}

var thumbnailCreateInfoGetter = function(){
  return {
    fileName: 'thumbnail.jpg',
    outformat: 'jpg',
    contentType: 'image/jpeg'
  };
};

function convertPdfToThumbnail(src, data, cb){
  let functionName = '[convertPdfToThumbnail] ';
  let convertObj = data.convertObj;
  let oriDocument = data.oriDocument || ('oridocument' + '.pdf');
  let thumbnaulInfo = thumbnailCreateInfoGetter();
  let fileObj = data.fileObj;
  let dstFile = 'logan/' + convertObj.fileId + '_convert'+ '/' + thumbnaulInfo.fileName;
  let srcFile = 'logan/' + convertObj.fileId + '_convert'+'/' + oriDocument;
  let options = {
    format: 'pdf',
    outformat: thumbnaulInfo.outformat,
    path: srcFile,
    outpath: dstFile,
    converteroptions: {
      "resize": "1000x1000",
      "resizeenlarge": true,
      "page_range": "0-0"
    }
  };
  logger.info(src.id, functionName + 'Will convert pdf page 1 to jpeg file with options', options);
  cloudConvertConvertfile(src,
    options,
    (err, result)=>{},
    (err, result)=>{
      if(err){
        logger.warn(src.id, functionName + 'Convert pdf page 1 to jpeg happen error', err);
        if (err.code==esErr.TaskNoRetryError){
          return interCallback(err);
        }        
        else{
          return cb(new esErr.ESErrors(esErr.FileConvertFailed));
        }
      }
      else{
        logger.info(src.id, functionName + 'Convert pdf page 1 to jpeg successfully');
        return cb(err, fileObj);
      }
  });
}

function copyBackRealFolder(src, data, cb){
  let functionName = '[copyBackRealFolder] '
  let convertObj = data.convertObj;
  let fileObj = data.fileObj;
  let oriDocument = data.oriDocument || 'oridocument';
  
  logger.info(src.id, functionName + 'List all svg files from converted folder');
  let filePrefix = oriDocument + '_';
  if (fileObj.pages == 1 && (convertObj instanceof cloudConvertToSvgFileView)){
    filePrefix = oriDocument + '.' + convertObj.outformat;
  }
  fs.listFiles(src,
      {bucketName: gcloudConfig.tempExtneralBucket,
       prefix: convertObj.fileId + '_convert/' + filePrefix,
  },(err, results)=>{
    logger.info(src.id, functionName + 'List all svg files from converted folder with files number=' + results.length);
    if (results.length < fileObj.pages){
      logger.warn(src.id, functionName + 'Happen some errors for list pages. pagenumber should be ' + fileObj.pages);      
      return cb(new esErr.ESErrors(esErr.FileConvertFailed));
    }
    async.forEachOf(results, (aSrcFile, idx, interCallback)=>{
      let cutPosString = 'logan/';
      let cutPos = aSrcFile.metadata.name.indexOf(cutPosString);
      let path = null;
      if (cutPos >= 0){
        path = aSrcFile.metadata.name.substring(cutPos + cutPosString.length);
      }
      else{
        logger.warn(src.id, functionName + 'Failed to get path from name=' + aSrcFile.metadata.name);
      }
      if (path){
        path = path.replace(filePrefix, 'page_');
        path = path.replace('page_0', 'page_');
        if (path.endsWith('page_')){
          path = path + '1' + '.' + convertObj.outformat;          
        }
        
        logger.info(src.id, functionName + 'Will copy file to bucket=' + gcloudConfig.bucket + ' path='+path);
        fs.createFileObj(src,
            {bucketName: gcloudConfig.bucket,
             path: path
        },(err, destFileObj) => {
          aSrcFile.copy(destFileObj, (err, result)=>{
            if (err){
              logger.warn(src.id, functionName + 'Copy file failed', err);
              return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
            }
            result.setMetadata({contentType: 'image/svg+xml'}, (err, apiResponse)=>{
              if (err){
                logger.error(src.id, functionName + 'Set mimetype image/svg+xml failed', err);
                return interCallback2(new esErr.ESErrors(esErr.FileConvertFailed));
              }
              else{
                return interCallback(null);
              }
            });
          });
        });
      }
      else{
        return interCallback(new esErr.ESErrors(esErr.FileConvertFailed));
      }
    },(err)=>{
      if (err){
        logger.warn(src.id, functionName + 'Copy converted file back to real folder happen error', err);
      }
      else{
        logger.info(src.id, functionName + 'Copy converted file back to real folder successfully');
      }
      return cb(err, fileObj);
    });
  });
}

function copyThumbnailRealFolder(src, data, cb){
  let functionName = '[copyThumbnailRealFolder] ';
  let convertObj = data.convertObj;
  let fileObj = data.fileObj;
  let thumbnailInfo = thumbnailCreateInfoGetter();
  let fileName = data.thumbnailfileName || thumbnailInfo.fileName;
  fs.listFiles(src,
      {bucketName: gcloudConfig.tempExtneralBucket,
       prefix: convertObj.fileId + '_convert/' + fileName,
  },(err, results)=>{
    if (results.length < 1){
      logger.warn(src.id, functionName + 'There is no thumbnail of ', convertObj.fileId + '_convert/' + fileName);      
      return cb(new esErr.ESErrors(esErr.FileConvertFailed));
    }
    let aSrcFile = results[0];
    fs.createFileObj(src,
        {bucketName: gcloudConfig.bucket,
         path: convertObj.fileId + '_convert/' + thumbnailInfo.fileName
    },(err, destFileObj) => {
      aSrcFile.copy(destFileObj, (err, result)=>{
        if (err){
          logger.warn(src.id, functionName + 'Copy file failed', err);
          return cb(new esErr.ESErrors(esErr.FileConvertFailed));
        }
        result.setMetadata({contentType: thumbnailInfo.contentType}, (err, apiResponse)=>{
          if (err){
            logger.error(src.id, functionName + 'Set mimetype ' + thumbnailInfo.contentType + ' failed', err);
            return cb(new esErr.ESErrors(esErr.FileConvertFailed));
          }
          else{
            return cb(null, fileObj);
          }
        });
      });
    });
  });
}

function cleanWork(src, data, cb){
  let functionName = '[cleanWork] ';
  let convertObj = data.convertObj;
  logger.info(src.id, functionName + 'Begin do clean work');
  let thumbnailInfo = thumbnailCreateInfoGetter();
  async.waterfall([
  (interCallback)=>{
    if(convertObj.convertedFileObj){
      let convertStatus = convertObj.convertedFileObj.convertStatus || 0;
      if (convertStatus == cst.ConvertStatusSuccess || convertStatus == cst.ConvertStatusFailed){
        logger.info(src.id, functionName + 'Update all files in topic message with fileId=' + convertObj.fileId + ' with status=' + convertStatus);
        dbWp.execute(TopicMessage,
          TopicMessage.update,
          src.id,
          {'content.data.fileId': convertObj.fileId},
          {'$set': {
                   'content.data.$.convertStatus': convertStatus,
                   'content.data.$.thumbnailFile': (convertStatus==cst.ConvertStatusSuccess) ? convertObj.fileId +'_convert/' + thumbnailInfo.fileName : '',
                   'modified': Date.now()}},
          {multi: true, new: true},
          (err, updateResults) => {
            if (err){
              logger.error(src.id, functionName + 'Update all files with same fileId failed', err);
              return interCallback(new esErr.ESErrors(esErr.ConvertFileCleanWorkFailed));
            }
            else{
              notifyClientsMessageUpdate(src, convertObj);
            }
        });
      }
    }
    else{
      logger.warn(src.id, functionName + 'There is no convertedFileObj in convertObj!!!');
    }
    return interCallback(null);
  },
  (interCallback)=>{
    removeTempfolder(src, convertObj, (err)=>{
      if (err){
        logger.warn(src.id, functionName + 'Remove temp folder failed', err);
      }
      else{
        logger.info(src.id, functionName + 'Remove temp folder failed successfully');
      }
      return cb(err);
    });
  },
  ],(err, result)=>{
    
  });
}

function cloudConvertToPdf(src, data, cb){
  let functionName = '[cloudConvertToPdf] ';
  let convertObj = data.convertObj;
  let fileObj = data.fileObj;
  let oriDocument = data.oriDocument || 'oridocument';
  
  if (fileObj.pages){
    logger.info(src.id, functionName + 'The slides file already get page number before. pages=' + fileObj.pages);
    return cb(null, fileObj); 
  }
  logger.info(src.id, ndutil.format('%s Convert file from format=%s to pdf',  functionName, convertObj.informat));
  let options = {
    format: convertObj.informat,
    outformat: 'pdf',
    path: 'logan/' + convertObj.fileId + '_convert'+'/' + oriDocument + '.' + convertObj.informat,
    outpath: 'logan/' + convertObj.fileId + '_convert'+'/' + oriDocument + '.pdf',
    processurl: convertObj.extraData.excelToPdfProcessurl,
  };
  if (data.toPdfConverteroptions){
    options.converteroptions = data.toPdfConverteroptions; 
  }
  let processobj = cloudConvertConvertfile(src,
     options,
    (err, result)=>{
      if (util.requestWillEndSoon(src)){
        logger.info(src.id, 'This request will end soon, create another taskqueue to continue working');
        fileViewCommon.endRequestAndLauchAnotherDefer(src, convertObj, (err)=>{
          return cb(new esErr.ESErrors(esErr.TaskNoRetryError));
        });
      }
    },
    (err, result)=>{
      delete convertObj.extraData.excelToPdfProcessurl;
      if(err){
        logger.warn(src.id, ndutil.format('%s Convert file from format=%s to pdf happen error',  functionName, convertObj.informat), err);
        if (err.code==esErr.TaskNoRetryError){
          return cb(err);
        }        
        else{
          return cb(new esErr.ESErrors(esErr.FileConvertFailed));
        }
      }
      else{
        logger.info(src.id, ndutil.format('%s Convert file from format=%s to pdf happen error successfully',  functionName, convertObj.informat));
        return cb(null, fileObj);
      }
  });
  if (processobj && processobj.url && (!convertObj.extraData.excelToPdfProcessurl)){
    convertObj.extraData.excelToPdfProcessurl = processobj.url;
  }
} 

class cloudConvertToSvgFileView extends fileViewCommon.FileViewer{
  cleanWork(cb){
    let inData = {
        convertObj:this,
    };        
    cleanWork(this.src, inData, cb);   
  }
  
  toPdf(inData, cb){
    cloudConvertToPdf(this.src, inData, cb);
  }
  
  convert(cb){
    var self = this;
    var functionName = '[docToSvgFileView.convert] ';
    var endRequestForRequestEndSoon = false;
    var oriDocument = 'oridocument';
    async.waterfall([
      //Query file information from database
      (interCallback)=>{
        let inData = {
            convertObj:self,
            oriDocument: oriDocument + '.' + self.informat
        };        
        initilizeConvertingProcess(self.src, inData, interCallback);  
      },
      //Convert to pdf file
      (fileObj, interCallback) =>{
        let inData = {
            convertObj:self,
            fileObj:fileObj,
            oriDocument: oriDocument
          };        
        self.toPdf(inData, interCallback)
      },
      //Convert pdf to svg    
      (fileObj, interCallback) => {
        let inData = {
          convertObj:self,
          fileObj:fileObj,
          oriDocument: oriDocument + '.pdf'
        };        
        cloudConvertPdftoSvg(self.src, inData, interCallback);      
      },
      //Copy converted folder back to real official folder
      (fileObj, interCallback) => {
        let inData = {
            convertObj:self,
            fileObj:fileObj,
            oriDocument: oriDocument
          };        
        copyBackRealFolder(self.src, inData, interCallback);
      },
      //Create thumbnail
      (fileObj, interCallback) => {
        let inData = {
            convertObj:self,
            fileObj: fileObj,
            oriDocument: oriDocument + '.pdf'
        };
        convertPdfToThumbnail(self.src, inData, interCallback);
      },
      //Copy thumbnail file to official folder
      (fileObj, interCallback) => {
        let inData = {
            convertObj:self,
            fileObj:fileObj,
            oriDocument: oriDocument,
            thumbnailfileName: oriDocument + '.jpg'
          };        
        copyThumbnailRealFolder(self.src, inData, interCallback);
      }
    ],(err, result)=>{
      if (endRequestForRequestEndSoon){
        fileViewCommon.endRequestAndLauchAnotherDefer(self.src, self, (err)=>{
          return cb(err);
        });
      }
      else{
        return cb(err, result);
      }
  })};
}

