import { Router } from 'express';
import controller from './migrate.controller';
import {asView} from '../viewBase/viewBase';
import cst from '../../utils/serverConstants';
const router = new Router();


router.post('/', asView(controller.migrate));
router.get('/:id', asView(controller.migrateQuery));
export default router;


