'use strict';

//Version 1.0

import { Router } from 'express';
import controller from './message.controller';
import {asView} from '../viewBase/viewBase';

const router = new Router();

//router.get('/', asView(controller.index));
router.get('/parselink', asView(controller.parseLink));
router.get('/:id', asView(controller.show));
router.get('/:msgId/files/:fileId/viewerUrl', asView(controller.getPreViewIndexUrl));
//router.post('/', asView(controller.create));
router.post('/:id', asView(controller.update));
router.patch('/:id', asView(controller.update));
router.delete('/:id', asView(controller.destroy));


router.get('/:id/files/:fileKey',  asView(controller.getSingleDownloadUrl));

export default router;



