import taskqueue from '../taskqueue/taskqueue.backend';
import cst from '../../utils/serverConstants';
import migrateDB from './migrate.model';
import dbWp from '../../utils/dbwrapper';
import esErr from '../../errors/errors';
import async from 'async';
import logger from '../../modules/logger';
import relationBK from '../relation/relation.backend';
import util from '../../utils/serverHelper';
import EventEmitter from 'events';
import userBK from '../user/user.backend';

var QuitHandleTimeout = 20000;
var RetyWaitTime = 10000;
var batchSize = 20;
function updateMigrateData(src, updatedMigrateData, cb){
  dbWp.execute(migrateDB, 
               migrateDB.update,
               src.id,
               {migrateId: src.migrateId},
               {migrateData: updatedMigrateData},
               (err, result)=>{
    return cb(err);
  });
}

function endRequestAndLauchAnotherDefer(src, updatedMigrateData, cb){
  let mgdata = {migrateId: src.migrateId, funcName: src.funcName};
  updateMigrateData(src, updatedMigrateData, ()=>{
    taskqueue.launchDefer(src,
        'migrateTaskHandle',
         mgdata,
         {defferOption: true,
          backoff_seconds: 300,
          attempts: 3
    });
    cb(new esErr.ESErrors(esErr.TaskqueueEndBeforeTimout));
  });
}

function fillUserRelation(src, migrateData, cb){
  let startUserId = '';
  if (!migrateData.fillRelationData.userfillData){
    migrateData.fillRelationData.userfillData = {};
  }
  if (migrateData.fillRelationData.userfillData.finished){
    return cb(null);
  }
  if (migrateData.fillRelationData.userfillData.startUserId){
    startUserId = migrateData.fillRelationData.userfillData.startUserId;
  }
  let userDB = require('../user/user.model');
  let exeObj = userDB.find();
  if (startUserId){
    exeObj = userDB.find({'_id': {'$gt': startUserId}});
  }
  exeObj = exeObj.sort({'_id': 1});
  let queryStream = exeObj.stream();
  let lastProcessUser = null;
  let closeManully = false;
  let esClosedFlag = false;
  let userCnt = 0;
  queryStream.on('data', function(userObj){
    userCnt += 1;
    if (src.esClosedFlag){
      logger.info(src.id, 'Should end process asap!');
      esClosedFlag = src.esClosedFlag;
      queryStream.destroy();
      return;
    }
    if (util.requestWillEndSoon(src)){
      if (!closeManully){
        logger.info(src.id, 'Request will end soon, launch another defer to continue work!');
        closeManully = true;
        queryStream.destroy();
      }
    }
    if (userCnt >= batchSize){
      queryStream.pause();
    }
    
    userBK.afterInsertUpdateUser(src, userObj, (err, result) => {        
      lastProcessUser = userObj;
      userCnt -= 1;
      if (userCnt <= 0 && (!esClosedFlag)){
        queryStream.resume();
      }
    });
    
  }).on('error', function(err){
    //Retry this user after some time!
    setTimeout(function(){
      if (lastProcessUser){
        migrateData.fillRelationData.userfillData.startUserId = lastProcessUser._id;
      }
      updateMigrateData(src, migrateData, (err)=>{
        return cb(new esErr.ESErrors(esErr.TaskqueueRetry));
      });
    }, RetyWaitTime);
    
  }).on('close', function(){
    if (esClosedFlag){
      return cb(new esErr.ESErrors(esErr.TaskqueueEndBeforeTimout));
    }
    if (closeManully){
      setTimeout(function(){
        if (lastProcessUser){
          migrateData.fillRelationData.userfillData.startUserId = lastProcessUser._id;
        }
        return endRequestAndLauchAnotherDefer(src, migrateData, cb);
      }, RetyWaitTime);
    }
    else{
      migrateData.fillRelationData.userfillData.finished = true;
      updateMigrateData(src, migrateData, (err)=>{
        return cb(null);
      });
    }
  });
}

function fillTopicUserRelation(src, migrateData, cb){
  let startTopicId = '';
  if (!migrateData.fillRelationData.topicfillData){
    migrateData.fillRelationData.topicfillData = {};
  }
  if (migrateData.fillRelationData.topicfillData.startTopicId){
    startTopicId = migrateData.fillRelationData.topicfillData.startTopicId;
  }
  let topicDB = require('../topic/topic.model');
  let exeObj = topicDB.find();
  if (startTopicId){
    exeObj = topicDB.find({'_id': {'$gt': startTopicId}});
  }
  exeObj = exeObj.sort({'_id': 1});
  let queryStream = exeObj.stream();
  let lastProcessTopic = null;
  let closeManully = false;
  let topicCnt = 0;
  let esClosedFlag = false;
  queryStream.on('data', function(topicObj){
    topicCnt += 1;
    if (src.esClosedFlag){
      logger.info(src.id, 'Should end process asap!');
      esClosedFlag = src.esClosedFlag;
      queryStream.destroy();
      return;
    }
    if (util.requestWillEndSoon(src)){
      //Close the stream 
      if (!closeManully){
        closeManully = true;
        queryStream.destroy();
      }
    }
    if (topicCnt >= batchSize){
      queryStream.pause();
    }
    var added_rels = [];
    for (let memberItem of topicObj.members){
      if (memberItem.memberType === 'userId'){
        let added_rel = {
            target_id: memberItem.member,
            target_type: cst.TypeUser,
            initiator_id: topicObj._id,
            initiator_type: cst.TypeTopic,
            relation_type: memberItem.role,
            relationdef_id: (cst.TypeUser + '_' + memberItem.member + '_' + cst.TypeTopic + '_'+ topicObj._id + '_' +  memberItem.role)
        };
        added_rels.push(added_rel);
      }
    }
    relationBK.addRelations(src, added_rels, (err, result) => {
      lastProcessTopic = topicObj;
      topicCnt -= 1;
      if (topicCnt <= 0 && (!esClosedFlag)){
        queryStream.resume();
      }      
    });
  }).on('error', function(err){
    //Retry this user after some time!
    setTimeout(function(){
      if (lastProcessTopic){
        migrateData.fillRelationData.topicfillData.startTopicId = lastProcessUser._id;
      }
      updateMigrateData(src, migrateData, (err)=>{
        return cb(new esErr.ESErrors(esErr.TaskqueueRetry))
      });
    }, RetyWaitTime);
  }).on('close', function(){
    if (esClosedFlag){
      return cb(new esErr.ESErrors(esErr.TaskqueueEndBeforeTimout));
    }
    if (closeManully){
      setTimeout(function(){
        if (lastProcessTopic){
          migrateData.fillRelationData.topicfillData.startTopicId = lastProcessTopic._id;
        }
        return endRequestAndLauchAnotherDefer(src, migrateData, cb);
      }, RetyWaitTime);
    }
    else{
      updateMigrateData(src, migrateData, (err)=>{
        return cb(null);
      });
    }
  });
}

var fillRelation = function(src, migrateData, cb){
  if (!migrateData.fillRelationData){
    migrateData.fillRelationData = {};
  }
  async.waterfall([
  (interCallBack)=>{
    //Fill users relation
    fillUserRelation(src, migrateData, (err, result)=>{
      interCallBack(err);
    });
  },
    (interCallBack) => {
      fillTopicUserRelation(src, migrateData, (err, result) => {
        interCallBack(err);
      })
    }
  ],
  (err, result) => {
    cb(err);
  });
};

var cacheTitleIntoTopicUser = function(src, migrateData, cb){
  var functionName = '[cacheTitleIntoTopicUser] ';
  var TopicUser = require('../notify/topicuser.model');
  var Topic = require('../topic/topic.model');
  var cursorId = '';
  var LIMIT = 20;
  var totalTopicNum = -LIMIT;
  var oneRoundUpdateTopicNum = LIMIT;
  logger.info(src.id, functionName + 'Begin working');
  async.whilst(
    ()=>{
      totalTopicNum = totalTopicNum + oneRoundUpdateTopicNum;
      logger.info(src.id, functionName + 'Finish one round with topic number ' + oneRoundUpdateTopicNum + ' total=' + totalTopicNum);
      if (oneRoundUpdateTopicNum < LIMIT){
        return false;
      }
      return true;
    },
    (interCallback)=>{
      let queryCondition = undefined;
      if (cursorId){
        queryCondition = {_id: {$gt: cursorId}};
      }
      let exeobj = Topic.find(queryCondition).sort({_id: 1}).limit(LIMIT);
      dbWp.execute(exeobj, exeobj.exec, src.id, (err, result)=>{
        if (err){
          logger.error(src.id, functionName + 'Query for topic happen error', err);
          return interCallback(err);
        }
        async.each(result,
          (topicObj, interCallback2)=>{
            dbWp.execute(TopicUser,
              TopicUser.update, 
              src.id,
              {targetId: topicObj._id},
              {$set: {targetType: cst.TypeTopic, title: topicObj.title}},
              {multi: true},
              (err, result)=>{
                interCallback2(err, result);
              })
          }, (err, updateResult)=>{
            if (err){
              logger.error(src.id, functionName + 'Update topic title happen error', err);
            }
            oneRoundUpdateTopicNum = result.length;
            if (oneRoundUpdateTopicNum >= LIMIT){
              cursorId = result[oneRoundUpdateTopicNum-1]._id;
            }
            interCallback(err);
          })
      })
    },
    (err, result)=>{
      if (err){
        logger.warn(src.id, functionName + 'Update title into topicuser table task happen error', err);
      }
      else{
        logger.info(src.id, functionName + 'Update title into topicuser table task done', err);
      }      
      return cb(err, result);
    }
  );
}

var funcNameMap = {
    'fillRelation': fillRelation,
    'cacheTitleIntoTopicUser': cacheTitleIntoTopicUser,
};

class TaskeHandleNotifier extends EventEmitter{
  constructor(){
    super();
  } 
}
var TaskeHandleNotifierObj = new TaskeHandleNotifier()

var migrateTaskHandle = function(src, mgdata, cb){
  logger.info(src.id, "Begin call handle migrateTaskHandle");
  let funcName = mgdata.funcName;
  if (funcName in funcNameMap){
    src.migrateId = mgdata.migrateId;
    src.funcName = funcName;
    dbWp.execute(migrateDB, migrateDB.findOne, src.id, {migrateId: mgdata.migrateId}, (err, result)=>{
      if (result){
        let migrateData = {};
        if (result.migrateData){
          migrateData = result.migrateData;
        }
        funcNameMap[funcName](src, migrateData, (err, result)=>{
          if ((!err) || err.code == esErr.TaskNoRetryError){
            RemoveMigrateTask(src, mgdata.migrateId);
          }
          else{
            if (mgdata.attempt_times >= mgdata.attempts){
              SetFailedStatusMigrateTask(src, mgdata.migrateId);
            }
          }
          
          if (cb){
            return cb(err, result);
          }
          setTimeout(function(){
            TaskeHandleNotifierObj.emit('finish', mgdata);
          }, 2000);
        });
      }
      else{
        logger.info(src.id, "Get error from migrateDB", err);
        logger.info(src.id, 'Query migrate data happen error!, end the migrate process');
        if (cb){
          return cb(new esErr.ESErrors(esErr.TaskNoRetryError));
        }
        setTimeout(function(){
          TaskeHandleNotifierObj.emit('finish', mgdata);
        }, 2000);
      }
    });
  }
  else{
    logger.info(src.id, "There is no funcname in map " + funcName);
    RemoveMigrateTask(src, mgdata.migrateId);
    if (cb){
      return cb(new esErr.ESErrors(esErr.TaskNoRetryError));
    }
    setTimeout(function(){
      TaskeHandleNotifierObj.emit('finish', mgdata);
    }, 2000);
  }
};

taskqueue.registerDeferHandle('migrateTaskHandle', migrateTaskHandle);

function RemoveMigrateTask(src, mgId, cb){
  let exeObj = migrateDB.remove({migrateId: mgId});
  dbWp.execute(exeObj, exeObj.exec, src.id, (err, result)=>{
  });
}

function SetFailedStatusMigrateTask(src, mgId, cb){
  dbWp.execute(migrateDB,
               migrateDB.update,
               src.id,
               {migrateId: src.migrateId},
               {status: cst.MigrateFailedStatus},
               (err, result)=>{});
}

exports.lauchMigrate = function(src, data, cb){
  let mgdata = {migrateId: src.id, funcName: data.funcName};
  let deferCallback = function(err, result){
    if (err){
      if (cb){
        return cb(err);
      }
      return
    }
    let savedData = {
        migrateId: mgdata.migrateId
    };
    dbWp.execute(migrateDB, migrateDB.create, src.id, savedData, (err, result)=>{
      if (err){
        if (cb){
          return cb(new esErr.ESErrors(esErr.RecordMigrageError));
        }
      }
      if(cb){
        return cb(null, src.fullurl + '/api/migrate/' + result._id);
      }
    });

  };
  taskqueue.launchDefer(src,
                        'migrateTaskHandle',
                         mgdata,
                         {defferOption: true,
                          backoff_seconds: 300,
                          attempts: 3,
                          callback:deferCallback
  });
};

exports.migrateQuery = function(src, data, cb){
  dbWp.execute(migrateDB, migrateDB.findOne, src.id, {'_id': data}, (err, result)=>{
    if (err){
      return cb(new esErr.ESErrors(esErr.RecordMigrageError));
    }
    if (result){
      if (result.status == cst.MigrateFailedStatus){
        return cb(null, {status: 'failed'});
      }
      return cb(null, {status: 'in_progressing'}); 
    }
    else{
      return cb(null, {status: 'success'});
    }
  });
}

if (require.main === module){
  var process = require('process');
  var exescriptName = 'cacheTitleIntoTopicUser';
  var config = require('../../config');
  var mongoose = require('mongoose');
  process.env.DeferType = 'timeout';
  
  mongoose.connect(config.mongo.uri, config.mongo.options);
  mongoose.connection.on('connected', function() {
    logger.info('MongoDB connected');
    process.env['mongoUA'] = 'connected';
    setTimeout(function(){
      console.log('Migrate from command line ' + exescriptName);
      exports.lauchMigrate({id: 'migrate_command_request'},
                           {funcName: exescriptName});
    }, 6000);
  });
  mongoose.connection.on('error', function(err) {
      logger.error('MongoDB connection error: ' + err);
      mongoose.disconnect();
  });
  mongoose.connection.on('disconnected', function() {
      logger.warn('MongoDB disconnected! Reconnecting in 3 seconds');
      process.env['mongoUA'] = 'disconnected';
  });
  
  TaskeHandleNotifierObj.on('finish', (mgdata)=>{
    setTimeout(function(){
      console.log('Migrate from command line ' + exescriptName + ' Finished');
      process.exit(1);
    }, QuitHandleTimeout);
  });
}



