import logger from '../../modules/logger/index';
import async from 'async';
import auth from '../../auth/auth.service';
import authorize from '../../auth/authorizers';
import esErr from '../../errors/errors';
import cst from '../../utils/serverConstants';
import util from '../../utils/serverHelper';
import {apiCache, userCache} from '../../auth/lru';
import _ from 'lodash';

var trycatch = null;
/** The trycatch module will cause stack ouput from build folder.
 *  Only product enviroment will apply this module.
 */
if (process.env.NODE_ENV === 'logan-production'){
    trycatch = require('trycatch');
}

var getToken = function(req, cb) {
    var header;
    if (req.headers && req.headers.authorization) {
      header = req.headers.authorization;
    }
    return header;
  }

class ViewBase{
    static AUTHENTICATORS = [auth.JwtAuthenticator, auth.Oauth2Authenticator, auth.AnonymousAuthenticator];
    static AUTHORIZERS = [authorize.OAuthAuthorizer];
    static OAUTHSCOPE = [cst.OAuth2ScopeLogan];
    static USECACHE = true;
  
    constructor(ViewClass){
        let authenticators = ViewClass.AUTHENTICATORS || [];
        this.authenticators = [];
        for (let authenticatorItem of authenticators){
          this.authenticators.push(new authenticatorItem());
        }
        
        this.oauthscope =  ViewClass.OAUTHSCOPE || [];
        this.useCache = ViewClass.USECACHE;
        let authorizers  = ViewClass.AUTHORIZERS || [];
        this.authorizers = [];
        if (authorizers.indexOf(authorize.OAuthAuthorizer) === -1){
          this.authorizers.push(new authorize.OAuthAuthorizer());
        }
        for (let authorizerItem of authorizers){
          if (authorizerItem instanceof authorize.PermissionAuthorizer){
            this.authorizers.push(authorizerItem);
          }
          else{
            this.authorizers.push(new authorizerItem());
          }
        }
    }

    handle(req, res, cb){
        logger.error(req.id, esErr.getMessageByCode(esErr.ViewNotImplement));
        return res.status(500).json(new esErr.ESErrors(esErr.SysUnkownError));
    }

    checkAuthentication(req, res, cb){
        var token,
            cachedApi,
            cachedUser;
        var self = this;
        if (this.authenticators.length === 0){
            return cb(null);
        }

        token = getToken(req);
        cachedApi = apiCache.get(token);
        if (cachedApi && cachedApi.cacheId) {
          cachedUser = userCache.get(cachedApi.cacheId);
        }
        if (self.useCache && token && cachedApi && cachedUser ) {
          _.assign(req, cachedApi, cachedUser);
          logger.info(req.id, 'Got user/anonymous from cache ' + cachedApi.cacheId);
          return cb(null);
        } else {
          async.eachSeries(
            this.authenticators,
            function(authObj, callback){
                logger.info(req.id, 'Check authentication by ' + authObj.name);
                authObj.auth(req, res, function(err){
                    if (!err) {
                        logger.info(req.id, 'Pass authenticator ' + authObj.name);
                        var cacheId = req.user ? 'user-' + req.user._id : 'anonymous-' + req.anonymousUser._id;
                        var cacheReq = {
                          auth: req.auth,
                          cacheId: cacheId
                        };
                        var cacheUser = {
                          user: req.user,
                          anonymousUser: req.anonymousUser
                        };
                        logger.info(req.id, 'Caching User with Id: ', cacheId);
                          apiCache.set(token, cacheReq);
                          userCache.set(cacheId, cacheUser);
                        callback(cst.StopFlag);
                    } else {
                        logger.info(req.id, 'Failed pass authenticator ' + authObj.name + ' try next');
                        self.es_error = err;
                        callback(null);
                    }
                });
            },
            function(err) {         
              if (err == cst.StopFlag){
                  return cb(null);
              } else {
                  return cb(self.es_error);
              }
          });
        }
    }
  
    checkAuthorization(req, res, cb){
      var self = this;
      if (this.authorizers.length === 0){
        return cb(null);
      }
      
      async.eachSeries(
          this.authorizers,
          function(authObj, callback){
              logger.info(req.id, 'Check authorization by ' + authObj.name);
              authObj.check(req, res, self, function(err){
              if (err){
                  logger.info(req.id, 'Failed authorization ' + authObj.name);
                  self.es_error = err;
                  callback(cst.StopFlag);
              }
              else{
                  logger.info(req.id, 'Pass authorization ' + authObj.name + ' check next');                
                  callback(null);
              }
            });
          },
          function(err){
              if (err == cst.StopFlag){
                  return cb(self.es_error);
              }
              else{
                  return cb(null);
              }
          }
        );
    }
    
    _view_no_error_catch(req, res, cb){
        var self = this;
        async.series([
          function(callback){
            self.checkAuthentication(req, res, callback);
          },
          function(callback){
            self.checkAuthorization(req, res, callback);
          },
          function(callback){
            self.handle(req, res, callback);
          }],
          function(err, results){
            if (err.code.indexOf(esErr.AuthenticateFailed) === 0){
              logger.info(req.id, 'Authenticate failed! with authorization ' + req.headers.authorization);
              if (err.code == esErr.anonymouse_jwt_auth_4001_failed){
                return res.status(cst.HttpUnauthorizedAnonymousStatus).json(err);
              }
              return res.status(cst.HttpUnauthorizedStatus).json(err);
            }
            else if (err.code.indexOf(esErr.AuthorizeFailed) === 0){
              logger.info(req.id, 'Authorize failed! with authorization ' + req.headers.authorization);
              return res.status(cst.HttpForbiddenStatus).json(err);
            }
            logger.info(req.id, 'Happen unkown err', err);
            return res.status(cst.HttpCriticalErrorStatus).json(new esErr.ESErrors(esErr.SysUnkownError));
         });
    }
    
    view(req, res, cb){
      var self = this;
      if (trycatch){
        trycatch(function(){          
            self._view_no_error_catch(req, res, cb);
        },function(err){
          var reqVal = {};
          util.getReqLogProperty(req, reqVal, 0)
          logger.info(req.id, 'Production is brought down', reqVal);
          logger.error(req.id, 'Production is brought down',err);          
          return res.status(cst.HttpCriticalErrorStatus).json(new esErr.ESErrors(esErr.SysUnkownError));
        });
      }
      else{          
        this._view_no_error_catch(req, res, cb);
      }
    }    
}

function asView(ViewClass){
  var viewObj = new ViewClass(ViewClass);
    function wapper(req, res, cb){
        viewObj.view(req, res, cb);
    }
    return wapper;
}

class serverCallView extends ViewBase{
  static AUTHENTICATORS = [auth.EsnaServerAuthenticator];
}

class regUserCallView extends ViewBase{
  static AUTHORISERS = [authorize.OAuthAuthorizer, authorize.regOnlyAuthorizer];
}

class anyCallView extends ViewBase{
  static AUTHENTICATORS = [auth.JwtAuthenticator, auth.Oauth2Authenticator, auth.AnonymousAuthenticator];
}

class anyCall4001View extends ViewBase{
  static AUTHENTICATORS = [auth.JwtAuthenticator, auth.Oauth2Authenticator, auth.AnonymousAuthenticator4001];
}

class DeveloperAdminView extends ViewBase{
  static AUTHORIZERS = [authorize.DeveloperAdminAuthorizer];
}

export default {
    ViewBase: ViewBase,
    asView: asView,
    serverCallView: serverCallView,
    anyCallView: anyCallView,
    anyCall4001View: anyCall4001View,
    DeveloperAdminView: DeveloperAdminView,
    regUserCallView: regUserCallView
};



