import OAuthAccessTokens from './accessToken.model';
import OAuthAccessTokensBackend from './accessToken.backend';
import User from '../../../api/user/user.model';
import config from '../../../config';
import auth from '../../auth.service.js';
import logger from '../../../modules/logger';
import vw from '../../../api/viewBase/viewBase';
import esErr from '../../../errors/errors';
import cst from '../../../utils/serverConstants';
import dbWp from '../../../utils/dbwrapper';

exports.deleteTokens = class deleteTokens extends vw.serverCallView{   
    handle(req, res, cb){
        if (!req.body || !req.body.accesstokens) {
            logger.error(req.id, 'Bad request, missing token');
            return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.SyncDeleteAccessTokenInvalidData));
        }
        let src = {
            reqId: req.id,
            type: 'req',
            domain: req.esDomain,
        };
        dbWp.execute(OAuthAccessTokens,
                     OAuthAccessTokens.remove,
                     req.id,
                     {
                        accessToken: {
                        $in: req.body.accesstokens
                        }
                     },
                     function(err, result){
                         if (err) {
                             logger.error(src.id, 'Error while deleting tokens ', req.body.accesstoken, err);
                             return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.SyncDeleteAccessTokenFailed));
                         }
                         return res.status(cst.HttpSuccessStatus).json({});
                     });
                     
    }
};
    
exports.newToken = class newToken extends vw.serverCallView{
    handle(req, res, cb){
        if (!req.body || !req.body.accesstoken) {
            logger.error(req.id, 'Bad request, missing token');
            return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.SyncAddAccessTokenInvalidData));
        }
        var token = req.body.accesstoken;
        if (!token.token || !token.clientId || !token.userId || !token.expires || !token.scope) {
            logger.error(req.id, 'Bad request without accesstoken, please check payload');
            return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.SyncAddAccessTokenInvalidData));
        }
        var onesnaUser = req.body.user;
        if (!onesnaUser){
            logger.error(req.id, 'Bad request with out user, please check payload');
            return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.SyncAddAccessTokenInvalidData));
        }
        let src = {
            reqId: req.id,
            type: 'req',
            domain: req.esDomain,
        };
        let data = {
           token: token,
           onesnaUser: onesnaUser
        };
        OAuthAccessTokensBackend.sync_add_accesstoken(src, data, function(err, result){
            if (err){
                logger.error(src.id, 'Error syncing accesstoken for this user ',data, err);
                return res.status(cst.HttpErrorStatus).json(err);
            }
            return res.status(cst.HttpSuccessStatus).json({});
        });
    }
};




