'use strict';

import _ from 'lodash';
import Anonymous from './anonymous.model';
import logger from '../../modules/logger';
import jwt from 'jsonwebtoken';
import config from '../../config';
import vw from '../viewBase/viewBase';
import auth from '../../auth/auth.service';
import esErr from '../../errors/errors'
import cst from '../../utils/serverConstants';
import anonymousEvent from './anonymous.event';
import util from '../../utils/serverHelper';

exports.create = class create extends vw.ViewBase{
    static AUTHENTICATORS = [];

    handle(req, res, cb){
        var newAnonymousUser = new Anonymous(req.body);

        newAnonymousUser.save(function(err, user) {
            if (err) return validationError(res, err);
            var parts = req.esDomain.split('.');
            var issval = req.esDomain;
            if (parts.length >= 3){
                issval = parts.slice(parts.length - 2);
                issval = issval;
            }
            var token = jwt.sign({anonymous_id: user._id, product_type: cst.ES_PRODUCT_ONESNA, iss: issval}, user.secret, { expiresInSeconds: 60*60*24 });
            // res.cookie('AUTH_TOKEN', token);
            anonymousEvent.emitAnonymousCreated(req, user);
            res.json({ token: token });
        });
    };
};

exports.me = class me extends vw.ViewBase{
    static AUTHENTICATORS = [auth.AnonymousAuthenticator];

    handle(req, res, cb){
        var userId = req.anonymousUser._id;
        Anonymous.findOne({
            _id: userId
        }, function(err, user) { // don't ever give out the password or salt
            if (err) {return next(err)};
            if (!user) {
                return res.status(cst.HttpUnauthorizedAnonymousStatus).send(new esErr.ESErrors(esErr.AuthenticateErrorAnony4001JWT))
            }
            user.secret = undefined;
            return res.status(cst.HttpSuccessStatus).send(user);
        });
    };
};



