import xtend from 'xtend';
import config from '../config';
import jwt from 'jsonwebtoken';

import User from '../api/user/user.model';
import Anonymous from '../api/anonymous/anonymous.model';
import logger from '../modules/logger';
import request from 'request';
import { Oauth2Authenticator } from './auth.service';
import { OAuthAuthorizer } from './authorizers';
import cst from '../utils/serverConstants';
import {socketCache} from './lru';
import _ from 'lodash';
import esErr from  '../errors/errors';

function UnauthorizedError (code, error) {
    Error.call(this, error.message);
    this.message = error.message;
    this.inner = error;
    this.data = {
        message: this.message,
        code: code,
        type: "UnauthorizedError"
    };
}

UnauthorizedError.prototype = Object.create(Error.prototype);
UnauthorizedError.prototype.constructor = UnauthorizedError;

function getTokenSecret (token, cb) {
    if (!token) {
        return cb({ message: 'Can not find a valid token' });
    }
    var payload = jwt.decode(token);
    var uid = payload.user_id;
    // for Anonymous token
    if (payload.anonymous_id) {
        Anonymous.findById(payload.anonymous_id, function (err, anonymous) {
            if (err || !anonymous) return cb(null);
            if (anonymous) {
                var secret = (new Buffer(anonymous.secret, 'utf-8')).toString('binary');
                return cb(anonymous, secret);
            }
        });
    } else {
        // for Onesna token
        User.findOne({ndbid: uid}, function (err, user) {
            if (err || !user) return cb(null);
            if (user) {
                var secret = (new Buffer(user.secret, 'utf-8')).toString('binary')
                return cb(user, secret + config.commonSecretJwt, token);
            }
        });
    }
}


function noQsMethod(options) {
    return function (socket) {
        var server = this;

        if (!server.$emit) {
            //then is socket.io 1.0
            var Namespace = Object.getPrototypeOf(server.server.sockets).constructor;
            if (!~Namespace.events.indexOf('authenticated')) {
                Namespace.events.push('authenticated');
            }
        }

        var auth_timeout = setTimeout(function () {
            socket.disconnect('unauthorized');
        }, options.timeout || 5000);

        socket.on('authenticate', function (data) {
            clearTimeout(auth_timeout);
            getTokenSecret(token, function(user, secret){
                jwt.verify(token, secret, options, function(err, decoded) {
                    if (err) {
                        return socket.disconnect('unauthorized');
                    }
                    data.user = user;
                    data.decoded_token = xtend({_id: user._id}, decoded);
                    socket.emit('authenticated');
                    if (server.$emit) {
                        server.$emit('authenticated', socket);
                    } else {
                        server.server.sockets.emit('authenticated', socket);
                    }
                });
            });
        });

    };
}

function authorize(options, onConnection) {
    var defaults = {
        success: function(data, accept){
            if (data.request) {
                accept();
            } else {
                accept(null, true);
            }
        },
        fail: function(error, data, accept){
            if (data.request) {
                accept(error);
            } else {
                accept(null, false);
            }
        }
    };

    var auth = xtend(defaults, options);

    if (!options.handshake) {
        return noQsMethod(options);
    }

    return function(data, accept){
        var token, error;
        var req = data.request || data;
        var authorization_header = (req.headers || {}).authorization;
        var method;

        if (authorization_header) {
            var parts = authorization_header.split(' ');
            if (parts.length == 2) {
                var scheme = parts[0],
                    credentials = parts[1];

                if (/^Bearer$/i.test(scheme)) {
                    token = credentials;
                }
            } else {
                error = new UnauthorizedError('credentials_bad_format', {
                    message: 'Format is Authorization: Bearer [token]'
                });
                return auth.fail(error, data, accept);
            }
        }

        //get the token from query string
        if (req._query && req._query.token) {
            token = req._query.token;
            //here check token type
            method = req._query.tokenType || 'jwt';
        }
        else if (req.query && req.query.token) {
            token = req.query.token;
        }

        if (!token) {
            error = new UnauthorizedError('credentials_required', {
                message: 'No Authorization header was found'
            });
            return auth.fail(error, data, accept);
        }

        if (method === 'jwt') {
            var cacheToken = 'jwt ' + token;
            if (cacheToken && socketCache.get(cacheToken)) {
              var cachedObj = socketCache.get(cacheToken);
              _.assign(data, cachedObj);
              logger.info(req.id, 'Got user/anonymous from cache');
              auth.success(data, accept);
            } else {
                getTokenSecret(token, function(user, secret){
                    jwt.verify(token, secret, options, function(err, decoded) {
                        if (err) {
                            error = new UnauthorizedError('invalid_token', err);
                            return auth.fail(error, data, accept);
                        }
                        logger.info('socket jwt decoded:', decoded);

                        if (user.aType === 'user') {
                            data.user = user;
                        }
                        if (user.aType === 'anonymous') {
                            data.anonymousUser = user;
                        }
                        data.decoded_token = xtend({_id: user._id}, decoded);
                        socketCache.set(cacheToken, {user: data.user, anonymousUser: data.anonymousUser, decoded_token: data.decoded_token});
                        auth.success(data, accept);
                    });
                });
            }
        } else if (method === 'oauth') {
            var cacheToken = 'Bearer ' + token;
            if (cacheToken && socketCache.get(cacheToken)) {
              var cachedObj = socketCache.get(cacheToken);
              _.assign(data, cachedObj);
              logger.info(req.id, '[SocketOauthAuth]Got user/anonymous from cache', cacheToken);
              return auth.success(data, accept);
            } else {
              var oauthAuthen = new Oauth2Authenticator();
              var oauthAuthor = new OAuthAuthorizer()
              oauthAuthen.verify(token, function(err, user){
                  if (err){
                      return auth.fail(err, data, accept);
                  }
                  let scope = oauthAuthen.accessToken.scope;

                  if (oauthAuthor.scopesCompareOtherScopes(scope, [cst.OAuth2ScopeLogan]) == cst.IncludeScope){
                    data.user = user;
                    socketCache.set(cacheToken, {user: user});
                    return auth.success(data, accept);
                  } else {
                    return auth.fail(new esErr.ESErrors(esErr.AuthorizeErrorOauth2), data, accept);
                  }
              });
            }
        }
    }
};

exports.authorize = authorize;


