import vw from '../viewBase/viewBase';
import logger from '../../modules/logger/index';
import ndutil from 'util'; 
import messageEvt from '../message/message.event';
import cst from '../../utils/serverConstants';
import util from '../../utils/serverHelper';

exports.notifyCallback = class notifyCallback extends vw.serverCallView{
  handle(req, res, cb){
    var functionName = '[notifyCallback] ';
    var src = util.getSrcFromRequest(req);
    var data = req.body;
    logger.info(src.id, ndutil.format('%s Notify user the document conver has some change', functionName), data);
    messageEvt.emitFileConverted(src, data, (err, result)=>{
      if (err){
        logger.error(src.id, functionName + 'Failed to emit event to notify clients the message\'s changing', err);
        return res.status(cst.HttpErrorStatus).json(err);
      }
      else{
        logger.info(src.id, functionName + 'Successful to emit event to notify clients the message\'s changing is updated');
        return res.json({})
      }
    });    
  }
};



