'use strict';

import config from '../../config';
import q from 'q';
import moment from 'moment';
import templater from './templater';
import sendgrid from './sendgrid';
import User from '../../api/user/user.model';
import logger from '../logger';
import userEvent from '../../api/user/user.event';
import dbWp from '../../utils/dbwrapper';
import esErr from '../../errors/errors';
import enrollEvent from '../../api/enroll/enroll.event';
import async from 'async';
import util from '../../utils/serverHelper';
import ga from "../analytics/google";
import gaCst from "../analytics/google/constants";

userEvent.onUserInvited(function (src, data) {
    logger.info(src.id,'1 User is invited! Event received!', data.user);
    if (!data.user) {
        logger.error(src.id,'error, undefined user', data);
    }
    if (!data.invite) {
        logger.error(src.id,'error, undefined invite', data);
    }

    var link = data.invite.makeInviteUrl(src.domain);
		var sender = data.sender.displayname || data.sender.username;
    var linkRef = new Buffer(JSON.stringify({
        spaceName: data.title,
        inviterName: data.sender.displayname,
				inviterEmail: data.sender.username
    })).toString('base64');

    if (linkRef) {
        link += "?inviteInfo=" + linkRef;
    }

    sendgrid.sendInviteEmail({
        sender: data.sender,
        name: data.user.name || data.user.email,
        email:  data.user.email,
        link: link,
        title: data.title,
        emailType: data.user.emailType
    })
    .then(function () {
        ga.postEvent({category: gaCst.c_Email, action: gaCst.a_newInviteEmail, label: data.invite._id});
        logger.info(src.id,'Invitation email successfully sent !!! ' +  data.user.email);
    })
    .catch(function (err) {
        logger.error(src.id,'Error sending invite to '  +  data.user.email, err);
    });
});


enrollEvent.onEnrollCreated(function (src, enroll) {
    logger.info(src.id, '1 User is requesting enrollAdmin! Event received!', enroll);
    enroll.link = config.getLink(src.domain) + "/spaces/enrollrequests/" + enroll._id + "/approval";
    util.getEnrollAdminEmails(function (emails) {
        async.each(emails, function(email, callback) {
            enroll.to = email;
            sendgrid.sendRequestControlEmail(enroll)
            .then(function () {
                ga.postEvent({category: gaCst.c_Email, action: gaCst.a_newEnrollEmail, label: ""});
                logger.info(src.id,'requesting email for ' + enroll.email + ' successfully sent to !!! ' +  email);
                callback();
            })
            .catch(function (err) {
                logger.error(src.id,'Error sending requests to '  +  email, err);
                callback();
            });
        }, function(err, result){
            logger.info(src.id, "done sending request emails");
        });      
    });
});

enrollEvent.onEnrollApproved(function (src, enroll) {
    logger.info(src.id,'Enroll request is approved! Event received!', enroll);
    enroll.link = config.getLink(src.domain) + "/spaces/invite";
    sendgrid.sendRequestApprovedEmail(enroll)
    .then(function () {
        ga.postEvent({category: gaCst.c_Email, action: gaCst.a_EnrollApprovedEmail, label: ""});
        logger.info(src.id,'Approved congratulation email successfully sent !!! ' +  enroll.email);
    })
    .catch(function (err) {
        logger.error(src.id,'Error sending congratulation email to '  +  enroll.email, err);
    });
});


