var logger = require('../modules/logger');


class OAuthAuthorizer{
    constructor(){
      this.name = 'OAuthAutherizer';
    }
    
    scopesCompareOtherScopes(userScopes, requiredScopes){
        if (requiredScopes === undefined ||  requiredScopes === null){
            return cst.IncludeScope;
        }
        if (requiredScopes && requiredScopes.length === 0){
            return cst.NotIncludeScope;
        }
        else{
            for (let scope of requiredScopes){
                if (userScopes.indexOf(scope) < 0){
                    logger.warn("scope required not matching", scope);
                    return cst.NotIncludeScope;
                }
            }
            return cst.IncludeScope;
        }
    }
    
    check(req, res, view, cb){ 
        if (req.auth && req.auth.authenType === cst.AuthenticateTypeOAuth2){
            if (this.scopesCompareOtherScopes(req.auth.scope, view.oauthscope) == cst.IncludeScope){
                return cb();
            }
            return cb(new esErr.ESErrors(esErr.AuthorizeErrorOauth2));
               
        }
        return cb();
    }
}

var verifyTopicCreator = function (user, topic, cb) {
    if (!user || !topic) {
        return cb('No user or No topic');
    };
    if (topic.cid.toString() === user._id.toString()) {

        return cb();
    }

    return cb(new esErr.ESErrors(esErr.AuthorizeFailed))
};

var verifyMessageCreator = function (user, message, cb) {
    if (!user || !message) {
        return cb('No user or No message');
    };
    if (message.sender && message.sender._id && message.sender._id.toString() === user._id.toString()) {
        return cb();
    }

    return cb(new esErr.ESErrors(esErr.AuthorizeFailed))
};

class isIdeaCreatorByParams{
    check(req, res, view, cb){
        if (!req.params || !req.params.ideaId) {
            return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.BadRequestError));
        }
        var me = req.anonymousUser || req.user;

        Message.findById(req.params.ideaId, function (err, message) {
            if (err) {
                logger.warn(req.id, 'Error find message :' + req.params.ideaId, err);
                return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.ServerInternalError));
            }
            if (!message) {
                logger.warn(req.id, 'Message not found : ' + req.params.ideaId);
                return res.status(cst.HttpNotFoundStatus).json(esErr.NotExsistedError);
            }
            verifyMessageCreator(me, message, function (err, result) {
                if (err) {
                    logger.warn(req.id, 'Fail to authorize user to this message' + req.params.ideaId, err);
                    return cb(err);
                } else {
                    req.message = message;
                    req.me = me;
                    return cb();    
                }
            });
        });
    }
}

