class isApprovedAuthorizer{
    check(req, res, view, cb){
        const user = req.user;
        if (!user) {
          return cb(new esErr.ESErrors(esErr.AuthorizeErrorDeveloperAdmin));
        }
        const emails = user.emails.map(function (email) {
          return email.value;
        });
        async.series([
          function(callback) {
            util.getDeveloperAdmins(function (admins) {
              if (admins.length > 0) {
                async.eachSeries(emails, function(email, callback) {
                  if (util.checkItemInList(email, admins)) {
                    return cb();
                 }
                 return callback();
                }, function(err, result){
                  return callback();
                });
              } else {
                return callback();
              }
            });
          },
          function(callback) {
            util.getEnrollAdminEmails(function (enrollAdmins) {
              if (enrollAdmins.length > 0) {
                async.eachSeries(emails, function(email, callback) {
                  if (util.checkItemInList(email, enrollAdmins)) {
                    return cb();
                 }
                 return callback();
                }, function(err, result){
                  return callback();
                });
              } else {
                return callback();
              }
            });
          },
          function(callback) {
            async.eachSeries(emails, function(email, callback) {
               if (util.checkZangUser(email)) {
                  return cb();
               }
               return callback();
            }, function(err){
                return callback();
            });
          },
          function(callback){
            Enroll.findOne({email: {$in: emails}}, function (err, enroll) {
                if(err) { 
                    return cb(err);
                }
                if(!enroll || enroll.status !== "approved") { 
                    return cb(new esErr.ESErrors(esErr.AuthorizeErrorNotOnApprovedList));
                }
                return cb();
            });
          }],
          function(err, results){
            if (err) {
                return callback(new esErr.ESErrors(esErr.AuthorizeErrorNotOnApprovedList));
            }
            return cb();
         });
  }
}

