class regOnlyAuthorizer{
  
  check(req, res, view, cb){
    let user = req.user;
    if (!user) {
      return cb(new esErr.ESErrors(esErr.AuthorizeErrorPermission));
    } else {
      return cb();
    }
  }
}


class PermissionAuthorizer{
  constructor(askPermission){
    this.name = 'PermissionAuthorizer';
    this.askPermission = askPermission;
  }
  
  check(req, res, view, cb){
    let user = req.anonymousUser || req.user;
    let self = this;
    permCfg.hasPerm(req, this.askPermission, user, null, null, (err, result)=>{
      if (err || (!result)){
        return cb(new esErr.ESErrors(esErr.AuthorizeErrorPermission));
      }
      req.relPermChecker = self;
      return cb();
    });
  }
  
  checkRelation(req, object, objectType, cb){
    let user = req.anonymousUser || req.user;
    permCfg.hasPerm(req, this.askPermission, user, object, objectType, (err, result)=>{
      if (err || (!result)){
        return cb(new esErr.ESErrors(esErr.AuthorizeErrorPermission));
      }
      return cb();
    });
  }
}

