/**
 * Broadcast updates to client when the model changes
 */
import messageCst from '../../flux/constants/MessageConstants';
import SocketConstants from '../../flux/constants/SocketConstants';
import logger from '../../modules/logger';
import messageEvt from './message.event';
//Model events to emit

export function register(socketio) {
  messageEvt.onFileConverted(function (src, eventObj, cb) {
    let functionName = '[messageEvt.onFileConverted] ';
    logger.info(src.id, functionName + 'Will send message update event to topic=' + eventObj.topicId + ' eventtype=' +
        messageCst.ACTIVITY_CATEGORY_TYPES.APP_EVENTS.MESSAGE_UPDATED + ' payload=', eventObj.payload);

    var data = Object.assign({
      //_id: eventObj.payload.messageId,
      category: messageCst.ACTIVITY_CATEGORY_TYPES.APP_EVENTS.MESSAGE_UPDATED,
    }, eventObj.payload)

    socketio.to('topic_' + data.topicId).emit(
      SocketConstants.EVENT_NAMESPACE.GROUP_MESSAGE_SENT, data);
    return cb(null);
  });
}



