import LRU from 'lru-cache';

var options = { 
  max: 100000,
  maxAge: 1000 * 5 };

var apiCache = LRU(options); // sets just the max size
var userCache = LRU(options);
var socketCache = LRU(options);
export default {apiCache, userCache, socketCache};


