'use strict';

import fs from '../../modules/file';
import logger from '../../modules/logger';
import esErr from '../../errors/errors';
import cst from '../../utils/serverConstants';
import uuid from'node-uuid';
import async from 'async';
import Anonymous from '../anonymous/anonymous.model';
import User from '../user/user.model';
import dbWp from '../../utils/dbwrapper';
import utils from '../../utils/Utils';

const DEFAULT_UPLOAD_URL_DURATION = 10 * 60; //10 min
const DEFAULT_DOWNLOAD_URL_DURATION = 1 * 60; //1min

function getUploadSignedUrls(src, data, expiration, cb) {
    expiration = expiration || utils.getSecondsFromNow(DEFAULT_UPLOAD_URL_DURATION);
    var processFiles = function (index, input, output, callback) {
        //console.log('[getUploadSignedUrls] input', input, index);
        if (!input) {
            return callback(new esErr.ESErrors(esErr.BadRequestError));
        }
        if (index === input.length) {
            return callback(null, output);
        }

        let cur_file = input[index];
        if (cur_file) {
            cur_file.fileKey = cur_file.fileKey || uuid.v4();
            fs.getUploadSignedUrl(src, {file: cur_file}, function (err, url) {
                if (url) {
                    output.push({
                        fileKey: cur_file.fileKey,
                        url: url
                    });
                    return processFiles(index+1, input, output, callback);
                } else {
                    return processFiles(index+1, input, output, callback);
                }
            }, expiration.getTime());
        } else {
            return processFiles(index+1, input, output, callback);
        }
    };

    processFiles(0, data.files, [], function(err, result) {
        if (err) {
            return cb(err);
        }

        return cb(null,result);
    });
}

function getDownloadSignedUrls (src, data, expiration, cb) {
    expiration = expiration || utils.getSecondsFromNow(DEFAULT_DOWNLOAD_URL_DURATION);
    var processFiles = function (index, input, output, callback) {
        //console.log('[getDownloadSignedUrls] input', input, index);
        if (!input) {
            return callback(new esErr.ESErrors(esErr.BadRequestError));
        }
        if (index === input.length) {
            return callback(null, output);
        }

        let cur_file = input[index];
        if (cur_file) {
            fs.getDownloadSignedUrl(src, {key: cur_file}, function (err, url) {
                if (url) {
                    output.push({
                        fileKey: cur_file,
                        url: url
                    });
                    return processFiles(index+1, input, output, callback);
                } else {
                    return processFiles(index+1, input, output, callback);
                }
            }, expiration.getTime());
        } else {
            return processFiles(index+1, input, output, callback);
        }
    };

    processFiles(0, data.fileKeys, [], function(err, result) {
        if (err) {
            return cb(err);
        }

        return cb(null,result);
    });
}


exports.getUploadUrls = function(src, data, cb) {
    for(var i in data){
        data[i].fileKey = uuid.v4(); //enforce key;
    }
    getUploadSignedUrls(src, data, null, cb);
}

exports.getUploadSignedUrls = function(src, data, expiration, cb) {
    getUploadSignedUrls(src, data, expiration, cb);
}

exports.getDownloadUrls = function(src, data, cb) {
    getDownloadSignedUrls(src, data, null, cb);
}

exports.getDownloadSignedUrls = function(src, data, expiration, cb) {
    getDownloadSignedUrls(src, data, expiration, cb);
}


exports.getProfileImageUploadUrl = function(src, data, cb) {
    var getPictureFileKey = function (user, picture, callback) {
        if (picture.dimension === 'original') {
            logger.info(src.id, 'uploading an original picture');
            picture.fileKey = 'pictures/pfpic'+ '_' + user.aType + '_' + uuid.v4();
            if (user.aType === 'anonymous') {
                dbWp.execute(Anonymous,
                    Anonymous.findOneAndUpdate,
                    src.id,
                    {_id: user._id},
                    {
                        $set: {
                            picturefile: picture.fileKey
                        }
                    }, {new: true},
                    function (err, updatedAnonymous) {
                        if (err) {
                            return callback(err);
                        }
                        return callback(null, picture);
                    }
                );
            }
            if (user.aType === 'user') {
                dbWp.execute(User,
                    User.findOneAndUpdate,
                    src.id,
                    {_id: user._id},
                    {
                        $set: {
                            picturefile: picture.fileKey
                        }
                    }, {new: true},
                    function (err, updatedUser) {
                        if (err) {
                            return callback(err);
                        }
                        return callback(null, picture);
                    }
                );
            }
        } else {
            if (!user.picturefile || user.picturefile.indexOf('pictures/') !== 0) {
                return callback('error');
            }
            logger.info(src.id, 'uploading a thumbnail picture');
            picture.fileKey = user.picturefile + '/' + picture.dimension + '_' + picture.dimension;
            return callback(null, picture);
        }
    };

    var processFiles = function (index, input, output, callback) {
        if (!input) {
            return callback(new esErr.ESErrors(esErr.BadRequestError));
        }
        if (index === input.length) {
            return callback(null, output);
        }

        let cur_file = input[index];
        if (cur_file) {
            getPictureFileKey(data.user, cur_file, function (err, resultFile) {
                if (err) {
                    return processFiles(index+1, input, output, callback);
                }
                fs.getUploadPublicUrl(src, {file: resultFile}, function (err, url) {
                    if (url) {
                        output.push({
                            fileKey: resultFile.fileKey,
                            url: url
                        });
                        return processFiles(index+1, input, output, callback);
                    } else {
                        return processFiles(index+1, input, output, callback);
                    }
                });
            });
        } else {
            return processFiles(index+1, input, output, callback);
        }
    };

    processFiles(0, data.files, [], function(err, result) {
        if (err) {
            return cb(err);
        }

        return cb(null,result);
    });
}



