import esErr from '../../errors/errors';
import logger from '../../modules/logger/index';
import auth from '../../auth/auth.service';
import vw from '../viewBase/viewBase';
import tkjbobk from './taskqueue.backend';
import cst from '../../utils/serverConstants';
import util from '../../utils/serverHelper';
import ndUtil from 'util';

function ChangeTimeoutOfReqTask(req, inWaitSeconds){
  let waitSeconds = inWaitSeconds || cst.taskRequestTimeoutSeconds;
  req.taskRequestTimeout = Date.now() + waitSeconds * 1000;
  req.clearTimeout();
  let tmout = setTimeout(function(){
    req.timedout = true;
    req.emit('timeout', waitSeconds * 1000);
  }, waitSeconds * 1000);
  return tmout;
}

exports.runner = class runner extends vw.serverCallView{
  handle(req, res, cb){
    let tmout = ChangeTimeoutOfReqTask(req, this.waitforSeconds);
    logger.info(req.id, 'Task queue will run a task');
    var src = util.getSrcFromRequest(req);
    req.connection.on('close', () => {
      logger.warn(req.id, 'Connection is closed');
      src.esClosedFlag = true;
    });
    src.taskRequestTimeout = req.taskRequestTimeout;
    tkjbobk.runner(src, req.body, (err, result) => {
      clearTimeout(tmout);
      if (err){
        logger.warn(req.id, 'Task queue runner happen error', err);
        if (err.code == esErr.SysAlreadyRegisterDeferError || 
            err.code == esErr.TaskNoRetryError ||
            err.code == esErr.TaskqueueEndBeforeTimout){
          //Never try again
          return res.status(cst.HttpErrorTaskQueueNeverTry).json(err);
        }
        else{
          return res.status(cst.HttpErrorStatus).json(err);
        }
      }
      else{
        logger.info(req.id, 'Task queue runner finish successfuly');
        return res.status(cst.HttpSuccessStatus).json({status: 'success'});
      }
    });
  }
};

exports.syncSenderOfMessages = class syncSenderOfMessages extends exports.runner{}


