// Updates an existing Topic in the DB.
exports.update = class update extends vw.ViewBase{
  static AUTHORIZERS = [new authorize.PermissionAuthorizer(authorize.pm.PERM_TOPIC_UPDATE)];
	handle(req, res, cb){
		if(req.body._id) { delete req.body._id; }
		Topic.findById(req.params.topicId, function (err, topic) {
  		if (err) {
  			logger.error(req.id, 'Error finding topic', err);
  			return handleError(res, new esErr.ESErrors(esErr.BadRequestError));
  		}
  		if(!topic) { return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.NotExsistedError)); }
  		req.relPermChecker.checkRelation(req, topic, cst.TypeTopic, (err)=>{
  		  if (err){
          return cb(err);
        }
  		  var updated = _.merge(topic, req.body);
  		  var src = util.getSrcFromRequest(req); 
        updated.save(function (err) {
          if (err) { return handleError(res, err); }
          TopicEvent.emitTopicUpdated(src, updated, topic);
          return res.status(cst.HttpSuccessStatus).json(topic);
        });
  		});
		});
	};
};

