exports.searchTopics = class searchTopics extends vw.ViewBase{
  	handle(req, res, cb){
  		var str,
  			skip,
  			limit,
  			query = [];
  		if (req.query) {
			str = req.query.search;
	  		skip = req.query.skip;
	  		limit = req.query.limit;
  		}

	 	if (str) {
		  	str = escapeStringRegexp(str);
		  	query.push({
				$match: {
					$or: [
						{ title: {$regex: str, $options: 'i'} }
					]
				}
			});
	 	}

		query.push({
			$project: {
				_id: 1,
				title: 1,
				d: 1,
				members: 1
			}
		});

		if (skip) {
	 		query.push({$skip:skip});
	 	}

	 	if (limit) {
	 		query.push({$limit:limit});
	 	}

	 	Topic.aggregate(query, function (err, topics) {
		  	if (err) {
				logger.error(req.id, 'Error searching topic', err);
				return handleError(res, new esErr.ESErrors(esErr.ServerInternalError));
			}
		  	return res.status(cst.HttpSuccessStatus).json({data: topics});
	  	});
	};
};

exports.newTopic = class newTopic extends vw.ViewBase{
  static AUTHORIZERS = [new authorize.PermissionAuthorizer(authorize.pm.PERM_TOPIC_CREATE), authorize.isApprovedAuthorizer];
	handle(req, res, cb){
		if (!req.body || !req.body.topic || !req.body.invitees) {
		  	return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.BadRequestError));
		}
		if (!req.user) {
			return res.status(cst.HttpUnauthorizedStatus).send(new esErr.ESErrors(esErr.AuthenticateFailed));
		}
		var src = util.getSrcFromRequest(req),
			data = {
			creator: req.user,
			invitees: req.body.invitees,
			topic: req.body.topic,
			startDateTime: req.body.startDateTime,
			endDateTime: req.body.endDateTime,
			now: Date.now(),
			members:[],
			informChannel: req.body.informChannel || 'server'
		};

		topicBz.newTopic(src, data, function (err, data) {
			if (err) {
				logger.error(req.id, 'Error creating topic', err);
				return handleError(res, new esErr.ESErrors(esErr.ServerInternalError));
			}
			return res.status(cst.HttpSuccessStatus).json(data);
		});
	};
};

exports.getMembersOfTopic = class getMembersOfTopic extends vw.anyCallView {
  static AUTHORIZERS = [new authorize.PermissionAuthorizer(authorize.pm.PERM_TOPIC_READ)];
	handle(req, res, cb){
		if (!req.params || !req.params.topicId) {
			return res.status(cst.HttpErrorStatus).send('Bad request');
		}
		req.relPermChecker.checkRelation(req, req.params.topicId, cst.TypeTopic, (err)=>{
      if (err){
        return cb(err);
      }
  		var src = util.getSrcFromRequest(req);
  		var page = Number(req.query.page) || 1,
          	size = util.getListOfItemCaps(parseInt(req.query.size), 20),
        	size = size > 30 ? 30: size,
          	apiRoute = config.getLink(req.esDomain) + '/api/topics/' + req.params.topicId + '/members';
  		var data = {
  			topicId: req.params.topicId,
  			pagination: {
  		        skip: (page-1)*size,
  		        limit: size,
  		        apiRoute: apiRoute
  		    },
  		    search: req.query.search
  		};
  		topicBz.getUsersOfTopic(src, data, function (err, result) {
  			if (err) {
  				logger.error(err);
  				if (err.code == esErr.AuthorizeErrorPermission){
  				  return cb(err)
  				}
  				return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.ServerInternalError));
  			}
  			var returnData = {
  		        data: result.data,
  		        from: (page - 1) * size + 1,
  		        to: (page - 1) * size + result.data.length
  		      };
  		    if (result.hasNext) {
  		        returnData.nextPageUrl = apiRoute + '?page=' + (page + 1) + '&size=' + size;
  		    }
  		    if (page > 1) {
  		      	returnData.previousPageUrl = apiRoute + '?page=' + (page - 1) + '&size=' + size;
  		    }
  		    return res.status(cst.HttpSuccessStatus).json(returnData);
  		});
		});
	}
};

exports.quitTopic = class quitTopic extends vw.ViewBase{
  	handle(req, res, cb){
		if (!req.params || !req.params.topicId) {
			return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.BadRequestError));
		}
		Topic.findById(req.params.topicId, function (err, topic) {
			if (err) {
				return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.ServerInternalError));
			}
			if (!topic) {
				return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.NotExsistedError));
			}

			if (req.user) {
				topicBz.userQuitTopic(req.user, topic, function (err, result) {
					if (err) {
						return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.ServerInternalError));
					}
					return res.status(cst.HttpSuccessStatus).send(result);
				});
			}

			if(req.anonymous) {
				topicBz.anonymousQuitTopic(req.anonymous, topic, function (err, result) {
					if (err) {
						return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.ServerInternalError));
					}
					return res.status(cst.HttpSuccessStatus).send(result);
				});
			}
		});
	};
};

