var ns = '[MediaSessionManager]';
import _ from 'underscore';
import async from 'async';
import config from '../../config-env/environment/index';
import fileBackend from '../../api/file/file.backend';
import gcsService from '../../modules/file/gcs/gcs.service';
import logger from '../../modules/logger';
import utilExtended from '../../modules/utils/index';
import SocketConstants from '../../flux/constants/SocketConstants';
import MessageConstants from '../../flux/constants/MessageConstants';
import memcache from '../../modules/memcache';


import utils from '../Utils';

const SCREEN_SHARE_KEY_PREFIX = 'screen_share_files';

const DEFAULT_UPLOAD_URL_DURATION = 60 * 60; //1 hour min
const DEFAULT_DOWNLOAD_URL_DURATION = 60 * 60; //1min
const MAX_NUMBER_OF_SCREEN_FILES = 10;
function MediaSessionLockKey(sid){
	return 'MediaSessionLock:' + sid;
}


class MediaSessionManager {
	constructor(options) {
		this.confSessions = {};//this shouldbe a mem-db;
	}

	startUserMediaSession(topicId, user) {
		var userMediaSession = {
			sessionId: utils.GUID(),
			topicId: topicId,
			joined: new Date()
		}
		return userMediaSession;
	}

	getTempFileKey(user, prefix=''){
		return prefix + utils.GUID();
	}

	getTempFileUploadUrl(user, fileObj, cb, expiration){
		return gcsService.getUploadSignedUrl({}, {file: fileObj}, cb, expiration);
	}

	getTempFileDownloadUrl(user, fileKey, cb, expiration){
		return gcsService.getDownloadSignedUrl({}, {key: fileKey}, cb, expiration);
	}



	getNextAvailableFileIndex(topicId, user, sessionData) {
		let func = ns + '[getNextAvailableFileIndex]';
		var nextIndex = sessionData.screenshare.activeFile + 1;
		if(sessionData.screenshare.files.length < MAX_NUMBER_OF_SCREEN_FILES){
			nextIndex = sessionData.screenshare.files.length;
			var newTempFile = {
				uploadUrl: null,
				downloadUrl: null,
				fileIndex: nextIndex,
				fileKey: 'screen_' + topicId + '/' + nextIndex,//.getTempFileKey(user, 'screen_' + topicId + '/'),
				expires: null
			};
			sessionData.screenshare.files[nextIndex] = newTempFile;
			logger.debug(func, 'allocating a file in the pool nextIndex:', nextIndex);
			return nextIndex;
		}

		if(nextIndex >= MAX_NUMBER_OF_SCREEN_FILES){
			nextIndex = 0;
		}

		logger.debug(func, 'nextIndex', nextIndex);
		return nextIndex;
	}



	generateScreenShareSignedUrls(topicId, user, contentType, cb) {
		let func = ns + '[generateScreenShareSignedUrls]';
		var expiration = utils.getSecondsFromNow(DEFAULT_UPLOAD_URL_DURATION);
		contentType = contentType || '';
		var dt = new Date();
		console.log(func, expiration);
		console.log(func, dt, dt.toISOString());
		var result = {
			files: [],
			expires: expiration.toISOString()
		};

		var screenFiles = {};

		var files = [];
		var fileKeys = [];

		logger.info(func, 'begin:', topicId);
		for (var i = 0; i < MAX_NUMBER_OF_SCREEN_FILES; i++) {
			var nextIndex = i + 1;
			var fileKey = 'screen_' + topicId + '/' + nextIndex;
			screenFiles[fileKey] = {fileKey: fileKey};
			fileKeys.push(fileKey);
			files.push({
				fileKey: fileKey,
				'Content-Type': contentType
			});
		}

		//logger.info(func, 'files:', files);

		fileBackend.getUploadSignedUrls({}, {files: files}, expiration, function (err, uploadResults) {
			if (err) {
				return cb(err, null);
			}
			fileBackend.getDownloadSignedUrls({}, {fileKeys: fileKeys}, expiration, function (err, downloadResults) {
				if (err) {
					return cb(err, null);
				}

				for (var f in uploadResults) {
					screenFiles[uploadResults[f].fileKey].uploadUrl = uploadResults[f].url;
				}

				for (var f in downloadResults) {
					screenFiles[downloadResults[f].fileKey].downloadUrl = downloadResults[f].url;
				}

				for (var f in screenFiles) {
					var file = {
						fileKey: screenFiles[f].fileKey,
						uploadUrl: screenFiles[f].uploadUrl,
						downloadUrl: screenFiles[f].downloadUrl
					}
					result.files.push(file);
				}

				cb(null, result);

			})
		});
	}

	getScreenShareMemcacheKey (topicId){
		return SCREEN_SHARE_KEY_PREFIX + '_' + topicId;
	}
	processScreenShareAppRequests(topicId, user, data, sessionData, cb){
		let func = ns + '[processScreenShareAppRequests]';
		let self = this;
		sessionData.screenshare = sessionData.screenshare || {files: [], activeFile: 0};
		switch(data.category){
			case MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS.VIDEO.StartScreenShare:
				var redKey = this.getScreenShareMemcacheKey(topicId);
				//logger.info(func, 'about to clear redKey:', redKey);
				//memcache.del({}, redKey, function(err, result){
				//
				//});
				break;

			case MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS.VIDEO.ScreenShareUploadUrl:
				var redKey = this.getScreenShareMemcacheKey(topicId);
				logger.info(func, ' redKey:', redKey);
				var contentType  = null;
				if(data && data.content){
					contentType = data.content['Content-Type'];
				}
				logger.info(func, redKey, ' about to generate urls contentType:', contentType);
				self.generateScreenShareSignedUrls(topicId, user, contentType, function(err, screenSignedData){
					if(err){
						return cb(err);
					}
					cb(null, screenSignedData);
				});
				return true;
				break;
		}
		return false;
	}


	processScreenShareAppEvent(topicId, user, data, sessionData, cb)
	{
		let func = ns + '[processScreenShareAppEvent]';
		let self = this;
		switch (data.category) {
			case MessageConstants.ACTIVITY_CATEGORY_TYPES.APP_EVENTS.VIDEO.ScreenShareData:
				if(data.content.image){
					cb(null, data);
					return true;
				}

				var fileIndex = data.content.fileIndex;
				logger.debug(func, 'file exists fileIndex:', fileIndex, sessionData.screenshare.files[fileIndex]);
				if(fileIndex && sessionData.screenshare.files[fileIndex]){
					data.content = data.content || {};
					data.content.image = sessionData.screenshare.files[fileIndex].downloadUrl;
					cb(null, data);
				}
				return true;
		}
		return false;
	}
}




export default MediaSessionManager;


