import mongoose from 'mongoose';
import dbWp from '../../utils/dbwrapper';
import TopicMessage from '../message/message.model';
import NotifyEvent from './notify.event';
import TopicUser from './topicuser.model';
import logger from '../../modules/logger/index';
import _ from 'lodash';
import meetingConstants from '../../flux/constants/MeetingConstants';
import relationbk from '../relation/relation.backend';
import cst from '../../utils/serverConstants';
import util from '../../utils/serverHelper';
import TopicUserBK from './topicuser.backend';

var UserAccessed = function (src, data, cb) {
  var user = data.user,
      target = data.target,
      time = Date.now();

  var getUserRole = function (user, topic, callback) {
    var isMember = _.find(topic.members, {member: user._id.toString()});
    if (isMember) {
      logger.info(src.id, 'This is already a member/admin ', isMember);
      return callback(isMember.role);
    } else {
      return callback(meetingConstants.ATTENDEE_TYPES.GUEST.type);
    }
  }

  getUserRole(user, target, function (result) {
    var accessObj = {
        userId: user._id,
        userType: user.aType,
        targetId: target._id,
        lastAccess: time,
        role: result,
        targetType: target.type || cst.TypeTopic,
        title: target.title,
    };
    //var userTags = util.getTagsByType(user, user.aType);
    //var targetTags = util.getTagsByType(target, accessObj.targetType);
    //accessObj.tags = _.concat(userTags, targetTags);
    return updateTopicUser(src, accessObj, cb);
  });
}


var UserJoinInvite = function (src, data, cb) {
  var user = data.user,
      target = data.target,
      role = data.role;

  var accessObj = {
      userId: user._id,
      userType: user.aType,
      targetId: target._id,
      role: role,
      targetType: target.type || cst.TypeTopic,
      title: target.title,
  };
  //var userTags = util.getTagsByType(user, user.aType);
  //var targetTags = util.getTagsByType(target, accessObj.targetType);
  //accessObj.tags = _.concat(userTags, targetTags);
  return updateTopicUser(src, accessObj, cb);
}


var UserCreateTopic = function (src, data, cb) {
  var user = data.user,
      target = data.target,
      role = data.role;

  var accessObj = {
      userId: user._id,
      userType: user.aType,
      targetId: target._id,
      role: role,
      targetType: target.type || cst.TypeTopic,
      title: target.title,
  };
  //var userTags = util.getTagsByType(user, user.aType);
  //var targetTags = util.getTagsByType(target, accessObj.targetType);
  //accessObj.tags = _.concat(userTags, targetTags);
  return updateTopicUser(src, accessObj, cb);
}

var updateTopicUser = function (src, accessObj, cb) {
   dbWp.execute(TopicUser, TopicUser.findOneAndUpdate, src.id, {userId: accessObj.userId, targetId: accessObj.targetId, userType: accessObj.userType}, accessObj, {upsert: true, new: true}, function (err, result) {
    if (err) {
      return cb(err);
    }
    logger.info(src.id,'User TopicUserTable updated !', result);
    return cb(null, result);
  });
};

NotifyEvent.onUserAccessed(function (src, data) {
  logger.info(src.id,'new user access captured !');
  UserAccessed(src, data, function (err, result) {
    if (err) {
      logger.error(src.id, 'user access capture failed', err);
    }
  });
});

NotifyEvent.onUserJoinInvite(function (src, data) {
  logger.info(src.id,'user join invite captured !');
  UserJoinInvite(src, data, function (err, result) {
    if (err) {
      logger.error(src.id, 'user access capture failed', err);
    }
  });
});

NotifyEvent.onUserCreateTopic(function (src, data) {
  logger.info(src.id,'user create topic captured !');
  UserCreateTopic(src, data, function (err, result) {
    if (err) {
      logger.error(src.id, 'user access capture failed', err);
    }
  });
});

