/**
 * Using Rails-like standard naming convention for endpoints.
 * GET     /message              ->  index
 * POST    /message              ->  create
 * GET     /message/:id          ->  show
 * PUT     /message/:id          ->  update
 * DELETE  /message/:id          ->  destroy
 */

'use strict';

import _ from 'lodash';
import Message from './message.model';
import MessageBk from './message.backend';
import logger from '../../modules/logger/index'
import auth from '../../auth/auth.service'
import vw from '../viewBase/viewBase'
import userBackend from '../user/user.backend'
import fileBackend from '../file/file.backend';
import esErr from '../../errors/errors'
import html2json from 'html-to-json';
import Is from '../../utils/Is';
import util from '../../utils/serverHelper';
import querystring from 'querystring';
import cst from '../../utils/serverConstants';
import authorize from '../../auth/authorizers';
import dbWp from '../../utils/dbwrapper'; 
import ga from "../../modules/analytics/google";
import gaCst from "../../modules/analytics/google/constants";

// Get list of messages
exports.index = class index extends vw.ViewBase{
    handle(req, res, cb){
        Message.find(function (err, messages) {
            if(err) { return handleError(res, err); }
            return res.status(200).json(messages);
        });
    };
};

// Get a single Message
exports.show = class show extends vw.ViewBase{
  static AUTHORIZERS = [new authorize.PermissionAuthorizer(authorize.pm.PERM_TOPIC_READ)];
  handle(req, res, cb){
    Message.findById(req.params.id, function (err, message) {
        if(err) { return handleError(res, err); }
        if(!message) { return res.status(404).send('Not Found'); }
        req.relPermChecker.checkRelation(req, message.topicId, cst.TypeTopic, (err)=>{
          if (err){
            return cb(err);
          }
          var src = util.getSrcFromRequest(req);
          MessageBk.toDownloadableClientFormatMessages(src, [message], (err, convertedResults) => {
            if (err){
              logger.error(src.id, "Get message by id happenen error!", err);
              return cb(err);
            }
            return res.json(convertedResults[0]);
          });          
        });
    })
  };
};

