// Updates an existing Message in the DB.
exports.update = class update extends vw.anyCallView{
  static AUTHORIZERS = [new authorize.PermissionAuthorizer(authorize.pm.PERM_TOPIC_UPDATE)];
  handle(req, res, cb){
    let data = req.body;
    req.relPermChecker.checkRelation(req, data.topicId, cst.TypeTopic, (err)=>{
      if (err){
        return cb(err);
      }
      if (req.user){
        data.sender = {
            _id: req.user._id,
            type: cst.TypeUser,
            username: req.user.username,
            displayname: req.user.displayname,
            picture_url: util.getPicture_url(req.user.picturefile),
        };        
      }
      else if(req.anonymousUser){
        data.sender = {
            _id: req.anonymousUser._id,
            type: cst.TypeAnonymous,
            username: req.anonymousUser.username,
            displayname: req.anonymousUser.displayname,
            picture_url: util.getPicture_url(req.user.picturefile),
        };      
      }
      else{
        return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.SysUnkownError));
      }
      let src = util.getSrcFromRequest(req);
      MessageBk.updateMessagetoNoSenderPublicInfo(src, data, function(err, result){
        if (err){
          return res.status(cst.HttpErrorStatus).json(err);
        }
        if (result.sender.type == cst.TypeUser){
          result.sender = _.extend(result.sender, req.user.profile);
        }
        else if(result.sender.type == cst.TypeAnonymous){
          result.sender = _.extend(result.sender,
                                   {displayname: req.anonymousUser.displayname,
                                    picture_url:  req.anonymousUser.picture_url,
                                    username: req.anonymousUser.username});
        }
        return res.status(200).json(result);
      });    
    });
  };
};

// Deletes a Message from the DB.
exports.destroy = class destroy extends vw.ViewBase{
  static AUTHORIZERS = [new authorize.PermissionAuthorizer(authorize.pm.PERM_TOPIC_UPDATE)];
  handle(req, res, cb){
    Message.findById(req.params.id, function (err, message) {
        if(err) { return handleError(res, err); }
        if(!message) { return res.status(404).send('Not Found'); }
        req.relPermChecker.checkRelation(req, message.topicId, cst.TypeTopic, (err)=>{
          if (err){
            return cb(err);
          }
          Message.remove(function(err) {
              if(err) { return handleError(res, err); }
              return res.status(204).send('No Content');
          });
        });
    });
  };
};

exports.parseLink = class parseLink extends vw.anyCallView{
  handle(req, res, cb){
    let link = req.query.link;
    if(!link || !Is.$url(link, true)){
      res.status(400).send('');
      return;
    }
  
    html2json.request(link, {
      'meta': ['meta', function($meta){
        return {
          property: $meta.attr("property") || "",
          name: $meta.attr("name") || "",
          content: $meta.attr("content") || ""
        }
      }],
      'link': ['link', function($link){
        return {
          href: $link.attr("href") || "",
          rel: $link.attr("rel") || ""
        }
      }],
      'title': ['title', function($title){
        return $title.html();
      }],
      'images': ['img', function($img){
        return $img.attr('src')
      }]
    }, function(err, result){
      if(err){
        res.status(400);
      } else {
        res.status(200).json(result);
      }
    });
  };
};


exports.getSingleDownloadUrl = class getSingleDownloadUrl extends vw.anyCallView{
  static AUTHORIZERS = [new authorize.PermissionAuthorizer(authorize.pm.PERM_TOPIC_READ)];
  handle(req, res, cb){
    let msgId = req.params.id;
    let fileKey = querystring.unescape(req.params.fileKey);
    var src = util.getSrcFromRequest(req);

    if (!msgId || !fileKey) {
      logger.error(src.id, 'getUploadUrl happen error ');
      return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.BadRequestError));
    }
    let data = {
      objectId: msgId,
      objectType: 'message',
      fileKeys:[fileKey]
    };
    Message.findById(msgId, function (err, message){
      if (err){
        logger.error(src.id, 'getDownloadUrl happen error ', err);
        return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.UploadUrlCreateFailed));
      }
      req.relPermChecker.checkRelation(req, message.topicId, cst.TypeTopic, (err)=>{
        if (err){
          return cb(err);
        }
        fileBackend.getDownloadUrls(src, data, function (err, results) {
          if (err){
            logger.error(src.id, 'getDownloadUrl happen error ', err);
            return res.status(cst.HttpErrorStatus).send(new esErr.ESErrors(esErr.UploadUrlCreateFailed));
          }
          return res.redirect(results[0]);
        });
      })
    });
  }
};

exports.getPreViewIndexUrl = class getPreViewIndexUrl extends vw.anyCallView{
  static AUTHORIZERS = [new authorize.PermissionAuthorizer(authorize.pm.PERM_TOPIC_READ)];
  handle(req, res, cb){
    let functionName = '[getPreViewIndexUrl.handle] ';
    let messageId = req.params.msgId;
    let fileId = req.params.fileId;
    let src = util.getSrcFromRequest(req);
    console.log(src.id, functionName + 'Try to get preview index file by messageId=' + messageId + 'and fileId=' + messageId);
    dbWp.execute(Message,
      Message.findOne,
      src.id,
      {'_id': messageId, 'content.data.fileId':fileId},
      { "topicId": 1, "content.data.$": 1},
      (err, message) => {
        if (err || (!message)){
          if (err){
            logger.error(src.id, functionName + 'Get message by messageId=' + messageId + 'and fileId=' + fileId, err);
          }
          if (!message){
            logger.warn(src.id, functionName + 'Get message by messageId=' + messageId + 'and fileId=' + fileId + ' no record');
          }
          return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.NotExsistedError));
        }
        else{
          req.relPermChecker.checkRelation(req, message.topicId, cst.TypeTopic, (err)=>{
            if (err){
              logger.warn(src.id, functionName + 'Get message by messageId=' + messageId + 'and fileId=' + fileId + ' failed no permission');
              return cb(err);
            }
            MessageBk.getPreviewFileUrl(src, message, (err, result)=>{
              if (err){
                logger.warn(src.id, functionName + 'Get message by messageId=' + messageId + 'and fileId=' + fileId + 'failed after call getPreviewFileUrl', err);
                return res.status(cst.HttpErrorStatus).json(err);
              }
              if (!result){
                logger.warn(src.id, functionName + 'Get message by messageId=' + messageId + 'and fileId=' + fileId + 'failed after call getPreviewFileUrl no result');
                return res.status(cst.HttpErrorStatus).json(new esErr.ESErrors(esErr.GetPreviewUrlFailed));
              }
              res.status(cst.HttpSuccessStatus).json({url: result});
            })
          });          
        }
    });
  }
};

function handleError(res, err) {
  return res.status(500).send(err);
}



