import { Router } from 'express';
import controller from './anonymous.controller';
import auth from '../../auth/auth.service';
import {asView} from '../viewBase/viewBase';



const router = new Router();

router.post('/auth', asView(controller.create));
router.get('/me', asView(controller.me));

export default router;



