import React, { PropTypes, Component } from 'react';
import counterpart from 'counterpart';
import ReactTranslateComponent from 'react-translate-component';

const Translation = {
  language: null,
  Component: ReactTranslateComponent,
  bind: function(key){
    "use strict";
    let self = this;
    if (!self.language) {
      this.setLanguage('en');
    }
    return self.language.messages[key] ?  {__html: self.language.messages[key]} : {__html: key};
  },
  get: function(key){
    "use strict";
    let self = this;
    if (!self.language) {
      this.setLanguage('en');
    }
    return self.language.messages[key] ? self.language.messages[key] : key;
  },
  initLanguage: function(navigator){
    "use strict";
    //let lang = navigator.language.indexOf("-") > -1 ? navigator.language.split("-")[0] : navigator.language;

    let lang = navigator.language;
    lang = lang.indexOf("en") > -1 ? "en" : lang;
    this.setLanguage(lang);
  },
  setLanguage: function(lang){
    "use strict";
    let file;
    try{
      file = require('../../_locale/' + lang);
    }catch(e){

      file = require('../../_locale/en');
      lang = "en";
    }
    file.locale = lang;
    this.language = file;
    counterpart.registerTranslations(lang, this.language);
    counterpart.setLocale(lang);
  }
};



export default Translation;



